/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';

import { Link } from 'react-router-dom';
import { FC, useContext, useEffect } from 'react';

import styles from '../../../assets/styles/home.module.scss';
import { GlobalContext } from '../../../context/GlobalContext';

type TCaseStudyResponse = {
  uuid: string;
  name: string;
  no_of_employees: number;
};

type TCaseStudyType = {
  caseStudies: Array<TCaseStudyResponse>;
};

const CaseStudyItem: FC<TCaseStudyType> = ({ caseStudies }) => {
  const { dispatch } = useContext(GlobalContext);

  useEffect(() => {
    dispatch({
      type: 'ADD_ROUTE',
      payload: {
        path: '/',
      },
    });
  }, []);


  return (
    <div className={styles.casestudyList}>
      {caseStudies?.length ? (
        <div>
          {caseStudies?.map((caseStudy, key: number) => (
            <div className={styles.casestudyItem} key={caseStudy.uuid}>
              <Link
                to={`/casestudy/${caseStudy.uuid}/original-route`}
                className={styles.link}>
                <div
                  className={styles.imgBlk}
                  sx={{ background: 'ghostwhite' }}>
                  <span
                    sx={{
                      variant: 'text.subhead',
                      color: 'slategray',
                      fontWeight: 'body',
                    }}>
                    {key + 1}
                  </span>
                </div>
                <div className={styles.contentBlk}>
                  <h3
                    sx={{
                      variant: 'text.body-large',
                      color: 'slategray',
                      fontWeight: 'body',
                    }}>
                    {caseStudy.name}
                  </h3>
                  <h4
                    sx={{
                      variant: 'text.body-large',
                      color: 'slategray',
                      fontWeight: 'body',
                    }}>
                    {caseStudy.no_of_employees} Employees
                  </h4>
                </div>
              </Link>
            </div>
          ))}
        </div>
      ) : null}
    </div>
  );
};

export default CaseStudyItem;
