//Styped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, FC } from 'react';
import Avatar from '@swvl/avatar';
import Icon from '@swvl/icon';

import styles from '../../../assets/styles/case-study.module.scss';
import { images } from '../../../constants';
import { IEmployeeDetail } from '../apiServices';
import { NoResult } from '../../../shared_ui_components';

const weekDays = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'];

interface IDetailTable {
  arrivalTime: string | undefined;
  departureTime: string | undefined;
  employeeDetail: Array<IEmployeeDetail>;
}

const DetailTable: FC<IDetailTable> = ({
  arrivalTime,
  departureTime,
  employeeDetail,
}) => {
  const [openTable, setOpenTable] = useState('');

  const compareId = (val: string) => {
    return openTable === val ? true : false;
  };

  const handleSetOpenTable = (val: string) => {
    if (val === openTable) {
      setOpenTable('');
    } else {
      setOpenTable(val);
    }
  };
  return (
    <div className={styles.userTable}>
      <table>
        <thead>
          <th
            sx={{
              variant: 'text.body-large',
              color: 'black',
              fontWeight: 'bold',
            }}
            className={styles.notSort}>
            Riders name
          </th>
          <th
            sx={{
              variant: 'text.body-large',
              color: 'black',
              fontWeight: 'bold',
            }}
            className={styles.notSort}>
            Distance to station
          </th>
          <th
            sx={{
              variant: 'text.body-large',
              color: 'black',
              fontWeight: 'bold',
            }}
            className={styles.notSort}>
            Pickup Address
          </th>
          <th
            sx={{
              variant: 'text.body-large',
              color: 'black',
              fontWeight: 'bold',
            }}
            className={styles.notSort}>
            Gender
          </th>
        </thead>
        <tbody>
          {employeeDetail?.map((item) => {
            return (
              <tr key={item.id}>
                <td
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}
                  className={
                    compareId(item.employee.name) ? styles.showDetail : ''
                  }>
                  <span
                    className={`flexCentered ${styles.avatar}`}
                    sx={{
                      variant: 'text.body-small',
                      color: 'dimgray',
                      fontWeight: 'body',
                    }}>
                    <Avatar
                      src={images.avatarDefault}
                      name='name'
                      size='small'
                    />
                    {item.employee.name}
                  </span>
                </td>
                <td
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}
                  className={`${
                    compareId(item.employee.name) ? styles.showDetail : ''
                  }`}>
                  {/* {item.distance_to_station} */}-
                  {compareId(item.employee.name) && (
                    <div className={styles.userDetail}>
                      <p
                        sx={{
                          variant: 'text.body-large',
                          color: 'dimgray',
                          fontWeight: 'bold',
                        }}>
                        Timing
                      </p>
                      <ul className={`listUnstyled ${styles.tableList}`}>
                        {[1].map((detail) => {
                          return (
                            <li
                              sx={{
                                variant: 'text.body-small',
                                color: 'dimgray',
                                fontWeight: 'body',
                              }}
                              className={`${styles.tableListItem}`}>
                              {arrivalTime} - {departureTime}
                            </li>
                          );
                        })}
                      </ul>
                    </div>
                  )}
                </td>
                <td
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}
                  className={`${
                    compareId(item.employee.name) ? styles.showDetail : ''
                  } ${styles.address}`}>
                  {item.employee.location}
                  {compareId(item.employee.name) && (
                    <div className={styles.userDetail}>
                      <p
                        sx={{
                          variant: 'text.body-large',
                          color: 'dimgray',
                          fontWeight: 'bold',
                        }}>
                        Active on
                      </p>
                      <ul className={`listUnstyled ${styles.tableList}`}>
                        <li
                          className={`${styles.tableListItem}`}
                          sx={{
                            variant: 'text.body-small',
                            color: 'dimgray',
                            fontWeight: 'bold',
                          }}>
                          <ul className={`listInline ${styles.weekdays}`}>
                            {weekDays?.map((day) => {
                              return (
                                <li
                                  className={`listInlineItem  ${
                                    item.employee?.days_of_week
                                      ?.split(',')
                                      .includes(day)
                                      ? styles.active + ''
                                      : ''
                                  }`}>
                                  {day}
                                </li>
                              );
                            })}
                          </ul>
                        </li>
                        {/* );
                        })} */}
                      </ul>
                    </div>
                  )}
                </td>
                <td
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}
                  className={`${styles.dropdown} ${
                    compareId(item.employee.name) ? styles.showDetail : ''
                  }`}>
                  <span className='flexCentered'>
                    {item.employee.gender}{' '}
                    <span
                      className={styles.icon}
                      onClick={() => handleSetOpenTable(item.employee.name)}>
                      <Icon name='down_arrow' />
                    </span>
                  </span>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      {employeeDetail?.length === 0 && (
        <NoResult title='No Riders Found' subtext='' height='500px' />
      )}
    </div>
  );
};

export default DetailTable;
