// Error Code
/*
0 => No error
1 => Empty field
2 => Minimum length
3 => Maximum length
4 => Pattern not matching
*/

type TFieldValidation = {
  requiredFlag: boolean;
  fieldval: string | undefined;
  minLength?: number;
  maxLength?: number;
  regexPattern?: string | RegExp;
};

export const fieldValidation = (data: TFieldValidation) => {
  if (data.requiredFlag) {
    if (data.fieldval && data.fieldval.toString().trim().length) {
      return checkForValidationPath(data);
    } else {
      return 1;
    }
  } else {
    if (data.fieldval && data.fieldval.toString().trim().length) {
      return checkForValidationPath(data);
    } else {
      return 0;
    }
  }
};

const checkForValidationPath = (data: TFieldValidation): number => {
  if (data.minLength) {
    if (checkMinlength(data.fieldval, data.minLength)) {
      return 2;
    }
  }
  if (data.maxLength) {
    if (checkMaxlength(data.fieldval, data.maxLength)) {
      return 3;
    }
  }
  if (data.regexPattern) {
    if (!checkPatternMatches(data.fieldval, data.regexPattern)) {
      return 4;
    }
  }
  return 0;
};

const checkMinlength = (
  fieldval: string | undefined,
  minLength: number
): boolean => {
  if (fieldval) {
    return fieldval.trim().length < minLength;
  } else {
    return false;
  }
};

const checkMaxlength = (
  fieldval: string | undefined,
  maxLength: number
): boolean => {
  if (fieldval) {
    return fieldval.trim().length > maxLength;
  } else {
    return false;
  }
};

const checkPatternMatches = (
  fieldval: string | undefined,
  regexPattern: string | RegExp
): boolean => {
  if (fieldval) {
    const newRegex = new RegExp(regexPattern);
    return newRegex.test(fieldval);
  } else {
    return false;
  }
};
