import { FC, useState, useEffect, useContext } from 'react';
import { Select } from '@swvl/select';
import queryString from 'query-string';
import Table from '@swvl/table';
import Avatar from '@swvl/avatar';
import { format, parseISO } from 'date-fns';
import { Link, useHistory, useLocation } from 'react-router-dom';
import { Column } from 'react-table';
import Tag from '@swvl/tag';

import styles from '../../../assets/styles/home.module.scss';
import { images } from '../../../constants';
import { useUsersList, useActivityList } from '../apiServices';
import { TUserInfoProp, TOptionsType } from '../../../types';
import { HomeShimmer, DialogActivityParams } from '../components';
import { NoResult ,CustomTooltip } from '../../../shared_ui_components';
import { capitalize, useLocalStorage } from '../../../utils';
import { GlobalContext } from '../../../context/GlobalContext';

const dateFormat = (date: string): string => {
  return `${format(parseISO(date), 'MM/dd/yyyy')} . ${format(
    parseISO(date),
    'pp'
  )}`;
};

type TableData = {
  user: JSX.Element;
  activity: JSX.Element;
  status: JSX.Element;
  created_on: string;
  company: JSX.Element;
  actions: JSX.Element;
};

const columns: Column<TableData>[] = [
  {
    Header: 'User',
    accessor: 'user',
  },
  {
    Header: 'Activity',
    accessor: 'activity',
  },

  {
    Header: 'Created On',
    accessor: 'created_on',
  },
  {
    Header: 'Company',
    accessor: 'company',
  },
  {
    Header: 'Status',
    accessor: 'status',
  },
  {
    Header: 'Actions',
    accessor: 'actions',
  },
];

type TParamsType = {
  max_route_meters: number;
  max_routes_seconds: number;
  capacity_limitation: number;
  station_wait: number;
  walk_to_station: number;
  grouping_distance: number;
  custom_fleets: string;
  fleet_mix: string[];
};

// Values - maintained for pooling response status from oracle engine  
// 0 ==> Pending response from oracle engine to webhook
// 1 ==> Response recieved from oracle engine to webhook
// 2 ==> Error recieved from oracle engine to webhook
// 3 ==> No solution error recieved from oracle engine to webhook

const checkStatus = (val: number) => {
  if (val === 0) {
    return (
      <Tag variant='goldenrod'>
        <span className='caption white'>Pending</span>
      </Tag>
    );
  } else if (val === 1) {
    return (
      <Tag variant='mediumseagreen'>
        <span className='caption white'>Completed</span>
      </Tag>
    );
  } else if (val === 2) {
    return (
      <Tag variant='crimson'>
        <span className='caption white'>Error</span>
      </Tag>
    );
  } else if (val === 3) {
    return (
      <Tag variant='crimson'>
        <span className='caption white'>No Solution Found</span>
      </Tag>
    );
  } else {
    return <span></span>;
  }
};

const ActivityLog: FC = () => {
  const { localStorageValue: userInfo } =
    useLocalStorage<TUserInfoProp>('userInfo');
  const { dispatch } = useContext(GlobalContext);
  const [user, setUser] = useState<TOptionsType>();
  const [userOptions, setUserOptions] = useState<Array<TOptionsType>>();
  const [acitivtyLog, setActivityLog] = useState<Array<TableData>>([]);
  const [openViewParamsDialog, setOpenViewParamsDialog] = useState(false);
  const [pageData, setPageData] = useState({
    total: 0,
    per_page: 10,
    number_of_pages: 10,
  });

  const [paramsData, setParamsData] = useState<TParamsType>();
  const [pageIndex, setPageIndex] = useState(0);

  const { data: usersList, isFetching: fetchingUsersList } = useUsersList({
    canAccess: userInfo?.user?.role?.permissions?.can_access_all_activity,
  });
  const { data: activityLogList, isFetching: activityFetching } =
    useActivityList({
      user: user && user.value ? user?.value.split(' ')[0] : '',
      page: pageIndex,
    });

  let history = useHistory();
  const location = useLocation();
  const { search } = location;
  const queryData = queryString.parse(search);

  useEffect(() => {
    if (usersList?.length) {
      const _users = usersList.map((item) => {
        return {
          label: item.name ? capitalize(item.name) : item.email,
          value: `${item.uuid} ${item.email}`,
        };
      });

      const _AllUsers = [{ label: 'All Users', value: '' }].concat(_users);
      if (!user) {
        setUser(_AllUsers[0]);
      }
      setUserOptions(_AllUsers);
    }
  }, [usersList]);

  useEffect(() => {
    dispatch({
      type: 'ADD_ROUTE',
      payload: {
        path: '/activity',
      },
    });
  }, []);

  useEffect(() => {
    if (activityLogList) {
      const { list = [], pagination } = activityLogList;
      if (pagination) {
        setPageData({
          total: pagination.total,
          per_page: pagination.per_page,
          number_of_pages: pagination.number_of_pages,
        });
      }
      const _activityLog = list?.map((item) => {
        return {
          user: (
            <div className='profileDetail'>
              <span className='userAvatar'>
                <Avatar
                  src={
                    item.user.profile_pic
                      ? item.user.profile_pic
                      : images.avatarDefault
                  }
                  size='small'
                  name='as'
                />
              </span>
              <ul className='listUnstyled userInfo '>
                <li className='supportText'>{item.user.name}</li>
                <li className='captionLarge captionReg lightFont'>
                  {item.user.email}
                </li>
              </ul>
            </div>
          ),
          activity: (
            <p className='listInlineItem'>
              {item.activity && item?.run?.name
                ? `${item.activity} - ${item.run.name}`
                : item.activity}
              <br />
              {item?.run?.error_message ? item.run.error_message : ''}
            </p>
          ),
          status: checkStatus(item.status.value),
          created_on: dateFormat(item.created_at),
          company: (
            <span className='companiesWrapper'>
              {item?.run?.company_names
                ? item.run.company_names.split(',').join(', ')
                : '-'}
            </span>
          ),
          actions: (
            <ul className='listInline activityLogActions flex'>
              {item?.run?.document && (
                <li className='listInlineItem'>
                  <CustomTooltip
                    id={`downloadIconButtom-${item.run.uuid}`}
                    left={70}
                    top={90}
                    content="
                    <div class='tooltip noShadow'>
                    <div class='small'> Download CSV</div>
                    </div>">
                  <a
                    href={item.run.document}
                    target='_blank'
                    rel='noopener noreferrer'
                    download>
                    <img src={images.downloadIconButtom} alt='download_icon' />
                  </a>
                  </CustomTooltip>
                  
                </li>
              )}
              {item.status.value === 1 && item?.run?.uuid && (
                <li className='listInlineItem'>
                  <Link to={`/generated-case/${item.run.uuid}`}>
                 
                  <CustomTooltip
                         id={`viewIconButton-${item.run.uuid}`}
                       left={70}
                       top={90}
                    content="
                     <div class='tooltip noShadow' >
                     <div class='small'> View Scenario</div>
                    </div>">
                        <img src={images.viewIconButton} alt='download_icon' />
                  </CustomTooltip>
                  
                  </Link>
                </li>
              )}
              {item.status.value > 1 && item?.run?.uuid && (
                <li
                  className='listInlineItem'
                  onClick={() => {
                    if (item?.run?.params) {
                      setOpenViewParamsDialog(true);
                      setParamsData(item?.run?.params);
                    }
                  }}>
                     <CustomTooltip
                     id={`infoIconOutlined-${item.run.uuid}`}
                       left={70}
                       top={90}
                    content="
                     <div class='tooltip noShadow'>
                     <div class='small'> View Parameters Info</div>
                    </div>">
                        <img src={images.infoIconOutlined} alt='infoIconOutlined' />
                  </CustomTooltip>
                 
                </li>
              )}
            </ul>
          ),
        };
      });

      setActivityLog(_activityLog);
    }
  }, [activityLogList]);

  useEffect(() => {
    if (user && queryData?.user !== user?.label && queryData?.user) {
      setUser(userOptions?.find(({ label }) => label === queryData?.user));
      setPageIndex(0);
      history.push(`/activity?user=${queryData?.user}`);
    }
  }, [queryData?.user]);

  const handleSetPagination = (val: number) => {
    setPageIndex(val);
  };

  const filterOption = (option: TOptionsType, inputValue: string) => {
    const { label, value } = option;
    const otherKey = userOptions?.filter(
      (opt) =>
        (opt.label === label && opt.value.includes(inputValue)) ||
        (opt.value === value && opt.label.includes(inputValue))
    );
    return value.includes(inputValue) || (otherKey && otherKey?.length > 0)
      ? true
      : false;
  };
  return (
    <div className={styles.activtyLog}>
      <DialogActivityParams
        open={openViewParamsDialog}
        handleCancel={() => {
          setOpenViewParamsDialog(false);
        }}
        paramsData={paramsData}
      />
      <section className={styles.activityLogSection}>
        {!fetchingUsersList && !activityFetching && acitivtyLog ? (
          <>
            <ul className={`listInline ${styles.header}`}>
              <li className='listInlineItem'>
                <h3 className='heading3'>Activity Log</h3>
              </li>
              {userInfo?.user?.role?.permissions?.can_access_all_activity && (
                <li className='listInlineItem'>
                  <ul className={`listInline ${styles.selectInputWrapper}`}>
                    <li className='listInlineItem'>
                      <p className='captionLarge captionReg'>
                        Show activity logs of
                      </p>
                    </li>
                    <li className={`listInlineItem ${styles.selectInput}`}>
                      {userOptions && user && (
                        <Select
                          options={userOptions}
                          filterOption={filterOption}
                          isClearable={false}
                          isSearchable={true}
                          onChange={(val) => {
                            if (val) {
                              setUser(val);
                              setPageIndex(0);
                              history.push(`/activity?user=${val.label}`);
                            }
                          }}
                          value={user}
                          variant='plain'
                          name='km'
                          label=''
                        />
                      )}
                    </li>
                  </ul>
                </li>
              )}
            </ul>
            <div className={styles.userTable}>
              {
                <Table
                  noDataComponent={
                    <NoResult
                      height='calc(100vh - 270px)'
                      image={images.noResultImage}
                      title='No User Activity Found'
                      subtext={``}
                    />
                  }
                  data={acitivtyLog}
                  columns={columns}
                  useZebraStyles={true}
                  useFullWidth={true}
                  useSameSizeCells={false}
                  pageSize={pageData.per_page}
                  totalCount={pageData.total}
                  pageIndex={pageIndex}
                  useTablePagination={true}
                  useControlledPagination={true}
                  onPageChange={handleSetPagination}
                />
              }
            </div>
          </>
        ) : (
          <HomeShimmer type='ActivityLog' />
        )}
      </section>
    </div>
  );
};

export default ActivityLog;
