//Styped

import { Route, Redirect, useLocation } from 'react-router-dom';
import queryString from 'query-string';
import { FC, ComponentType } from 'react';
import useLocalStorage from '../utils/custom_hooks/useLocalStorage';
import { TUserInfoProp } from '../types';

interface IPrivateRouteProps {
  component: ComponentType<any>;
  path: string;
  exact: boolean;
}

const PrivateRoute: FC<IPrivateRouteProps> = ({
  component: Component,
  path,
  exact,
}) => {
  const { localStorageValue, setLocalStorage } =
    useLocalStorage<TUserInfoProp>('userInfo');
  let location = useLocation();

  const isUserLoggedIn = () => {
    if (localStorageValue?.access) {
      return true;
    } else {
      const querys = queryString.parse(location.search);
      const redirection = querys.redirection;
      if (redirection) {
        setLocalStorage('redirection', location.pathname);
      }
      return false;
    }
  };

  return (
    <Route
      path={path}
      exact={exact}
      render={(props) =>
        isUserLoggedIn() ? (
          <Component {...props} />
        ) : (
          <Redirect
            to={{
              pathname: '/signin',
            }}
          />
        )
      }
    />
  );
};

export default PrivateRoute;
