import { FC } from 'react';
import { ThemeProvider } from 'theme-ui';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Theme from '@swvl/theme';
import { AlertContainer } from '@swvl/alert';
import { QueryClient, QueryClientProvider } from 'react-query';
import { authRoutes } from './pages/auth/authRoutes';
import { homeRoutes } from './pages/home/homeRoutes';
import { generateRoutes } from './pages/generate_route/generateRoutes';
import { caseStudyRoutes } from './pages/casestudy/caseStudyRoutes';
import PrivateRoute from './hocs/PrivateRoute';
import GlobalContextProvider from './context/GlobalContext';
import { PageNotFound } from './components';
import { pageLayoutHoc } from './hocs/pageLayoutHoc';
import './assets/styles/app.scss';



export const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnWindowFocus: false,
    },
  },
});

const App: FC = () => {
  return (
    <div className='pageWrapper english'>
      <ThemeProvider theme={Theme}>
        <QueryClientProvider client={queryClient}>
          <GlobalContextProvider>
            <Router>
              <AlertContainer position='top-right' />
              <Switch>
                {[...authRoutes].map(({ path, component, key }) => (
                  <Route exact path={path} component={component} key={key} />
                ))}
                {[...homeRoutes, ...caseStudyRoutes, ...generateRoutes].map(
                  ({ path, component, key }, index) => (
                    <PrivateRoute
                      exact
                      path={path}
                      component={component}
                      key={key}
                    />
                  )
                )}
                <Route exact path='*' component={pageLayoutHoc(PageNotFound)} />
              </Switch>
            </Router>
          </GlobalContextProvider>
        </QueryClientProvider>
      </ThemeProvider>
    </div>
  );
};

export default App;
