/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';

import { useState, useEffect, FC, FormEvent } from 'react';
import Grid from '@swvl/grid';
import Icon from '@swvl/icon';
import Button from '@swvl/button';
import Toggle from '@swvl/toggle';
import Input from '@swvl/input';

import styles from '../../../assets/styles/rider-detail.module.scss';
import { updateArrayOfObj, uuidv4 } from '../../../utils';
import { IFleetDetailResponse } from '../apiServices';
import ClickAwayListener from '../../../shared_ui_components/ClickAwayListener';
import GenerateRoutesShimmer from './Shimmer';

type TFleetDetailProps = {
  fleetList: Array<IFleetDetailResponse> | undefined;
  handleFleetListFetch: (obj: { fleet_type: number }) => void;
  fleetDetailsFetching: boolean;
  handleStepID: (val: number) => void;
  handleSetFleetData: (val: {
    fleets: string;
    fleet_type: number;
    custom_fleets: string;
  }) => void;
};

type TCustomFleetType = {
  id: string;
  seats: string;
  label: string;
  image: string;
  checked: boolean;
  quantity: string;
  rpkm: string;
  error_rpkm: string;
  error_quantity: string;
  error_seats: string;
};
const initalCustomFleetDetail: Array<TCustomFleetType> = [
  {
    id: uuidv4(),
    seats: '',
    label: 'Bus',
    image: '',
    checked: false,
    quantity: '',
    rpkm: '',
    error_rpkm: '',
    error_quantity: '',
    error_seats: '',
  },
];

const FleetDetail: FC<TFleetDetailProps> = ({
  fleetList,
  handleFleetListFetch,
  fleetDetailsFetching,
  handleStepID,
  handleSetFleetData,
}) => {
  const [vehicalDetail, setVehicalDetail] =
    useState<Array<IFleetDetailResponse>>();
  const [isSvwlFeet, setIsSvwlFeet] = useState(true);
  const [vehicalId, setVehicalID] = useState('');
  const [disabledBtn, setDisabledBtn] = useState(false);
  const [customFleetDetail, setCustomFleetDetail] = useState(
    initalCustomFleetDetail
  );
  const [isError, setIsError] = useState(false);
  const [customFleetId, setCustomFleetId] = useState('');

  const handleUpdateVehicleData = (
    id: string | number,
    obj: Partial<{
      id: number;
      seats: number;
      label: string;
      image: string;
      checked: boolean;
      quantity: number;
    }>
  ) => {
    if (vehicalDetail) {
      setVehicalDetail(updateArrayOfObj(vehicalDetail, obj, 'id', id));
    }
  };

  const handleUpdateCustomFleetDetail = <T, U>(id: T, obj: U) => {
    let _customFleetDetail = updateArrayOfObj(
      customFleetDetail,
      obj,
      'id',
      String(id)
    );
    _customFleetDetail = _customFleetDetail.map((item) => {
      return {
        ...item,
        error_rpkm: '',
        error_quantity: '',
        error_seats: '',
      };
    });
    setCustomFleetDetail(_customFleetDetail);
  };

  const handleSwitchFleetType = (
    e: FormEvent<HTMLInputElement>,
    val: boolean
  ) => {
    setIsSvwlFeet(!val);
    handleFleetListFetch({ fleet_type: !val ? 0 : 1 });
  };

  const handleSetVehicleId = (id: string) => {
    if (id !== vehicalId) {
      setVehicalID(String(id));
    }
  };

  const handleAddNewRow = () => {
    setCustomFleetDetail([
      ...customFleetDetail,
      {
        id: uuidv4(),
        seats: '',
        label: 'Bus',
        image: '',
        checked: false,
        quantity: '',
        rpkm: '',
        error_rpkm: '',
        error_quantity: '',
        error_seats: '',
      },
    ]);
  };

  const fleetDataFormatted = () => {
    const fleetData = {
      fleets: '',
      fleet_type: isSvwlFeet ? 0 : 1,
      custom_fleets: '',
    };
    if (vehicalDetail?.length) {
      const _fleets = vehicalDetail
        .filter((item) => item.quantity)
        .map((item) => {
          return `${item.id}x${item.quantity}`;
        })
        ?.join(',');
      fleetData.fleets = _fleets;
    }

    if (customFleetDetail?.length) {
      const _custom_fleets = customFleetDetail
        .filter((item) => item.quantity && item.seats && item.rpkm)
        .map((item) => {
          return `${item.seats}x${item.quantity}x${item.rpkm}`;
        })
        ?.join(',');
      fleetData.custom_fleets = _custom_fleets;
    }
    return fleetData;
  };

  const updateIfNumber = (val = "", prevValue = "") => {
    let reg = /^[0-9]+$/;
    return reg.test(val) ? val : prevValue;

  };

  const checkIfValidDecimal = (val = "") => {
    let reg = /^[0-9]\d*(\.\d+)?$/;
    return reg.test(val);
  };

  useEffect(() => {
    const addNewFields: Array<IFleetDetailResponse> | undefined =
      fleetList?.map((item) => {
        return { ...item, ...{ quantity: 0 } };
      });

    setVehicalDetail(addNewFields);
  }, [fleetList]);

  useEffect(() => {
    if (
        vehicalDetail?.every((item) => item?.quantity === 0) &&
        customFleetDetail?.every((item) => {
          const { quantity, rpkm, seats } = item;
          return [quantity, rpkm, seats].includes('');
        })
      ) {
        setDisabledBtn(true);
      } else if (!vehicalDetail?.length && !customFleetDetail?.length) {
        setDisabledBtn(true);
      } else {
        setDisabledBtn(false);
      }
  }, [vehicalDetail, customFleetDetail]);

  const handleValidateCustomFleet = (fleetData: Array<TCustomFleetType>) => {
    let _error = false;
    fleetData?.forEach((fleet) => {
      const { quantity, rpkm, seats } = fleet;
      if (quantity?.length || rpkm?.length || seats?.length) {
        if ([quantity, rpkm, seats].includes('')) {
          if (!quantity) {
            fleet.error_quantity = 'Quantity is required';
          }
          if (!rpkm) {
            fleet.error_rpkm = 'Rate Per KM is required';
          }
          if (!seats) {
            fleet.error_seats = 'Seat is required';
          }
          _error = true;
        }
        if (rpkm && !checkIfValidDecimal(rpkm)){
          fleet.error_rpkm = 'Please enter valid rate per KM';
          _error = true;
        }
      }
    });
    return { customFleetDetail: fleetData, error: _error };
  };

  const handleContinue = () => {
    const validate_custom_fleet = handleValidateCustomFleet(customFleetDetail);
    if (validate_custom_fleet?.error) {
      setCustomFleetDetail([...validate_custom_fleet.customFleetDetail]);
    } else {
      handleStepID(3);
      const _fleetData = fleetDataFormatted();
      handleSetFleetData(_fleetData);
    }
  };

  return (
    <div className={styles.fleetDetailWrapper}>
      <h4 sx={{ variant: 'text.headline', color: 'black', fontWeight: 'bold' }}>
        Select / Configure Fleet details
      </h4>
      <div className={styles.fleetSwticher}>
        <ul className='listInline flexCentered'>
          <li className='listInlineItem'>
            <p
              sx={{
                variant: 'text.subhead',
                color: 'black',
                fontWeight: `${isSvwlFeet ? 'body' : 'light'}`,
              }}>
              Choose Swvl Fleet
            </p>
          </li>
          <li className={styles.toggleSwitch + ' listInlineItem'}>
            <Toggle
              id='1'
              checked={!isSvwlFeet}
              onChange={(e) => handleSwitchFleetType(e, isSvwlFeet)}
            />
          </li>
          <li className='listInlineItem'>
            <p
              sx={{
                variant: 'text.subhead',
                color: 'black',
                fontWeight: `${!isSvwlFeet ? 'body' : 'light'}`,
              }}>
              Corporate’s Fleet
            </p>
          </li>
        </ul>
      </div>
      <ClickAwayListener
        onClickAway={() => {
          setVehicalID('');
        }}>
        <div>
          {fleetDetailsFetching ? (
            <GenerateRoutesShimmer type='FleetDetail' />
          ) : (
            <Grid gap={3} columns='repeat(4, 1fr)'>
              {vehicalDetail?.map((item: any) => {
                return (
                  <div
                    key={item.name}
                    onClick={() => {
                      handleSetVehicleId(item.id);
                      setCustomFleetId(String(item.id));
                      if (item.quantity === 0) {
                        handleUpdateVehicleData(item.id, {
                          quantity: 1,
                        });
                      }
                    }}>
                    <div
                      className={`${styles.vehicalDetailWrapper} ${
                        item.quantity !== 0 ? styles.active : ''
                      }`}>
                      <div
                        className={`textCenter ${styles.vehicalCoverImageWrapper} `}>
                        <span className={styles.vehicalCoverImage}>
                          <img src={item.image} alt={item.label} />
                        </span>
                      </div>
                      <div className={styles.contentBlk}>
                        <Grid columns='repeat(2, 1fr)'>
                          <div className={styles.vehicleInfo}>
                            <h6
                              sx={{
                                variant: 'text.body-small',
                                color: 'dimgray',
                                fontWeight: 'body',
                              }}>
                              {item?.name}
                            </h6>
                            <p
                              sx={{
                                variant: 'text.caption-large',
                                color: 'slategray',
                                fontWeight: 'body',
                              }}>
                              {item.seats} Seater
                            </p>
                          </div>
                          <div>
                            {(customFleetId === String(item.id) ||
                              item.quantity > 0) && (
                              <div className={styles.buttonGroup}>
                                <span
                                  sx={{ background: 'whitesmoke' }}
                                  className={`${styles.smallBtn} ${styles.minus}`}
                                  onClick={() => {
                                    handleUpdateVehicleData(item.id, {
                                      quantity:
                                        item.quantity > 0
                                          ? item.quantity - 1
                                          : item.quantity,
                                    });
                                  }}
                                />
                                <span
                                  className={`${styles.quantity}`}
                                  sx={{
                                    variant: 'text.caption-large',
                                    background: 'whitesmoke',
                                  }}>
                                  <p>{item.quantity}</p>
                                </span>
                                <span
                                  sx={{ background: 'whitesmoke' }}
                                  className={`${styles.smallBtn} ${styles.plus}`}
                                  onClick={() => {
                                    handleUpdateVehicleData(item.id, {
                                      quantity: item.quantity + 1,
                                    });
                                  }}
                                />
                              </div>
                            )}
                          </div>
                        </Grid>
                      </div>
                    </div>
                  </div>
                );
              })}
            </Grid>
          )}
        </div>
      </ClickAwayListener>
      <div>
        <Grid gap={3} columns='repeat(1, 1fr)'>
          <div className={styles.fleetTypeAdd}>
            <h3
              sx={{
                variant: 'text.body-large',
                color: 'black',
                fontWeight: 'body',
              }}>
              Did not find your fleet type ?
            </h3>
            <p
              sx={{
                variant: 'text.caption-large',
                color: 'black',
                fontWeight: 'body',
              }}>
              You can enter a custom quantity below
            </p>
            <div className={styles.fleetInputGroup}>
              {customFleetDetail.map((item, i) => {
                const isFirstElement = i === 0 ? true : false;
                return (
                  <div className={styles.customQuantityRow}>
                    <ul className='listInline'>
                      <li className='listInlineItem'>
                        <Input
                          height={'small'}
                          placeholder={'Ex. 30 Seater'}
                          label={isFirstElement ? 'Capacity' : ''}
                          value={item.seats}
                          onChange={(e) => {
                            handleUpdateCustomFleetDetail(item.id, {
                              seats: updateIfNumber(e.target.value, item.seats),
                            });
                          }}
                        />
                        {item?.error_seats && (
                          <h6 className='errorMsg absolute'>
                            {item?.error_seats}
                          </h6>
                        )}
                      </li>
                      <li className='listInlineItem'>
                        <p
                          className={`${styles.multiply} heading4 ${
                            isFirstElement ? styles.multiplyBlk : ''
                          }`}>
                          ×
                        </p>
                      </li>
                      <li className='listInlineItem'>
                        <Input
                          height={'small'}
                          placeholder={'Ex. 10 nos.'}
                          label={isFirstElement ? 'Quantity' : ''}
                          value={item.quantity}
                          onChange={(e) => {
                            handleUpdateCustomFleetDetail(item.id, {
                              quantity: updateIfNumber(
                                e.target.value,
                                item.quantity
                              ),
                            });
                          }}
                        />
                        {item?.error_quantity && (
                          <h6 className='errorMsg absolute'>
                            {item.error_quantity}
                          </h6>
                        )}
                      </li>
                      <li className='listInlineItem'>
                        <Input
                          height={'small'}
                          placeholder={'Ex. 45 $'}
                          label={
                            isFirstElement ? '$ Rate per Km (per vehicle)' : ''
                          }
                          value={item.rpkm}
                          onChange={(e) => {
                            handleUpdateCustomFleetDetail(item.id, {
                              rpkm: e.target.value,
                            });
                          }}
                        />
                        {item?.error_rpkm && (
                          <h6 className='errorMsg absolute'>
                            {item?.error_rpkm}
                          </h6>
                        )}
                      </li>
                      {isFirstElement ? (
                        <li
                          className={styles.addMoreBtnBlk + ' listInlineItem'}>
                          <Button
                            onClick={handleAddNewRow}
                            variant='outlineGray'>
                            &#43; Add more
                          </Button>
                        </li>
                      ) : (
                        <li className='listInlineItem'>
                          <div className={styles.closeTextField}>
                            <span
                              onClick={() => {
                                setCustomFleetDetail(
                                  customFleetDetail.filter(
                                    (data) => data.id !== item.id
                                  )
                                );
                              }}>
                              <Icon name='cancel' />
                            </span>
                          </div>
                        </li>
                      )}
                    </ul>
                  </div>
                );
              })}
            </div>
          </div>
        </Grid>
        <Grid gap={3} columns='repeat(1, 1fr)'>
          <div className={styles.bottomContinueWrapper}>
            <Button variant='primary' disabled={disabledBtn} onClick={handleContinue}>
              Continue
            </Button>
          </div>
        </Grid>
      </div>
    </div>
  );
};

export default FleetDetail;
