/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, FC, useContext } from 'react';
import { images } from '../constants';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { LogoutDialog } from './';
import { showAlert } from '@swvl/alert';
import { useMutation } from 'react-query';
import axiosInstance from '../utils/httpInterceptor';
import { useLocalStorage } from '../utils';
import { GlobalContext } from '../context/GlobalContext';
import { TUserInfoProp } from '../types';
import Menu from '@swvl/menu';

interface TProfileDropDownProps extends RouteComponentProps {
  isHomeScreen: boolean;
}

export const ProfileDropDown: FC<TProfileDropDownProps> = ({
  history,
  isHomeScreen,
}) => {
  const { dispatch } = useContext(GlobalContext);
  const { localStorageValue: userInfo, clearLocalStorage } =
    useLocalStorage<TUserInfoProp>('userInfo');
  const [openLogPop, setLogPop] = useState(false);
  const [isMenu1Visible, setMenu1Visibility] = useState(false);

  const LogoutApi = useMutation(
    () => {
      return axiosInstance({
        method: 'POST',
        url: '/logout/',
      });
    },
    {
      onSuccess: (
        response: Partial<{ statusCode: number; data: any; message: string }>
      ) => {
        const { statusCode, message } = response;
        if (statusCode === 200) {
          dispatch({
            type: 'CLEAR_ROUTE',
          });
          clearLocalStorage();
          history.push('/signin');
          showAlert({ message: String(message), type: 'success' });
        }
      },
    }
  );

  const logout = () => {
    LogoutApi.mutate();
  };

  const activityLog = () => {
    dispatch({
      type: 'ADD_ROUTE',
      payload: {
        path: '/activity',
      },
    });
    history.push('/activity');
  };

  const logOut = () => {
    setLogPop(true);
  };

  return (
    <div className='profileWrapper'>
      {userInfo ? (
        <div>
          {
            // eslint-disable-next-line jsx-a11y/role-supports-aria-props
            <ul
              className='listInline flexCentered profileList'
              onClick={() => setMenu1Visibility(!isMenu1Visible)}
              // onClick={handleClick}
              //@ts-ignore
              aria-controls='simple-menu'
              //@ts-ignore
              aria-haspopup='true'>
              <li className={`listInlineItem profilePic`}>
                {userInfo?.user?.profile_pic ? (
                  <img
                    className='profilePic'
                    src={userInfo.user.profile_pic}
                    alt='Profile pic'
                  />
                ) : (
                  <img
                    className='profilePic'
                    src={images.avatarDefault}
                    alt='Profile pic'
                  />
                )}
              </li>
              <li className='listInlineItem'>
                {userInfo?.user?.name ? (
                  <span
                    className='name'
                    sx={{
                      variant: 'text.body-medium',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    {userInfo?.user?.name?.substring(0, 19)}
                  </span>
                ) : (
                  <span
                    className='name'
                    sx={{
                      variant: 'text.body-medium',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    {userInfo?.user?.email.split('@')[0]}
                  </span>
                )}
                {userInfo?.user?.email ? (
                  <p
                    className='name email'
                    sx={{
                      variant: 'text.body-small',
                      color: 'black',
                      fontWeight: 'light',
                    }}>
                    {userInfo?.user?.email}
                  </p>
                ) : (
                  ''
                )}
              </li>
              <li className='listInlineItem'>
                <span>
                  <img className='caret' src={images.headerCaret} alt='img' />
                </span>
              </li>
            </ul>
          }
          <div className={`innerMenuList ${isHomeScreen ? 'home' : ''}`}>
            <Menu
              items={[
                {
                  name: 'ActivityLog',
                  label: 'Activity Log',
                  onClick: activityLog,
                },
                {
                  name: 'Logout',
                  label: 'Logout',
                  onClick: logOut,
                },
              ]}
              isVisible={isMenu1Visible}
              setVisibility={setMenu1Visibility}
              onSelectItem={(item) => {}}
            />
          </div>
          <LogoutDialog
            open={openLogPop}
            text='Are you sure you want to logout?'
            handleSuccess={logout}
            handleCancel={setLogPop}
          />
        </div>
      ) : null}
    </div>
  );
};

export default withRouter(ProfileDropDown);
