import { useQuery } from 'react-query';
import { AxiosResponse } from 'axios';

import axiosInstance from '../../utils/httpInterceptor';
import { minutes, hours } from '../../utils';

const reactQueryGeneralConfig = {
  keepPreviousData: true,
  refetchOnMount: false,
  refetchOnWindowFocus: false,
  staleTime: minutes(10),
  cacheTime: hours(24),
};

interface IFleetDetailResponse {
  id: number;
  image: string;
  is_ac: boolean;
  name: string;
  rate_per_km: number;
  seats: number;
  type_of_vehicle: string;
  vehicle_make: null | string;
  vehicle_model: null | string;
  quantity?: number;
}

type TfleetQuery = {
  fleet_type: number | undefined;
};

export function useFleetDetail(query: TfleetQuery) {
  return useQuery(
    ['fleetdetail', query],
    async () => {
      const { data }: AxiosResponse<IFleetDetailResponse[]> =
        await axiosInstance({
          method: 'GET',
          url: `/fleets/`,
          params: query,
        });
      return data;
    },
    { ...reactQueryGeneralConfig }
  );
}

type TCheckRun = {
  checkForRoute: boolean;
  generateUuid: string;
};
interface ICheckRunResponse {
  error_message:string,
  name: string;
  status: { value: number; label: string };
  uuid: string;
}

export function useCheckRun(query: TCheckRun) {
  return useQuery(
    'run',
    async () => {
      const { data }: AxiosResponse<ICheckRunResponse> = await axiosInstance({
        method: 'GET',
        url: `/check-status/${query.generateUuid}/`,
      });

      return data;
    },
    {
      retryDelay: minutes(1),
      enabled: query.checkForRoute && query.generateUuid ? true : false,
      retry: 30,
      keepPreviousData: false,
      refetchInterval: 1000 * 10,
      cacheTime: 0,
    }
  );
}

export type { IFleetDetailResponse };
