import { FC } from 'react';
import RangeSlider from '@swvl/range-slider';
import Grid from '@swvl/grid';

type CustomSliderProps = {
  label: string;
  value: number[];
  hideThumb: boolean;
  max: number;
  min: number;
  step: number;
  disabled: boolean;
  handleChange: (val: number[]) => void;
};

const CustomSlider: FC<CustomSliderProps> = ({
  value,
  hideThumb,
  max,
  min,
  step,
  disabled,
  label,
  handleChange,
}) => {
  return (
    <div className={`progressSliderWrapper flexCentered ${
        hideThumb ? 'hideThumb' : ''
      }`}>
      <Grid gap={1} columns="repeat(1, 1fr)" style={{width:'100%'}}>
        <div >
          <RangeSlider
            min={min}
            max={max}
            step={step}
            values={value}
            onChange={(val) => {
              handleChange(val);
            }}
            disabled={disabled}
          />
        </div>
      </Grid>
    </div>
  );
};

export default CustomSlider;
