import { ComponentType } from 'react';

type TRoutesType = {
  path: string;
  component: ComponentType;
  key: string;
};

type TKeyValueProps = {
  [key: string]: string;
};

type TUserInfoProp = {
  access: string;
  refresh: string;
  user: {
    email: string;
    name: string | null;
    profile_pic: string | null;
    uuid: string;
    role: {
      label: string;
      permissions: {
        can_access_all_activity: boolean;
        can_access_case_study: boolean;
      };
      value: number;
    };
  };
};

type TShimmerComponentProps = {
  type: string;
};

/*
uid - case study uuid 
suid - case study scenario uuid
type - scenario type
guid - generated case study uuid
gsuid - generated scneario uuid
ruid - ride uuid 
rnum - route number
*/

type TGlobalParamsType = {
  guid: string;
  gsuid: string;
  uid: string;
  suid: string;
  type: string;
  ruid: string;
  rnum: string;
};

type TLocationProps = {
  coords: {
    lat: number;
    lng: number;
  };
  label: number | string;
};

type TOptionsType = { label: string; value: string };

enum scenarioState {
  noSolution = 'no-solution',
}
export { scenarioState };
export type {
  TRoutesType,
  TKeyValueProps,
  TUserInfoProp,
  TShimmerComponentProps,
  TGlobalParamsType,
  TLocationProps,
  TOptionsType,
};
