/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "theme-ui";
import { useState, useEffect, FC, Fragment } from "react";
import Spinner from "@swvl/spinner";
import { Marker, InfoBox } from "@react-google-maps/api";
import { CustomMap } from "../../../shared_ui_components";
import styles from "../../../assets/styles/case-study.module.scss";
import Map from "@swvl/map";
import { IScenariosResponse, IEmployeeDetailList } from "../apiServices";
import { mapStyle, images } from "../../../constants";

const infoOptions = {
  closeBoxURL: "",
  enableEventPropagation: true,
};

type TMapRoutesProps = {
  scenariosData: IScenariosResponse | undefined;
  scenarioDatFetching: boolean;
  employeeLocationList: IEmployeeDetailList[] | undefined;
  selectedRouteId?: string;
  handleSelectRouteId: (val: string, from: string) => void;
};

type TLocation =
  | {
      coords: {
        lat: number;
        lng: number;
      };
      label: string;
    }
  | undefined;

type TRideWayPoints = {
  [x: string]: {
    startColor: string;
    endColor: string;
    strokeWeight: number;
    stations: {
      style: {
        path?: string;
        scale?: number;
        strokeColor?: string;
        fillColor?: string;
        fillOpacity?: number;
        strokeWeight?: number;
      };
      id: string;
      lat: number;
      lng: number;
    }[];
    encodedPolyline: string;
  };
};

const MapRoutes: FC<TMapRoutesProps> = ({
  scenariosData,
  scenarioDatFetching,
  employeeLocationList,
  selectedRouteId,
  handleSelectRouteId,
}) => {
  const [showDemandMap, setShowDemandMap] = useState(false);
  const [employeeId, setEmployeeId] = useState("");
  const [officeLocation, setOfficeLocation] = useState<TLocation>();
  const [showOfficeLocationInfo, setShowOfficeLocationInfo] = useState(false);
  const [routes, setRoutes] = useState<TRideWayPoints>({});
  const [localRouteId, setLocalRouteId] = useState("");
  const [mapProps] = useState({
    options: {
      styles: mapStyle,
    },
  });

  useEffect(() => {
    setLocalRouteId(String(selectedRouteId));
  }, [selectedRouteId]);

  useEffect(() => {
    if (scenariosData?.route_details?.length) {
      const { route_details } = scenariosData;

      const rideWayPoints: Array<TRideWayPoints> = route_details?.map(
        (route) => {
          return {
            [route.route_id]: {
              startColor: route.color_code,
              endColor: route.color_code,
              strokeWeight: 2,
              geodesic: true,
              stations: route?.rides?.length
                ? route?.rides?.map((item) => {
                    return {
                      style: {
                        strokeColor: route.color_code,
                      },
                      id: String(item.id),
                      lat: Number(item.station_latitude),
                      lng: Number(item.station_longitude),
                    };
                  })
                : [{ id: "", lat: 0.0, lng: 0.0, style: {} }],
              onMouseOver: () => {
                if (localRouteId !== route.route_id) {
                  handleSelectRouteId(route.route_id, "map");
                }
              },
              onMouseOut: () => {
                setLocalRouteId("");
              },
              encodedPolyline: route.geometry ? route.geometry : "",
            },
          };
        }
      );

      const officeLoaction = scenariosData?.route_details[0];
      const getOfficeLocationWayPoint: TLocation = {
        coords: {
          lat: Number(officeLoaction.last_station_latitude),
          lng: Number(officeLoaction.last_station_longitude),
        },
        label: officeLoaction.end_station_name,
      };

      const _rideWayPoints: TRideWayPoints = {};
      rideWayPoints?.forEach((item) => {
        Object.assign(_rideWayPoints, {
          [Object.keys(item)[0]]: item[Object.keys(item)[0]],
        });
      });
      setRoutes(_rideWayPoints);
      setOfficeLocation(getOfficeLocationWayPoint);
    }
  }, [scenariosData]);

  const updateRouteObject = <
    P extends { [key: string]: any },
    Q extends string
  >(
    object: P,
    id: Q
  ): any => {
    if (id) {
      return {
        [id]: object[id] ? object[id] : {},
      };
    } else {
      return object;
    }
  };

  return (
    <Fragment>
      {!scenarioDatFetching ? (
        <Fragment>
          <CustomMap
            options={{
              styles: mapStyle,
            }}
            routes={
              Object.keys(updateRouteObject(routes, String(localRouteId)))
                ?.length
                ? updateRouteObject(routes, String(localRouteId))
                : {}
            }
          >
            {officeLocation && (
              <Fragment>
                <Marker
                  position={{
                    lat: Number(officeLocation.coords.lat),
                    lng: Number(officeLocation.coords.lng),
                  }}
                  onMouseOver={() => {
                    setShowOfficeLocationInfo(true);
                  }}
                  onMouseOut={() => {
                    setShowOfficeLocationInfo(false);
                  }}
                  icon={images.officeLocation}
                />
                {showOfficeLocationInfo && (
                  <InfoBox
                    options={infoOptions}
                    position={{
                      lat: Number(officeLocation.coords.lat),
                      lng: Number(officeLocation.coords.lng),
                    }}
                  >
                    <div className="mapInfoBox">
                      <div>
                        <p className="supportText">{officeLocation.label}</p>
                      </div>
                    </div>
                  </InfoBox>
                )}
              </Fragment>
            )}
            {showDemandMap &&
              employeeLocationList?.map((location) => {
                return (
                  <Fragment>
                    <Marker
                      position={{
                        lat: Number(location.latitude),
                        lng: Number(location.longitude),
                      }}
                      key={location?.name}
                      onMouseOver={() => {
                        setEmployeeId(String(location?.uuid));
                      }}
                      onMouseOut={() => {
                        setEmployeeId("");
                      }}
                      icon={images.userIconMarker}
                    />
                    {String(location.uuid) === employeeId && (
                      <InfoBox
                        options={infoOptions}
                        position={{
                          lat: Number(location.latitude),
                          lng: Number(location.longitude),
                        }}
                      >
                        <div className="mapInfoBox">
                          <p
                            sx={{
                              variant: "text.body-small",
                              color: "dimgray",
                              fontWeight: "body",
                            }}
                          >
                            {location.name}
                          </p>
                          <div
                            className="flexCentered walk"
                            sx={{
                              variant: "text.body-small",
                              color: "dimgray",
                              fontWeight: "body",
                            }}
                          >
                            <img
                              src={images.distanceToStationIcon}
                              alt="distanceToStationIcon"
                            />{" "}
                            250m Walk
                          </div>
                        </div>
                      </InfoBox>
                    )}
                  </Fragment>
                );
              })}
          </CustomMap>
          <div className={styles.demandMapWrapper}>
            <span
              sx={{
                variant: "text.body-small",
                color: "dimgray",
                fontWeight: "body",
              }}
              className={`${styles.demandButton} ${
                showDemandMap ? styles.active : ""
              }`}
              onClick={() => {
                setShowDemandMap(!showDemandMap);
              }}
            >
              Demand Map
            </span>
          </div>
        </Fragment>
      ) : (
        <div className="gridCentered">
          <Spinner />
        </div>
      )}
    </Fragment>
  );
};

export default MapRoutes;
