/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { FC, useState, useEffect } from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { showAlert } from '@swvl/alert';
import { useMutation } from 'react-query';
import Grid from '@swvl/grid';
import { regExpression } from '../../../constants/index';
import { fieldValidation } from '../../../utils/formValidation';
import styles from '../../../assets/styles/auth.module.scss';
import { Introduction, SignInForm } from '../components';
import axiosInstance from '../../../utils/httpInterceptor';
import { useLocalStorage } from '../../../utils';
import { TUserInfoProp } from '../../../types';

const SignInn: FC<RouteComponentProps> = ({ history }) => {
  const { setLocalStorage, localStorageValue } =
    useLocalStorage<TUserInfoProp>('userInfo');
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    password_show: false,
  });

  const signInPost = useMutation(
    (data: Partial<{ email: string; password: string }>) => {
      return axiosInstance({
        method: 'POST',
        url: '/auth/login/',
        data: data,
      });
    },
    {
      onSuccess: (
        response: Partial<{ statusCode: number; data: any; message: string }>
      ) => {
        const { statusCode, data, message } = response;
        if (statusCode === 200) {
          setLocalStorage('userInfo', data);
          showAlert({ message: 'Logged in successfully', type: 'success' });
          if (data?.user?.role?.permissions?.can_access_case_study) {
            history.push('/');
          } else {
            history.push('/generateroute');
          }
        } else {
          if (message) {
            showAlert({ message: message, type: 'error' });
          }
          setLoader(false);
        }
      },
      onError: (err: Error) => {
        setLoader(false);
      },
    }
  );

  const [error, setError] = useState({});
  const [errorCode] = useState({
    email: {
      0: '',
      1: 'Please enter email address',
      4: 'Invalid email address',
    },
    emailObj: {
      requiredFlag: true,
      regexPattern: regExpression.email,
    },
    password: {
      0: '',
      1: 'Please enter password',
    },
    passwordObj: {
      requiredFlag: true,
    },
  });

  const [loader, setLoader] = useState(false);

  useEffect(() => {
    if (localStorageValue?.access) {
      history.push('/');
    }
  }, [history]);

  const togglePassword = () => {
    setFormData({
      ...formData,
      password_show: !formData.password_show,
    });
  };

  const handleChange = (key: string, val: string) => {
    setFormData({
      ...formData,
      [key]: val,
    });

    setError({ ...error, [key]: '' });
  };

  const handleSubmit = () => {
    const validateNewInput: {} = {
      email:
        errorCode['email'][
          fieldValidation({
            ...errorCode['emailObj'],
            fieldval: formData.email,
          })
        ],
      password:
        errorCode['password'][
          fieldValidation({
            ...errorCode['passwordObj'],
            fieldval: formData.password,
          })
        ],
    };
    if (
      Object.keys(validateNewInput).every((k) => validateNewInput[k] === '')
    ) {
      setLoader(true);
      signInPost.mutate({ email: formData.email, password: formData.password });
    } else {
      setError(validateNewInput);
    }
  };

  return (
    <div className={styles.authPage + ' ' + styles.loginPage}>
      <section className={styles.loginSection}>
        <div className={styles.loginRow}>
          <Grid gap={2} columns={[2, '2fr 1fr']}>
            <div className={styles.leftBlk}>
              <Introduction />
            </div>
            <div className={styles.rightBlk}>
              <SignInForm
                handleChange={handleChange}
                error={error}
                formData={formData}
                handleSubmit={handleSubmit}
                loader={loader}
                togglePassword={togglePassword}
              />
            </div>
          </Grid>
        </div>
      </section>
    </div>
  );
};

export default withRouter(SignInn);
