import { ComponentType } from 'react';
import { withRouter, useHistory } from 'react-router-dom';

import { TUserInfoProp } from '../types';
import { useLocalStorage } from '../utils';

function caseStudyAccess(ComposedComponent: ComponentType<any>) {
  function CaseStudyAccessHoc() {
    const { localStorageValue } = useLocalStorage<TUserInfoProp>('userInfo');
    let canAccess =
      localStorageValue?.user?.role?.permissions?.can_access_case_study;
    const history = useHistory();
    if (!canAccess) {
      history.push('/generateroute');
      return null;
    }
    return <ComposedComponent />
  }
  return withRouter(CaseStudyAccessHoc);
}

export default caseStudyAccess;
