//STyped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { FC } from 'react';
import Popup from '@swvl/popup';
import Grid from '@swvl/grid';

type TDialogActivityProps = {
  open: boolean;
  handleCancel: (val: boolean) => void;
  paramsData:
    | {
        max_route_meters: number;
        max_routes_seconds: number;
        capacity_limitation: number;
        station_wait: number;
        walk_to_station: number;
        grouping_distance: number;
        custom_fleets: string;
        fleet_mix: Array<string>;
      }
    | undefined;
};

const DialogActivityParams: FC<TDialogActivityProps> = ({
  open,
  handleCancel,
  paramsData,
}) => {
  const handleClose = () => {
    handleCancel(false);
  };

  return (
    <div>
      <Popup isOpen={open} closePopup={handleClose} withClose={true}>
        <div className='dilaogParamsActivity'>
          <div className='dialogTitle'>
            <h2
              sx={{
                variant: 'text.body-x-large',
                color: 'black',
                fontWeight: 'bold',
              }}>
              Optimization Parameters of Route
            </h2>
          </div>
          {paramsData && (
            <div className='dialogBody'>
              <Grid gap={2}>
                <span
                  sx={{
                    variant: 'text.body1',
                    color: 'black',
                    fontWeight: 'regular',
                  }}>
                  Max Distance:{' '}
                  <b>
                    {paramsData.max_route_meters
                      ? `${paramsData.max_route_meters / 1000} Km`
                      : '--'}
                  </b>
                </span>
                <span
                  sx={{
                    variant: 'text.body1',
                    color: 'black',
                    fontWeight: 'regular',
                  }}>
                  Max Duration:{' '}
                  <b>
                    {paramsData.max_routes_seconds
                      ? `${paramsData.max_routes_seconds / 60} mins`
                      : '--'}
                  </b>
                </span>
                <span
                  sx={{
                    variant: 'text.body1',
                    color: 'black',
                    fontWeight: 'regular',
                  }}>
                  Capactiy Limitation :{' '}
                  <b>
                    {paramsData.capacity_limitation
                      ? `${paramsData.capacity_limitation}%`
                      : '--'}
                  </b>
                </span>
                <span
                  sx={{
                    variant: 'text.body1',
                    color: 'black',
                    fontWeight: 'regular',
                  }}>
                  Station Wait Time:{' '}
                  <b>
                    {paramsData.station_wait
                      ? `${paramsData.station_wait} mins`
                      : '--'}
                  </b>
                </span>
                <span
                  sx={{
                    variant: 'text.body1',
                    color: 'black',
                    fontWeight: 'regular',
                  }}>
                  Walk to Station:{' '}
                  <b>
                    {paramsData.walk_to_station
                      ? `${paramsData.walk_to_station} sec`
                      : '--'}
                  </b>
                </span>
                <span
                  sx={{
                    variant: 'text.body1',
                    color: 'black',
                    fontWeight: 'regular',
                  }}>
                  Grouping Distance:{' '}
                  <b>
                    {paramsData.grouping_distance
                      ? `${paramsData.grouping_distance} m`
                      : '--'}
                  </b>
                </span>
                <span
                  sx={{
                    variant: 'text.body1',
                    color: 'black',
                    fontWeight: 'regular',
                  }}>
                  Custom Fleets:{' '}
                  <b>
                    {paramsData.custom_fleets
                      ? `${paramsData.custom_fleets?.split(',').join('$ ,')}$`
                      : '--'}
                  </b>
                </span>
                <span
                  sx={{
                    variant: 'text.body1',
                    color: 'black',
                    fontWeight: 'regular',
                  }}>
                  Fleets Mix:{' '}
                  <b>
                    {paramsData.fleet_mix
                      ? `${paramsData.fleet_mix.join(',')}`
                      : '--'}
                  </b>
                </span>
              </Grid>
            </div>
          )}
        </div>
      </Popup>
    </div>
  );
};

export default DialogActivityParams;
