import { useQuery } from 'react-query';
import { AxiosResponse } from 'axios';

import axiosInstance from '../../utils/httpInterceptor';
import { minutes, hours } from '../../utils';

const reactQueryGeneralConfig = {
  keepPreviousData: true,
  refetchOnMount: false,
  refetchOnWindowFocus: false,
  staleTime: minutes(10),
  cacheTime: hours(24),
};

interface ICaseStudyResponse {
  uuid: string;
  name: string;
  no_of_employees: number;
}

type TCaseStudy = {
  authorized: boolean;
};

function useCaseStudies(query: TCaseStudy) {
  const { authorized } = query;
  return useQuery(
    'casestudy',
    async () => {
      const { data }: AxiosResponse<Array<ICaseStudyResponse>> =
        await axiosInstance({
          method: 'GET',
          url: `/case-studies/`,
        });
      return data;
    },
    {
      ...reactQueryGeneralConfig,
      enabled: authorized,
    }
  );
}

interface IUserResponseList {
  email: string;
  uuid: string;
  name: string | null;
  profile_pic: string | null;
}

interface IUsersQuery {
  canAccess: boolean;
}

function useUsersList(query: IUsersQuery) {
  const { canAccess } = query;
  return useQuery(
    'users',
    async () => {
      const { data }: AxiosResponse<Array<IUserResponseList>> =
        await axiosInstance({
          method: 'GET',
          url: `/users/`,
        });
      return data;
    },
    {
      ...reactQueryGeneralConfig,
      enabled: canAccess,
    }
  );
}

interface IActivityQuery {
  user: string;
  page: number;
}
interface IActivityList {
  action: string;
  activity: string;
  created_at: string;
  ip_address: string;
  status: { label: string; value: number };
  run: {
    company_names: string;
    document: string;
    name: string;
    error_message: string;
    uuid: string;
    params: {
      max_route_meters: number;
      max_routes_seconds: number;
      capacity_limitation: number;
      station_wait: number;
      walk_to_station: number;
      grouping_distance: number;
      custom_fleets: string;
      fleet_mix: Array<string>;
    };
  };
  user: IUserResponseList;
  user_agent: string;
}

interface IPagination {
  current_page: number;
  number_of_pages: number;
  per_page: number;
  total: number;
}

interface IActivityResponse {
  list: Array<IActivityList>;
  pagination: IPagination;
}

function useActivityList(query: IActivityQuery) {
  const { user, page } = query;
  return useQuery(
    ['activity-list', user, page],
    async () => {
      const { data }: AxiosResponse<IActivityResponse> = await axiosInstance({
        method: 'GET',
        url: `/activities/`,
        params: { user, page: page + 1 },
      });
      return data;
    },
    {
      enabled: page + 1 ? true : false,
    }
  );
}

export { useCaseStudies, useUsersList, useActivityList };

export type { ICaseStudyResponse };
