import { useState } from 'react';

const useLocalStorage = <P>(
  key: string,
  initialValue?: P,
  onAccessError?: () => void,
  onSetError?: () => void,
  onClearError?: () => void
) => {
  const [localStorageValue, setLocalStorageValue] = useState<P>(() => {
    try {
      const item = window.localStorage.getItem(key);
      return item ? JSON.parse(item) : initialValue;
    } catch (error) {
      if (onAccessError) {
        onAccessError();
      }

      return initialValue;
    }
  });

  const setLocalStorage = (storageKey: string, value: any) => {
    try {
      if (value) {
        setLocalStorageValue(value);
        window.localStorage.setItem(storageKey, JSON.stringify(value));
      }
    } catch (error) {
      if (onSetError) {
        onSetError();
      }
    }
  };

  const clearLocalStorage = (value?: string) => {
    try {
      if (value) {
        window.localStorage.removeItem(value);
      } else {
        window.localStorage.clear();
      }
    } catch {
      if (onClearError && typeof onClearError === 'function') {
        onClearError();
      }
    }
  };

  return { localStorageValue, setLocalStorage, clearLocalStorage };
};

export default useLocalStorage;
