type ActionMap<M extends { [index: string]: any }> = {
  [Key in keyof M]: M[Key] extends undefined
    ? {
        type: Key;
      }
    : {
        type: Key;
        payload: M[Key];
      };
};

export type RouteObject = {
  path: string;
};

export enum routesActions {
  Add = 'ADD_ROUTE',
  Clear = 'CLEAR_ROUTE',
  Remove = 'REMOVE_ROUTE',
}

type RoutesPayload = {
  [routesActions.Add]: RouteObject;
  [routesActions.Clear]: undefined;
  [routesActions.Remove]: undefined;
};

export function getLastPath(arr: Array<RouteObject>, dispatch) {
  if (arr.length > 2) {
    let _path = arr[arr.length - 2].path;
    dispatch({
      type: routesActions.Remove,
    });
    return _path ? _path : '/';
  } else {
    return '/';
  }
}
export type RouteActions =
  ActionMap<RoutesPayload>[keyof ActionMap<RoutesPayload>];

export function routesReducer(state: Array<RouteObject>, action: RouteActions) {
  switch (action.type) {
    case routesActions.Add:
      if (action.payload.path !== state[state.length - 1]?.path) {
        return [...state, action.payload];
      } else {
        return [...state];
      }

    case routesActions.Clear:
      return [];
    case routesActions.Remove:
      return state.splice(0, state.length - 1);

    default:
      return state;
  }
}
