//STyped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, FC, useEffect } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import Button from '@swvl/button';
import Popup from '@swvl/popup';
import Grid from '@swvl/grid';
import { useMutation } from 'react-query';

import { GenerateRoutesShimmer } from '../../generate_route/components';
import { NoResult } from '../../../shared_ui_components';
import { images } from '../../../constants/index';
import axiosInstance from '../../../utils/httpInterceptor';
import { useCheckRun } from '../../generate_route/apiServices';
import { TGlobalParamsType } from '../../../types';

type SliderData = {
  max_route_meters: number;
  max_routes_seconds: number;
  capacity_limitation: number;
};

type TGenerateRouteDialog = {
  open: boolean;
  handleCancel: (val: boolean) => void;
  sliderData: SliderData;
};

const GenerateRouteDialog: FC<TGenerateRouteDialog> = ({
  open,
  handleCancel,
  sliderData,
}) => {
  const [errorCsv, setErrorCsv] = useState(false);
  const { guid } = useParams<TGlobalParamsType>();
  const [sliderDataUpdated, setSliderDataUpdated] = useState(false);
  const [newRouteUUID, setNewRouteUUID] = useState('');
  const [checkForRoute, setCheckForRoute] = useState(false);
  const history = useHistory();

  const { data: checkRun } = useCheckRun({
    generateUuid: newRouteUUID,
    checkForRoute,
  });

  const generateRouteApi = useMutation(
    (data: SliderData) => {
      return axiosInstance({
        method: 'POST',
        url: `/new-route/${guid}/`,
        data: data,
      });
    },
    {
      onSuccess: (response) => {
        if (response) {
          if (response?.data?.uuid) {
            const { uuid } = response.data;
            setNewRouteUUID(uuid);
            setCheckForRoute(true);
          }
        }
      },
      onError: (err) => {
        setErrorCsv(true);
      },
    }
  );

  const handleClose = () => {
    handleCancel(false);
  };

  useEffect(() => {
    if (sliderData && open && !sliderDataUpdated) {
      generateRouteApi.mutate({
        max_route_meters: sliderData.max_route_meters * 1000,
        max_routes_seconds: sliderData.max_routes_seconds * 60,
        capacity_limitation: sliderData.capacity_limitation,
      });

      setSliderDataUpdated(true);
    }
  }, [sliderData, open]);

  // Values for status - maintained for pooling response status from oracle engine
  // 0 ==> Pending response from oracle engine to webhook
  // 1 ==> Response recieved from oracle engine to webhook
  // 2 ==> Error recieved from oracle engine to webhook
  // 3 ==> No solution error recieved from oracle engine to webhook

  useEffect(() => {
    if (checkRun) {
      if (checkRun?.uuid && checkRun?.status?.value === 1) {
        handleReset();
        history.push(`/generated-case/${checkRun?.uuid}`);
      } else if (checkRun?.uuid && checkRun?.status?.value > 1) {
        setErrorCsv(true);
        setCheckForRoute(false);
      }
    }
  }, []);

  const handleReset = () => {
    setSliderDataUpdated(false);
    setCheckForRoute(false);
    setErrorCsv(false);
    handleCancel(false);
  };

  return (
    <Popup isOpen={open} closePopup={handleClose} width={800}>
      <div className='dsp-dialogs generateDialog'>
        <div className='dialogTitle' id='scroll-dialog-title'>
          {errorCsv ? (
            <NoResult
              title='Error Generating Route'
              subtext='We could not process your rider details due to incorrect information '
              height='300px'
              image={images.csvErrorIcon}
              buttonText='Start Over'
              onClick={() => {
                handleReset();
              }}
            />
          ) : (
            <div>
              <h3
                sx={{
                  variant: 'text.title-small',
                  color: 'slategray',
                  fontWeight: 'body',
                }}>
                Generating Route
              </h3>
              <GenerateRoutesShimmer type='Dialog' />
            </div>
          )}
        </div>

        <div className='dialogActions'>
          <Grid gap={2} columns='repeat(2, 1fr)'>
            <div className='cancelBtn'>
              <Button variant='secondary' onClick={() => handleReset()}>
                Cancel
              </Button>
            </div>
          </Grid>
        </div>
      </div>
    </Popup>
  );
};

export default GenerateRouteDialog;
