import { withRouter, matchPath, RouteComponentProps } from 'react-router-dom';
import { useState, ComponentType } from 'react';

import { Header, Sidebar } from '../components';

enum routes {
  generate_route = '/generateroute',
  compare_screen = '/compare/screens/:uid',
  employee_detail = '/employee/:uid/detail/:suid/ride/:ruid/:rnum',
  case_study = '/casestudy/:uid/:type/:suid?',
  activity_log = '/activity',
  generated_run = '/generated-case/:guid/:type?/:gsuid?',
}

//Hide Padding Side Padding
const HIDE_PADDING_ROUTES = [
  routes.generate_route,
  routes.compare_screen,
  routes.employee_detail,
  routes.activity_log,
];

//Show sidebar routes
const SHOW_SIDEBAR_ROUTES = [routes.case_study, routes.generated_run];

export const pageLayoutHoc = (HocComponent: ComponentType<any>) => {
  function PageLayout(props: RouteComponentProps) {
    const [expandedSidebar, setExpandedSidebar] = useState(false);

    const handleSidebarToggle = (flag: boolean) => {
      setExpandedSidebar(flag);
    };

    const checkRoutePath = (
      type: string,
      path: string | undefined
    ): boolean => {
      if (type === 'padding' && path) {
        const checkIfMatches = HIDE_PADDING_ROUTES.map((item) => {
          return Boolean(
            matchPath(path, { path: item, exact: true, strict: true })?.isExact
          );
        });
        return checkIfMatches.some((item) => item);
      } else if (type === 'sidebar' && path) {
        const checkIfMatches = SHOW_SIDEBAR_ROUTES.map((item) => {
          return Boolean(
            matchPath(path, { path: item, exact: true, strict: true })?.isExact
          );
        });
        return checkIfMatches.some((item) => item);
      } else {
        return false;
      }
    };

    return (
      <>
        <Header />
        {checkRoutePath('sidebar', props.location.pathname) ? (
          <aside>
            <Sidebar handleSidebarToggle={handleSidebarToggle} />
          </aside>
        ) : null}
        <main>
          <div
            style={
              checkRoutePath('padding', props.location.pathname)
                ? { padding: '0px' }
                : {}
            }
            className={expandedSidebar ? 'expandedSibarPadding' : ''}>
            <HocComponent {...props} />
          </div>
        </main>
      </>
    );
  }

  return withRouter(PageLayout);
};
