/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, Fragment, FC, useEffect } from 'react';
import Grid from '@swvl/grid';
import { useMutation } from 'react-query';
import { useHistory } from 'react-router-dom';

import styles from '../../../assets/styles/rider-detail.module.scss';
import { images } from '../../../constants';
import { ConvertToCSV } from '../../../utils';
import { RiderDetail, Optimization, FleetDetail } from '../components';
import { useFleetDetail, useCheckRun } from '../apiServices';
import axiosInstance from '../../../utils/httpInterceptor';

interface IStepsList {
  step: number;
  label: string;
}

const STEPS_LIST: Readonly<Array<IStepsList>> = [
  {
    step: 1,
    label: 'Rider Details',
  },
  {
    step: 2,
    label: 'Fleet Details',
  },
  {
    step: 3,
    label: 'Optimization',
  },
];

type TFleetData = {
  fleets: string;
  fleet_type: number;
  custom_fleets: string;
};

const GenerateRoute: FC = () => {
  const history = useHistory();
  const [query, setQuery] = useState({ fleet_type: 0 });
  const [csvJSON, setCsvJSON] = useState<string>();
  const [fleetData, setFleetData] = useState<TFleetData>();
  const [checkForRoute, setCheckForRoute] = useState(false);
  const [optimizationData, setOptimizationData] = useState('');
  const [csvFormatFile, setCsvFormatFile] = useState<File>();
  const [generateUuid, setGenerateUuid] = useState('');
  const [stepID, setStepID] = useState(STEPS_LIST[0]?.step);
  const [csvName, setCSVName] = useState('sample.csv');
  let { data: checkRun } = useCheckRun({
    checkForRoute,
    generateUuid,
  });
  const [invalidDataError, setInvalidDataError] = useState(false);
  const [stepAfterError,setStepAfterError]=useState(0);
  const [errorMessage,setErrorMessage]=useState('');

  // Values for status - maintained for pooling response status from oracle engine
  // 0 ==> Pending response from oracle engine to webhook
  // 1 ==> Response recieved from oracle engine to webhook
  // 2 ==> Error recieved from oracle engine to webhook
  // 3 ==> No solution error recieved from oracle engine to webhook

  useEffect(() => {
    if (checkRun) {
      if (checkRun?.uuid && checkRun?.status?.value === 1) {
        history.push(`/generated-case/${checkRun?.uuid}`);
      } else if (checkRun?.uuid && checkRun?.status?.value > 1) {
        if(checkRun?.status?.value == 3){
          setStepAfterError(1)
        }
        setErrorMessage(checkRun?.error_message)
        setInvalidDataError(true);
        setCheckForRoute(false);
      } else {
        setCheckForRoute(true);
      }
    }
  }, [checkRun]);

  const generateRouteApi = useMutation(
    (data: FormData) => {
      return axiosInstance({
        method: 'POST',
        url: '/generate-route/',
        data: data,
        headers: { 'Content-Type': 'multipart/form-data' },
      });
    },
    {
      onSuccess: (response) => {
        if (response?.data?.uuid) {
          setGenerateUuid(response.data.uuid);
        }
      },
      onError: (err) => {
        setInvalidDataError(true);
        setCheckForRoute(false);
      },
    }
  );

  const { data: fleetList, isFetching: fleetDetailsFetching } =
    useFleetDetail(query);

  const handleStepID = (id: number) => {
    setStepID(Number(id));
  };
  const handleFleetListFetch = (query: { fleet_type: number }) => {
    setQuery(query);
  };

  const resetAllData = (val:number) => {
    if(val !== 1){
      setCsvJSON('');
      setCsvFormatFile(undefined);
    }
    setStepID(STEPS_LIST[val]?.step);
    setCheckForRoute(false);
    setFleetData(undefined);
    setGenerateUuid('');
    setInvalidDataError(false);
  };

  const handleSetCsvData = (csv: string, filename: string) => {
    const _csv = ConvertToCSV(JSON.parse(csv));
    var blob = new Blob([_csv], { type: 'text/csv' });
    var csvFile = new File([blob], filename, { type: 'text/csv' });
    setCsvFormatFile(csvFile);
    setCsvJSON(csv);
    setCSVName(filename);
  };

  const handleSetFleetData = (val: TFleetData) => {
    setFleetData(val);
  };

  const handleOptimizationData = (optimization_data) => {
    setCheckForRoute(true);
    setOptimizationData(optimization_data);

    let generateRouteFormData = new FormData();

    if (csvFormatFile && csvJSON) {
      generateRouteFormData.append('csv_input_data', csvJSON);
      generateRouteFormData.append('csv_file', csvFormatFile);
    }
    if (fleetData) {
      for (const property in fleetData) {
        generateRouteFormData.append(property, fleetData[property]);
      }
    }
    if (optimization_data) {
      for (const property in optimization_data) {
        generateRouteFormData.append(property, optimization_data[property]);
      }
    }

    generateRouteApi.mutate(generateRouteFormData);
  };

  return (
    <section className={styles.generateRoutesPageWrapper}>
      <div className={styles.generateRoutesPage}>
        <Grid columns='repeat(1, 1fr)'>
          <div className={styles.generateRoutesSteps}>
            <div className={styles.generateRoutesBox}>
              <div className={styles.generateRoutesTextWrapper}>
                <img
                  src={images.riderDetailSidebar}
                  alt='cover'
                  className={styles.sidebarCoverImg}
                />
                <h4
                  sx={{
                    variant: 'text.subhead',
                    color: 'white',
                    fontWeight: 'body',
                  }}>
                  Generate Routes
                </h4>
                <ul className={`listUnstyled ${styles.stepList} supportText`}>
                  {STEPS_LIST.map((item) => {
                    return (
                      <li
                        className={` ${styles.stepListItem} ${
                          stepID === item.step ? styles.active : ''
                        } ${stepID > item.step ? styles.completed : ''}`}
                        key={item.label}>
                        <span
                          sx={{ variant: 'body-small', fontWeight: 'body' }}>
                          {item.label}
                        </span>
                      </li>
                    );
                  })}
                </ul>
              </div>
            </div>
            <Fragment>
              {stepID === 1 && (
                <RiderDetail
                  handleStepID={(step) => handleStepID(step)}
                  onContinue={handleSetCsvData}
                />
              )}
              {stepID === 2 && (
                <div className={styles.sidebarPaddingWrapper}>
                  <FleetDetail
                    handleStepID={(step) => handleStepID(step)}
                    fleetList={fleetList}
                    handleFleetListFetch={handleFleetListFetch}
                    fleetDetailsFetching={fleetDetailsFetching}
                    handleSetFleetData={handleSetFleetData}
                  />
                </div>
              )}
              {stepID === 3 && (
                <div className={styles.sidebarPaddingWrapper}>
                  <Optimization
                    handleOptimizationData={handleOptimizationData}
                    loader={checkForRoute}
                    csvName={csvName}
                    reset={resetAllData}
                    errorMessage={errorMessage}
                    invalidDataError={invalidDataError}
                    stepAfterError={stepAfterError}
                  />
                </div>
              )}
            </Fragment>
          </div>
        </Grid>
      </div>
    </section>
  );
};

export default GenerateRoute;
