import { useState, useEffect, FC } from 'react';
import { GanttChart } from 'ibm-gantt-chart-react';

import styles from '../../../assets/styles/case-study.module.scss';
import 'ibm-gantt-chart/dist/ibm-gantt-chart.css';
import { IScenariosResponse } from '../apiServices';

import CasestudyShimmer from './Shimmer';

const config = {
  data: {
    // Configures how to fetch resources for the Gantt
    resources: {
      // data: data, // resources are provided in an array. Instead, we could configure a request to the server.
      // Activities of the resources are provided along with the 'activities' property of resource objects.
      // Alternatively, they could be listed from the 'data.activities' configuration.
      activities: 'activities',
      name: 'name', // The name of the resource is provided with the name property of the resource object.
      id: 'id', // The id of the resource is provided with the id property of the resource object.
    },
    // Configures how to fetch activities for the Gantt
    // As activities are provided along with the resources, this section only describes how to create
    // activity Gantt properties from the activity model objects.
    activities: {
      start: 'start', // The start of the activity is provided with the start property of the model object
      end: 'end', // The end of the activity is provided with the end property of the model object
      name: 'name', // The name of the activity is provided with the name property of the model object
    },
  },
  timeWindow: {
    // Fetch the time window returned as an object with the format
    url: `/timewindow.json`,
    start: 'windowStart', // The start of the time window is provided with the 'windowStart' property of the returned time window object
    end: 'windowEnd', // The end of the time window is provided with the 'windowStart' property of the returned time window object
  },
  timeTable: {
    renderer: {
      background: function (activity: { color: string }) {
        return activity.color;
      },
      color: 'automatic',
      tooltipProperties: function (
        activity: {
          id: string;
          count: number;
          no_of_seats: number;
          percentage: number;
        },
        ctx
      ) {
        return [
          `Ride : `,
          ` ${activity.id}`,
          `Riders : `,
          `${activity.count}`,
          `Seater :`,
          `${activity.no_of_seats} (${activity.percentage}% Utilized)`,
        ];
      },
    },
    layout: {
      strategy: function () {
        return `tile`;
      },
    },
  },
  table: {
    columns: [
      {
        title: `New title`,
      },
    ],
  },
};

interface IGanttCharts {
  scenarioDatFetching: boolean;
  scenariosData: IScenariosResponse | undefined;
  handleScrollGanttChart: (event: CustomEvent) => void;
}

interface IGraphData {
  id: number;
  name: string;
  activities: {
    id: number;
    name: string;
    start: number;
    end: number;
    percentage: number;
    count: number;
    no_of_seats: number;
    color: string;
  }[];
}

const GanttCharts: FC<IGanttCharts> = ({
  scenarioDatFetching,
  scenariosData,
  handleScrollGanttChart,
}) => {
  const [loader, setLoader] = useState(true);
  const [graphData, setGraphData] = useState<Array<IGraphData>>([]);
  const [graphCofig, setGraphConfig] = useState<any>(config);
  useEffect(() => {
    if (scenariosData?.calender_view?.length) {
      let graphDataUpdated = scenariosData.calender_view;
      let _graphDataUpdated: Array<IGraphData> = graphDataUpdated.map(
        (item, i) => {
          return {
            id: item.id,
            name: item.name,
            activities: item.scheduler.map((activity) => {
              return {
                id: i + 1,
                name: activity.name,
                start: new Date(activity.from_date).getTime(),
                end: new Date(activity.to_date).getTime(),
                percentage: Number(String(activity.percentage.toFixed(2))),
                count: activity.count,
                no_of_seats: activity.no_of_seats,
                color: item.color_code,
              };
            }),
          };
        }
      );
      setGraphData(_graphDataUpdated);
      setGraphConfig({
        ...graphCofig,
        data: {
          ...graphCofig['data'],
          resources: {
            ...graphCofig['data']['resources'],
            data: _graphDataUpdated,
          },
        },
      });
      setLoader(false);
      setTimeout(() => {
        const rightDiv = document.getElementsByClassName(`time-table-scroller`);
        if (rightDiv && rightDiv.length) {
          rightDiv[0].addEventListener(
            'scroll',
            handleScrollGanttChart as EventListener
          );
        }
      }, 100);
    } else {
      setLoader(true);
    }
  }, [scenariosData]);

  return (
    <div className={styles.ganttChartWrapper}>
      {loader || scenarioDatFetching ? (
        <div className={`gridCenteredd ${styles.spinner}`}>
          <CasestudyShimmer type='GanttChart' />
        </div>
      ) : graphData && graphData.length ? (
        <GanttChart
          className='custom-ganttchart'
          config={graphCofig}
          data={graphData}
        />
      ) : (
        `No routes available`
      )}
    </div>
  );
};

export default GanttCharts;
