//Styped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "theme-ui";
import { Fragment, FC, useState, UIEvent, useEffect } from "react";

import styles from "../../../assets/styles/case-study.module.scss";
import { NumberTag, RouteList, RouteListItem } from "./StyledComponents";
import { images } from "../../../constants";
import { NoResult } from "../../../shared_ui_components";
import { Indices } from "./";
import { IScenariosResponse } from "../apiServices";
import { searchFunctionality } from "../../../utils";
import CasestudyShimmer from "./Shimmer";

type TRouteDetailProps = {
  sidebarView: string;
  handleSidebarView: (val: string) => void;
  scenariosData: IScenariosResponse | undefined;
  scenarioDatFetching: boolean;
  handleOpenRoutesDrawer: (state: boolean, id: string, num: number) => void;
  handleScrollRoutesList: (e: UIEvent<HTMLDivElement>) => void;
  selectedRouteId: string;
  handleSelectRouteId: (val: string, from: string) => void;
  canGenerateNewRoute: boolean;
};

type TEmployeeProps = {
  end_station_name: string;
  color_code: string;
  id: number;
  riders: string;
  start_station_name: string;
  total_riders: number;
  total_vehicles: number;
  route_id: string;
};

const RoutesDetail: FC<TRouteDetailProps> = ({
  sidebarView,
  handleSidebarView,
  scenariosData,
  scenarioDatFetching,
  handleOpenRoutesDrawer,
  handleScrollRoutesList,
  handleSelectRouteId,
  selectedRouteId,
  canGenerateNewRoute,
}) => {
  const [searchText, setsearchText] = useState<string | null>(null);
  const [searchScenarioData, setSearchScenarioData] = useState<
    Array<TEmployeeProps> | undefined
  >([]);

  useEffect(() => {
    setsearchText("");
    if (scenariosData?.route_details?.length) {
      const { route_details } = scenariosData;
      const employeesAddedData = route_details.map((item) => {
        return {
          end_station_name: item.end_station_name,
          color_code: item.color_code,
          id: item.id,
          riders: item.riders.map((rider) => rider?.name).join(" "),
          start_station_name: item.start_station_name,
          total_riders: item.total_riders,
          total_vehicles: item.total_vehicles,
          route_id: item.route_id,
        };
      });
      setSearchScenarioData(employeesAddedData);
    }
  }, [scenariosData]);
  return (
    <Fragment>
      <div className={styles.headerBlockWrapper}></div>
      {!scenarioDatFetching && scenariosData?.route_details ? (
        <Fragment>
          <div
            className={`${styles.allRoutesOuterWrapper} ${
              sidebarView !== "performance-index"
                ? styles.viewRoute
                : styles.hideRoute
            }`}
            onScroll={handleScrollRoutesList}
            id="allRoutesDiv"
          >
            {sidebarView === "performance-index" && (
              <Indices
                handleSidebarView={() => {
                  handleSidebarView("route-detail");
                }}
                performanceIndicesData={
                  scenariosData?.scenario?.performance_indices
                }
                canGenerateNewRoute={canGenerateNewRoute}
              />
            )}
            <div
              className={styles.innerWrapper}
              onMouseLeave={() => {
                if (selectedRouteId) {
                  handleSelectRouteId("", "sidebar");
                }
              }}
            >
              <h4
                className={`heading5 ${styles.heading5}`}
                sx={{
                  variant: "text.headline",
                  color: "black",
                  fontWeight: "body",
                }}
              >
                All Routes
              </h4>
              <span
                className={styles.closeIcon}
                onClick={() => handleSidebarView("performance-index")}
              >
                <img src={images.closeFloaterIcon} alt="" />
              </span>
              <ul className={`listUnstyled ${styles.allRoutesWrapper}`}>
                {searchFunctionality(
                  searchScenarioData,
                  "riders",
                  searchText ? searchText : ""
                )?.map((item, i) => {
                  return (
                    <li
                      className={`${
                        item.route_id === selectedRouteId ? styles.active : ""
                      } ${styles.eachRouteItem}`}
                      key={item.id}
                      onMouseOver={() => {
                        if (item.route_id !== selectedRouteId) {
                          handleSelectRouteId(item.route_id, "sidebar");
                        }
                      }}
                      onClick={() =>
                        handleOpenRoutesDrawer(true, String(item.id), i + 1)
                      }
                      id={`routes-${item.route_id}`}
                    >
                      <div
                        className={`${styles.RouteNumber} flexCentered`}
                        sx={{
                          variant: "text.caption-large",
                          color: "black",
                          fontWeight: "body",
                        }}
                      >
                        Route:{" "}
                        <NumberTag color={item?.color_code}>{i + 1}</NumberTag>
                      </div>
                      <RouteList>
                        <RouteListItem color={item?.color_code}>
                          {item.start_station_name}
                        </RouteListItem>
                        <RouteListItem color={item?.color_code}>
                          {item.end_station_name}
                        </RouteListItem>
                      </RouteList>
                      <ul className={`listInline ${styles.routeInformation}`}>
                        <li className="listInlineItem">
                          <span
                            sx={{
                              variant: "text.body-large",
                              color: "black",
                              fontWeight: "body",
                            }}
                          >
                            <img
                              src={images.userOutlinedIcon}
                              alt="userOutlinedIcon"
                            />
                            {item.total_riders}
                          </span>
                        </li>
                        <li className="listInlineItem">
                          <span
                            sx={{
                              variant: "text.body-large",
                              color: "black",
                              fontWeight: "body",
                            }}
                          >
                            <img
                              src={images.carOutlinedIcon}
                              alt="carOutlinedIcon"
                            />
                            {item.total_vehicles}
                          </span>
                        </li>
                      </ul>
                    </li>
                  );
                })}
              </ul>
              {searchFunctionality(
                searchScenarioData,
                "riders",
                searchText ? searchText : ""
              )?.length === 0 &&
                searchText && (
                  <NoResult
                    title="No Result"
                    subtext={
                      <Fragment>
                        We didn't find any employees
                        <br /> that matched your search
                      </Fragment>
                    }
                    height="80%"
                  />
                )}

              <div className={styles.bottomWrapper}>
                <h3
                  sx={{
                    variant: "text.caption-small",
                    color: "black",
                    fontWeight: "body",
                  }}
                >
                  Total Routes: {scenariosData?.route_details?.length}
                  <span></span>
                  Total Riders: {scenariosData?.total_riders}
                </h3>
              </div>
            </div>
          </div>
        </Fragment>
      ) : (
        <div className="gridCenteredd">
          <CasestudyShimmer type="RoutesDetail" />
        </div>
      )}
    </Fragment>
  );
};

export default RoutesDetail;
