//Styped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, useEffect, FC, Fragment } from 'react';
import Drawer from '@swvl/drawer';
import { Link, useParams } from 'react-router-dom';


import styles from '../../../assets/styles/case-study.module.scss';
import { NumberTag, BorderTag } from './StyledComponents';
import { images } from '../../../constants';
import { useRideDetail } from '../apiServices';
import CasestudyShimmer from './Shimmer';
import { TGlobalParamsType } from '../../../types';

const compare = (v1: number, v2: number) => {
  return v1 === v2;
};

interface IScheduleProps {
  color: string;
  textData: string;
  travelDuration: string;
  text: string;
  bottomExtension: boolean;
}

const Schedule: FC<IScheduleProps> = ({
  color,
  textData,
  bottomExtension,
  travelDuration,
  text,
}) => {
  return (
    <div className={styles.schedule}>
      <ul className={`listInline`}>
        <li className={`listInlineItem  ${styles.time}`}>
          <span
            sx={{
              variant: 'text.body-small',
              color: 'dimgray',
              fontWeight: 'body',
            }}>
            {textData}
          </span>
        </li>
        <li className='listInlineItem'>
          {
            //@ts-ignore
            <span
              className={`${styles.hollowCircle} ${
                bottomExtension ? styles.bottomExtension : ' '
              }`}
              //@ts-ignore
              style={{ '--tool-color': color }}></span>
          }
        </li>
        <li
          sx={{
            variant: 'text.body-small',
            color: 'dimgray',
            fontWeight: 'body',
          }}
          className={`listInlineItem ${
            travelDuration ? styles.travelDuration : ''
          }`}
          //@ts-ignore
          travel={`Travel Duration: ${travelDuration}`}>
          {text}
        </li>
      </ul>
    </div>
  );
};

interface IRoutesDrawerProps {
  open: boolean;
  routeId: string;
  rideNo: number;
  onClose: () => void;
}

const RoutesDrawer: FC<IRoutesDrawerProps> = ({
  open,
  onClose,
  routeId,
  rideNo,
}) => {
  const { uid, suid, gsuid, guid } = useParams<TGlobalParamsType>();
  const [anchor, setAnchor] = useState(false);

  const { data: rideDetailList, isFetching: rideDetailFetching } =
    useRideDetail({
      run_uuid: uid ? uid : guid,
      item_uuid: suid ? suid : gsuid,
      route_id: routeId,
    });

  useEffect(() => {
    setAnchor(open);
  }, [open]);

  const handleSetAnchor = (val: boolean) => {
    onClose();
    setAnchor(val);
  };

  return (
    <Drawer
      isOpen={anchor}
      size={'full'}
      anchor={'right'}
      clickOutSideToClose={true}
      closeDrawer={() => handleSetAnchor(false)}>
      <div className={styles.drawerWrapper}>
        <div className={styles.drawerContainer}>
          {rideDetailFetching || rideNo === 0 ? (
            <CasestudyShimmer type='RouteDrawer' />
          ) : (
            <div>
              <div className={styles.routeDetailWrapper}>
                <p
                  className='flexCentered'
                  sx={{
                    variant: 'text.body-large',
                    color: 'dimgray',
                    fontWeight: 'bold',
                  }}>
                  Route:
                  <NumberTag color={String(rideDetailList?.color_code)}>
                    {rideNo}
                  </NumberTag>
                </p>

                <h6
                  sx={{
                    variant: 'text.subhead',
                    color: 'dimgray',
                    fontWeight: 'bold',
                  }}>
                  Ride:
                  <BorderTag color={String(rideDetailList?.color_code)}>
                    {rideNo}
                  </BorderTag>
                </h6>
                <ul className='listInline'>
                  <li className='listInlineItem'>
                    <span
                      sx={{
                        variant: 'text.body-small',
                        color: 'dimgray',
                        fontWeight: 'body',
                      }}>
                      <img
                        src={images.userOutlinedIcon}
                        alt='userOutlinedIcon'
                      />{' '}
                      Riders : {rideDetailList?.total_riders}
                    </span>
                  </li>
                </ul>
              </div>
              <div className={styles.complexRouteList}>
                {rideDetailList?.rides.map((item, i) => {
                  return (
                    <Link
                      to={
                        uid && suid
                          ? `/employee/${uid}/detail/${suid}/ride/${routeId}/${rideNo}`
                          : `/employee/${guid}/detail/${gsuid}/ride/${routeId}/${rideNo}`
                      }>
                      <div className={styles.complexRouteBox}>
                        <div
                          className={`${styles.complexRouteBoxInner} ${
                            compare(i, rideDetailList.rides.length - 1)
                              ? styles.noBorder
                              : ''
                          }`}>
                          <div className={styles.topLeftWrapper}>
                            <Schedule
                              bottomExtension={false}
                              text={'Arrival'}
                              textData={item.arrival_time}
                              color={rideDetailList?.color_code}
                              travelDuration={''}
                            />
                          </div>
                          <div className={styles.bottomLeftWrapper}>
                            {compare(i, rideDetailList.rides.length - 1) ? (
                              <Schedule
                                bottomExtension={false}
                                text={''}
                                textData={''}
                                color={'#fff'}
                                travelDuration={''}
                              />
                            ) : (
                              <Schedule
                                bottomExtension={true}
                                text={'Departure'}
                                textData={item.departure_time}
                                color={rideDetailList?.color_code}
                                travelDuration={`${parseInt(
                                  item.travel_time
                                )} min`}
                              />
                            )}
                          </div>
                          <ul className={`${styles.rideLocation} listInline`}>
                            <li className={`listInlineItem`}>
                              <p
                                sx={{
                                  variant: 'text.body-large',
                                  color: 'dimgray',
                                  fontWeight: 'body',
                                }}>
                                {item.station_name}
                              </p>
                              <div
                                className={`${styles.rideNumber}`}
                                sx={{
                                  variant: 'text.body-small',
                                  color: 'darkgray',
                                  fontWeight: 'body',
                                }}>
                                {!compare(
                                  i,
                                  rideDetailList.rides.length - 1
                                ) && (
                                  <Fragment>
                                    <img
                                      src={images.userOutlinedIcon}
                                      alt='userOutlinedIcon'
                                    />
                                    Rider : {item?.total_riders}
                                  </Fragment>
                                )}
                              </div>
                            </li>
                            <li className={`listInlineItem`}>
                              <img
                                src={images.rightArrowGray}
                                alt='rightArrowGray'
                              />
                            </li>
                          </ul>
                        </div>
                      </div>
                    </Link>
                  );
                })}
              </div>
            </div>
          )}
        </div>
      </div>
    </Drawer>
  );
};

export default RoutesDrawer;
