import styles from '../../../assets/styles/rider-detail.module.scss';
import { useState, FC } from 'react';
import FileUpload from './FileUpload';
import TableCSV from './TableCSV';
import ClusterMapView from './ClusterMapView';
import { replaceKeys } from '../../../utils/index';

type TRideDetailProps = {
  handleStepID: (val: number) => void;
  onContinue: (val: any, filename: string) => void;
};

type TKeyValue = {
  [key: string]: boolean | string | number;
};

const replaceArrKeys = (arr: Array<TKeyValue>)=> {
  return arr?.length !== 0
    ? arr?.map((item) => {
        return replaceKeys(item);
      })
    : arr;
}

const RiderDetail: FC<TRideDetailProps> = ({ handleStepID, onContinue }) => {
  const [step, setStep] = useState(1);
  const [csvData, setCsvData] = useState<Array<{}>>();
  const [csvRaw, setCsvRaw] = useState<string>('');
  const [csvName, setCsvName] = useState('');

  return (
    <>
      {step === 1 && (
        <div className={styles.sidebarPaddingWrapper}>
          <div className={styles.rideDetailWrapper}>
            <FileUpload
              onContinue={(step: number) => {
                setStep(step);
              }}
              onFileUpload={(data) => {
                setCsvData(data);
              }}
              onDragged={(name: string) => {
                setCsvName(name);
              }}
            />
          </div>
        </div>
      )}
      {step === 2 && (
        <div className={styles.sidebarPaddingWrapper}>
          <div className={styles.rideDetailWrapper}>
            <TableCSV
              onContinue={(step: number) => {
                setStep(step);
              }}
              fileData={csvData}
              onEditFile={(data: Array<{}> | undefined, raw: string) => {
                if (data) {
                  setCsvData(replaceArrKeys(data));
                  setCsvRaw(raw);
                }
              }}
              csvName={csvName}
            />
          </div>
        </div>
      )}
      {step === 3 && (
        <div className={styles.sidebarPaddingWrapper}>
          <div className={styles.rideDetailWrapper}>
            <ClusterMapView
              onContinue={(step) => {
                if (step > 3) {
                  handleStepID(2);
                  onContinue(csvRaw, csvName);
                } else {
                  setStep(step);
                }
              }}
              fileData={csvData}
            />
          </div>
        </div>
      )}
    </>
  );
};

export { RiderDetail };
