//Styped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "theme-ui";
import { useParams, withRouter, RouteComponentProps } from "react-router-dom";
import { FC } from "react";

import styles from "../../../assets/styles/case-study.module.scss";
import { images } from "../../../constants";
import { checkSidebarRoute } from "../../../utils";
import { TGlobalParamsType } from "../../../types";

interface TSidebarHeaderProps extends RouteComponentProps {
  sidebarView: string;
  handleSidebarView: (val: string) => void;
  downloadFile: boolean;
  showSwitchRoute: boolean;
  downloadFileUrl: string | undefined;
}

const SidebarHeader: FC<TSidebarHeaderProps> = ({
  sidebarView,
  handleSidebarView,
  downloadFile,
  showSwitchRoute,
  downloadFileUrl,
}) => {
  const { type } = useParams<TGlobalParamsType>();

  return (
    <div className={styles.headerBlock}>
      <ul className="listInline">
        <li className={`${styles.leftBlock} listInlineItem`}>
          <span
            sx={{
              variant: "text.headline",
              color: "black",
              fontWeight: "body",
            }}
          >
            {checkSidebarRoute(type ?? "")?.label.prefix}
            <p
              sx={{
                variant: "text.body-small",
                color: "black",
                fontWeight: "body",
              }}
            >
              {checkSidebarRoute(type ?? "")?.label.sufix}
            </p>
          </span>
          {showSwitchRoute && (
            <p
              sx={{
                variant: "text.body-large",
                color: "crimson",
                fontWeight: "body",
              }}
              onClick={() => {
                handleSidebarView(
                  sidebarView === "route-detail"
                    ? "performance-index"
                    : "route-detail"
                );
              }}
            >
              {sidebarView === "route-detail"
                ? "View Performance Indices"
                : "View Route Details"}
            </p>
          )}
        </li>
        {downloadFile && downloadFileUrl && (
          <li className={`${styles.rightBlock} listInlineItem`}>
            <a href={downloadFileUrl} target="_blank" rel="noreferrer">
              <img src={images.downloadHtmlIcon} alt="downloadHtmlIcon" />
            </a>
          </li>
        )}
      </ul>
    </div>
  );
};

export default withRouter(SidebarHeader);
