/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, FC, MouseEvent } from 'react';
import Button from '@swvl/button';
import Popup from '@swvl/popup';

type TLogoutDialogProps = {
  open: boolean;
  text: string;
  handleCancel: (val: boolean) => void;
  handleSuccess: (val: MouseEvent<HTMLButtonElement>) => void;
};

const LogOutDialog: FC<TLogoutDialogProps> = ({
  open,
  text,
  handleCancel,
  handleSuccess,
}) => {
  const [loader, setLoader] = useState(false);
  const handleClose = () => {
    handleCancel(false);
  };

  return (
    <div>
      <Popup
        isOpen={open}
        closePopup={handleClose}
        aria-labelledby='scroll-dialog-title'
        sx={{ zIndices: 'modal' }}>
        <div className='dsp-dialogs logOutDialog'>
          <h3
            className='textCenter'
            sx={{
              variant: 'text.title-small',
              color: 'slategray',
              fontWeight: 'body',
            }}>
            {text}
          </h3>
          <ul className='listInline'>
            <li className='listInlineItem'>
              <Button variant='secondary' onClick={() => handleCancel(false)}>
                No
              </Button>
            </li>
            <li className='listInlineItem'>
              <Button
                variant='primary'
                disabled={loader ? true : false}
                onClick={(e: MouseEvent<HTMLButtonElement>) => {
                  setLoader(true);
                  handleSuccess(e);
                }}>
                {loader ? 'Please wait...' : 'Yes'}
              </Button>
            </li>
          </ul>
        </div>
      </Popup>
    </div>
  );
};

export default LogOutDialog;
