/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, ChangeEvent, FC, Fragment } from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { Radio, RadioGroup } from '@swvl/radio';
import Grid from '@swvl/grid';
import Icon from '@swvl/icon';
import { Select } from '@swvl/select';
import Input from '@swvl/input';
import Button from '@swvl/button';

import GenerateRoutesShimmer from './Shimmer';
import { images } from '../../../constants';
import { CustomSlider, NoResult } from '../../../shared_ui_components';
import styles from '../../../assets/styles/rider-detail.module.scss';

// Optimisation Preferences for Scenario Types - type
// - 1 : Balanced for Cost
// - 2 : Balanced for Time
// - 3 : Low Cost
// - 4 : Low Travel Time

interface IOptimizationProps extends RouteComponentProps {
  handleOptimizationData: (val: any) => void;
  loader: boolean;
  invalidDataError: boolean;
  csvName: string;
  errorMessage:string;
  stepAfterError:number;
  reset: (val:number) => void;
}

interface errors {
  duration_distance?: string;
  need_oracle_station?: string;
  grouping_distance?: string;
  optimization?: string;
}

const optimization_preference_init = [
  {
    type: 1,
    label: (
      <Fragment>
        <span className='bodyMedium'>
          Balanced <p className='supportText'>for Travel Time</p>
        </span>
      </Fragment>
    ),
    image: images.clockDollarIcon,
    selected: true,
  },
  {
    type: 2,
    label: (
      <Fragment>
        <span className='bodyMedium'>
          Balanced <p className='supportText'>for Cost</p>
        </span>
      </Fragment>
    ),
    image: images.dollarClockIcon,
    selected: true,
  },
  {
    type: 3,
    label: (
      <Fragment>
        <span className='bodyMedium'>Lowest Cost</span>
      </Fragment>
    ),
    image: images.dollarIcon,
    selected: true,
  },
  {
    type: 4,
    label: (
      <Fragment>
        <span className='bodyMedium'>Lowest Travel Time</span>
      </Fragment>
    ),
    image: images.clockIcon,
    selected: true,
  },
];

const distanceOptions = [
  { value: 'km', label: 'Km' },
  { value: 'meter', label: 'Meters' },
];

const convertToMeters = (value: string, unit: string) => {
  if (unit === 'km') {
    return Number(value) * 1000;
  } else {
    return Number(value);
  }
};

const convertToSeconds = (value: string, unit: string) => {
  if (unit === 'days') {
    return Number(value) * 24 * 60 * 60;
  } else if (unit === 'hours') {
    return Number(value) * 60 * 60;
  } else {
    return Number(value) * 60;
  }
};

const durationOptions = [
  { value: 'minutes', label: 'Minutes' },
  { value: 'hours', label: 'Hours' },
  { value: 'days', label: 'Days' },
];

const Optimization: FC<IOptimizationProps> = ({
  handleOptimizationData,
  stepAfterError,
  loader,
  csvName,
  reset,
  invalidDataError,
  errorMessage,
}) => {
  const [addCustomSetting, setAddCustomSetting] = useState(true);

  const [optimizationData, setOptimizationData] = useState({
    max_route_meters: '',
    distance_unit: { value: 'km', label: 'Km' },
    max_routes_seconds: '',
    duration_unit: { value: 'minutes', label: 'Minutes' },
    capacity_limitation: 100,
    station_wait: 2,
    need_oracle_station: 'No',
    walk_to_station: '',
    group_corporate_location: 'No',
    grouping_distance: '',
  });
  const [disableBtn, setDisableBtn] = useState(false);
  const [errors, setErrors] = useState<errors>({});

  const updateIfNumber = (val: string, prevValue: string) => {
    var reg = /^[0-9]+$/;
    if (val) {
      return reg.test(val) ? val : prevValue;
    } else {
      return val;
    }
  };

  const handleChangeOptimizationData = (
    key: string,
    value: string | number | { value: string; label: string }
  ) => {
    const numbersOnly = [
      'max_route_meters',
      'max_routes_seconds',
      'capacity_limitation',
      'station_wait',
      'walk_to_station',
      'grouping_distance',
    ];
    let _optimizationData;
    if (key) {
      if (typeof value === 'string' && numbersOnly.includes(key)) {
        _optimizationData = {
          ...optimizationData,
          ...{ [key]: updateIfNumber(value, optimizationData[key]) },
        };
      } else {
        _optimizationData = {
          ...optimizationData,
          ...{ [key]: value },
        };
      }

      setOptimizationData(_optimizationData);
    }
  };

  const validateOptimizationData = () => {
    if (Object.keys(optimizationData)?.length) {
      if (optimizationData.need_oracle_station === 'Yes') {
        if (!optimizationData.walk_to_station) {
          setErrors({
            ...errors,
            need_oracle_station: 'Walk To Station values are required',
          });
          return;
        }
      }

      if (optimizationData.group_corporate_location === 'Yes') {
        if (!optimizationData.grouping_distance) {
          setErrors({
            ...errors,
            grouping_distance: 'Group Distance Radius values are required',
          });
          return;
        }
      }

      return {
        station_wait: optimizationData.station_wait,
        capacity_limitation: optimizationData.capacity_limitation,
        max_route_meters: optimizationData.max_route_meters
          ? convertToMeters(
              optimizationData.max_route_meters,
              optimizationData.distance_unit.value
            )
          : '',
        max_routes_seconds: optimizationData.max_routes_seconds
          ? convertToSeconds(
              optimizationData.max_routes_seconds,
              optimizationData.duration_unit.value
            )
          : '',
        walk_to_station: optimizationData.walk_to_station,
        grouping_distance: optimizationData.grouping_distance,
      };
    } else {
      setErrors({
        ...errors,
        optimization: 'Optimization Data Required',
      });
      return;
    }
  };

  return (
    <div className={styles.optimizationWrapper}>
      {loader ? (
        <GenerateRoutesShimmer type='Optimization' />
      ) : (
        <div>
          {invalidDataError ? (
            <NoResult
              title={
                <div>
                  <p className='paragraph red'>
                    <Icon name='attachment' fill='#ff0000' /> {csvName}
                  </p>
                  <br />
                  <h4 className='heading3'>Oops!</h4>
                </div>
              }
              height='500px'
              image={images.csvErrorIcon}
              subtext={errorMessage ? errorMessage :`We could not process your rider details due to incorrect information `}
              buttonText='Start Over'
              onClick={() => {
                reset(stepAfterError);
              }}
            />
          ) : (
            <div>
              <h4
                sx={{
                  variant: 'text.headline',
                  color: 'black',
                  fontWeight: 'bold',
                }}>
                Configure remaining fields required for route generation
              </h4>
              <div className={`flexNotCentered ${styles.optimizationItem}`}>
                <Grid gap={3} columns='repeat(4, 1fr)'>
                  {optimization_preference_init?.map((item) => {
                    return (
                      <div
                        key={item.type}
                        className={`${styles.optimizationItems} ${
                          item.selected ? styles.active : ''
                        }`}>
                        <div className={`${styles.imageWrapper} textCenter`}>
                          <img src={item.image} alt={`${item.type} item`} />
                        </div>
                        <div
                          className={styles.bottomText}
                          sx={{
                            variant: 'text.body-large',
                            color: 'black',
                            fontWeight: 'body',
                          }}>
                          {item.label}
                        </div>
                      </div>
                    );
                  })}
                </Grid>
              </div>
              <div className={styles.customOptimizationWrapper}>
                <Grid
                  gap={1}
                  columns={
                    addCustomSetting ? `repeat(1, 1fr)` : `repeat(2, 1fr)`
                  }>
                  <div
                    className={`${styles.addCustomOptimization} ${
                      addCustomSetting ? styles.active : ''
                    } `}
                    onClick={() => {
                      if (!addCustomSetting) {
                        setAddCustomSetting(true);
                      }
                    }}>
                    <div className={styles.outerWrapper}>
                      <Grid
                        gap={3}
                        columns='repeat(1, 1fr)'
                        className={styles.titleBlk}>
                        <span
                          sx={{
                            variant: 'text.body-small',
                            color: 'black',
                            fontWeight: 'body',
                          }}
                          className={`flexCentered ${styles.buttonTitle}`}
                          onClick={() => {
                            setAddCustomSetting(!addCustomSetting);
                          }}>
                          <img
                            src={
                              addCustomSetting
                                ? images.settingsColorIcon
                                : images.settingsIcon
                            }
                            alt='settings'
                          />
                          Add Custom Optimization
                        </span>
                      </Grid>
                      {addCustomSetting && (
                        <Grid gap={3} columns='repeat(2, 1fr)'>
                          <div className={styles.addCustomOptimizationRow}>
                            <ul className='listInline flexCentered'>
                              <li
                                className='listInlineItem'
                                style={{ width: '26%' }}>
                                <Input
                                  height={'small'}
                                  label={'Distance'}
                                  placeholder={'Ex. 10'}
                                  value={optimizationData.max_route_meters}
                                  variant='plain'
                                  onChange={(
                                    e: ChangeEvent<HTMLInputElement>
                                  ) => {
                                    handleChangeOptimizationData(
                                      'max_route_meters',
                                      e.target.value
                                    );
                                    setErrors({
                                      ...errors,
                                      duration_distance: '',
                                    });
                                  }}
                                />
                              </li>
                              <li
                                className='listInlineItem'
                                style={{ width: '20%' }}>
                                <Select
                                  options={distanceOptions}
                                  placeholder={'Km'}
                                  isClearable={false}
                                  isSearchable={false}
                                  variant='plain'
                                  name='km'
                                  label='Unit'
                                  value={optimizationData.distance_unit}
                                  onChange={(val) => {
                                    if (val) {
                                      handleChangeOptimizationData(
                                        'distance_unit',
                                        val
                                      );
                                    }
                                  }}
                                />
                              </li>
                              <li
                                className='listInlineItem'
                                style={{ width: '26%' }}>
                                <Input
                                  height={'small'}
                                  placeholder={'Ex. 30'}
                                  label={'Duration'}
                                  value={optimizationData.max_routes_seconds}
                                  variant='plain'
                                  onChange={(
                                    e: ChangeEvent<HTMLInputElement>
                                  ) => {
                                    handleChangeOptimizationData(
                                      'max_routes_seconds',
                                      e.target.value
                                    );
                                    setErrors({
                                      ...errors,
                                      duration_distance: '',
                                    });
                                  }}
                                />
                              </li>
                              <li
                                className='listInlineItem'
                                style={{ width: '20%' }}>
                                <Select
                                  options={durationOptions}
                                  placeholder={'Hr'}
                                  isClearable={false}
                                  isSearchable={false}
                                  variant='plain'
                                  name='hr'
                                  label='Unit'
                                  value={optimizationData.duration_unit}
                                  onChange={(val) => {
                                    if (val) {
                                      handleChangeOptimizationData(
                                        'duration_unit',
                                        val
                                      );
                                    }
                                  }}
                                />
                              </li>
                            </ul>
                            {errors?.duration_distance ? (
                              <h6 className='errorMsg'>
                                {errors?.duration_distance}
                              </h6>
                            ) : null}
                          </div>
                          <div>
                            {
                              <div
                                className={styles.progressSlider}
                                //@ts-ignore
                                minval={'10%'}
                                maxval={'100%'}>
                                <div
                                  className={styles.progressSliderValueLabel}>
                                  <p className='paragraph'>
                                    {optimizationData.capacity_limitation}%
                                  </p>
                                </div>
                                <div className={styles.progressSliderLabel}>
                                  <p
                                    sx={{
                                      variant: 'text.body-large',
                                      color: 'black',
                                      fontWeight: 'body',
                                    }}>
                                    Capacity Limitation
                                  </p>
                                </div>
                                <CustomSlider
                                  step={1}
                                  max={100}
                                  min={1}
                                  disabled={false}
                                  hideThumb={false}
                                  value={[optimizationData.capacity_limitation]}
                                  handleChange={(val) => {
                                    handleChangeOptimizationData(
                                      'capacity_limitation',
                                      val[0]
                                    );
                                  }}
                                  label={'Capacity Limitation'}
                                />
                              </div>
                            }
                          </div>
                        </Grid>
                      )}
                    </div>
                  </div>
                </Grid>
              </div>
              <div className={styles.bottomOptions}>
                <Grid gap={2} columns='repeat(2, 1fr)'>
                  {
                    <div
                      className={styles.progressSlider}
                      //@ts-ignore
                      minval='1 min'
                      maxval='20 mins'>
                      <div className={styles.progressSliderValueLabel}>
                        <p className='paragraph'>
                          {optimizationData.station_wait} mins
                        </p>
                      </div>
                      <div className={styles.progressSliderLabel}>
                        <p
                          sx={{
                            variant: 'text.body-large',
                            color: 'black',
                            fontWeight: 'body',
                          }}>
                          Max Station Wait Time
                        </p>
                      </div>

                      <CustomSlider
                        step={1}
                        max={20}
                        min={1}
                        disabled={false}
                        hideThumb={false}
                        value={[optimizationData.station_wait]}
                        handleChange={(val) => {
                          handleChangeOptimizationData('station_wait', val[0]);
                        }}
                        label={'Max Station Wait Time'}
                      />
                    </div>
                  }
                </Grid>

                <div className={styles.optimizationRadio}>
                  <Grid gap={2} columns='repeat(2, 1fr)'>
                    <div>
                      <div className={styles.generateStation}>
                        <RadioGroup
                          label={'Would you like oracle to generate a stations'}
                          align={'horizontal'}
                          disabled={false}
                          name={'oracle-generate'}
                          value={optimizationData.need_oracle_station}
                          onChange={(e: ChangeEvent<HTMLInputElement>) => {
                            handleChangeOptimizationData(
                              'need_oracle_station',
                              e.target.value
                            );
                          }}>
                          <Radio
                            checked={
                              optimizationData.need_oracle_station === 'Yes'
                            }
                            value='Yes'
                            align='horizontal'
                            onChange={() => {}}
                            name='Yes'
                            tabIndex={1}>
                            Yes
                          </Radio>
                          <Radio
                            checked={
                              optimizationData.need_oracle_station === 'No'
                            }
                            value='No'
                            align='horizontal'
                            onChange={() => {}}
                            name='No'
                            tabIndex={2}>
                            No
                          </Radio>
                        </RadioGroup>
                      </div>
                    </div>
                    {optimizationData.need_oracle_station === 'Yes' && (
                      <div>
                        <Grid gap={3} columns='repeat(2, 1fr)'>
                          <div className={styles.inputBlk}>
                            <Input
                              height={'small'}
                              placeholder={'Ex. 500'}
                              label={'Walk to station '}
                              value={optimizationData.walk_to_station}
                              variant='plain'
                              onChange={(e: ChangeEvent<HTMLInputElement>) => {
                                handleChangeOptimizationData(
                                  'walk_to_station',
                                  e.target.value
                                );
                                setErrors({
                                  ...errors,
                                  need_oracle_station: '',
                                });
                              }}
                            />
                          </div>
                          <div className={styles.metricBlk}>
                            <Button
                              variant='gray'
                              onClick={() => {}}
                              style={{ background: '#F7F8F9' }}>
                              Meters
                            </Button>
                          </div>
                        </Grid>
                        {errors?.need_oracle_station ? (
                          <h6 className='errorMsg'>
                            {errors?.need_oracle_station}
                          </h6>
                        ) : null}
                      </div>
                    )}
                  </Grid>
                </div>

                <div className={styles.optimizationRadio}>
                  <Grid gap={2} columns='repeat(2, 1fr)'>
                    <div>
                      <div className={styles.generateStation}>
                        <RadioGroup
                          label={'Group corporate location'}
                          align={'horizontal'}
                          disabled={false}
                          name={'groupCorporateLocation'}
                          value={optimizationData.group_corporate_location}
                          onChange={(e: ChangeEvent<HTMLInputElement>) => {
                            handleChangeOptimizationData(
                              'group_corporate_location',
                              e.target.value
                            );
                          }}>
                          <Radio
                            checked={
                              optimizationData.group_corporate_location ===
                              'Yes'
                            }
                            value='Yes'
                            align='horizontal'
                            name='Yes'
                            onChange={() => {}}
                            tabIndex={1}>
                            Yes
                          </Radio>
                          <Radio
                            checked={
                              optimizationData.group_corporate_location === 'No'
                            }
                            value='No'
                            align='horizontal'
                            name='No'
                            onChange={() => {}}
                            tabIndex={2}>
                            No
                          </Radio>
                        </RadioGroup>
                      </div>
                    </div>
                    {optimizationData.group_corporate_location === 'Yes' && (
                      <div>
                        <Grid gap={3} columns='repeat(2, 1fr)'>
                          <div className={styles.inputBlk}>
                            <Input
                              height={'small'}
                              placeholder={'Ex. 500'}
                              label={'Grouping distance radius'}
                              value={optimizationData.grouping_distance}
                              variant='plain'
                              onChange={(e: ChangeEvent<HTMLInputElement>) => {
                                handleChangeOptimizationData(
                                  'grouping_distance',
                                  e.target.value
                                );
                                setErrors({
                                  ...errors,
                                  grouping_distance: '',
                                });
                              }}
                            />
                          </div>
                          <div className={styles.metricBlk}>
                            <Button
                              variant='gray'
                              onClick={() => {}}
                              style={{ background: '#F7F8F9' }}>
                              Meters
                            </Button>
                          </div>
                        </Grid>
                        {errors?.grouping_distance ? (
                          <h6 className='errorMsg'>
                            {errors?.grouping_distance}
                          </h6>
                        ) : null}
                      </div>
                    )}
                  </Grid>
                </div>

                <div className={styles.bottomContinueWrapper}>
                  <Button
                    variant='primary'
                    disabled={disableBtn}
                    onClick={() => {
                      const _optimizationData = validateOptimizationData();
                      if (_optimizationData) {
                        handleOptimizationData(_optimizationData);
                      }
                    }}>
                    Continue
                  </Button>
                </div>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default withRouter(Optimization);
