//STyped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { images } from '../../../constants';
import styles from '../../../assets/styles/auth.module.scss';
import Button from '@swvl/button';
import { MouseEvent, ChangeEvent, FC } from 'react';
import Input from '@swvl/input';
import { getYear } from 'date-fns';

type TSignInFormProps = {
  handleChange: (key: string, value: string) => void;
  error?: { [key: string]: string };
  formData: { email: string; password: string; password_show: boolean };
  loader?: boolean;
  togglePassword?: () => void;
  handleSubmit: () => void;
};

const SignInForm: FC<TSignInFormProps> = ({
  handleChange,
  error,
  formData,
  loader,
  handleSubmit,
  togglePassword,
}) => (
  <div className={styles.formWrapper + ' flexCentered'}>
    <div className={styles.form}>
      <h3
        sx={{
          variant: 'text.title-large',
          color: 'dimgray',
          fontWeight: 'body',
        }}>
        Sign In
      </h3>
      <div className='inputGroup'>
        <Input
          label={'Swvl Email ID'}
          placeholder={'Ex. john.doe@swvl.com'}
          value={formData.email}
          onChange={(e: ChangeEvent<HTMLInputElement>) =>
            handleChange('email', e.target.value)
          }
          name='email'
        />
        {error && (
          <h6 className={`${error.email ? 'errorMsg' : ''} flexCentered`}>
            {error.email ? error.email : ''}
          </h6>
        )}
      </div>
      <div className='inputGroup passwordGroup'>
        <Input
          label={'Password'}
          placeholder='********'
          value={formData?.password}
          type={formData?.password_show ? 'text' : 'password'}
          onChange={(e: ChangeEvent<HTMLInputElement>) =>
            handleChange('password', e.target.value)
          }
          onKeyPress={(e) => {
            if (e.key === 'Enter') {
              handleSubmit();
            }
          }}
        />
        <span
          className='passwordShow metaText'
          onClick={() => {
            if (togglePassword) {
              togglePassword();
            }
          }}>
          {formData?.password_show ? (
            <img src={images.hideEye} alt='Hide' />
          ) : (
            <img src={images.showEye} alt='Show' />
          )}
        </span>
        {error && (
          <h6 className={`${error.password ? 'errorMsg' : ''} flexCentered`}>
            {error.password ? error.password : ''}
          </h6>
        )}
      </div>
      <div className={styles.submitBlk + ' submitBlk'}>
        <Button
          variant={`primary`}
          disabled={Boolean(loader)}
          onClick={(e: MouseEvent) => {
            e.preventDefault();
            e.stopPropagation();
            handleSubmit();
          }}>
          {loader ? 'Signing In...' : 'Sign In'}
        </Button>
      </div>
      <p className={styles.copyRights + ' textCenter'}>
        <span
          sx={{
            variant: 'text.body-small',
            color: 'darkgray',
            fontWeight: 'body',
          }}>
          © {getYear(new Date())} Swvl. All rights reserved.
        </span>
      </p>
    </div>
  </div>
);

export default SignInForm;
