/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, useEffect, ChangeEvent, FC, useContext } from 'react';
import { useHistory, useParams, useLocation } from 'react-router-dom';
import Icon from '@swvl/icon';
import Input from '@swvl/input';
import Grid from '@swvl/grid';

import styles from '../../../assets/styles/case-study.module.scss';
import {
  BorderTag,
  RouteList,
  ContinouseRouteListItem,
  CasestudyShimmer,
  DetailTable,
} from '../components';
import { images } from '../../../constants';
import { TGlobalParamsType } from '../../../types';
import { GlobalContext } from '../../../context/GlobalContext';

import {
  useRideDetail,
  useEmployeeDetail,
  IEmployeeDetail,
} from '../apiServices';

const RouteEmployeeDetail: FC = () => {
  const { state, dispatch } = useContext(GlobalContext);

  const history = useHistory();
  const location = useLocation();
  const { pathname } = location;
  const { ruid, suid, rnum, uid } = useParams<TGlobalParamsType>();
  const [rideId, setRideId] = useState('');
  const [search, setSearch] = useState('');
  const [employeeSearchList, setEmployeeSearchList] = useState<
    Array<IEmployeeDetail>
  >([]);

  const { data: rideDetailList, isFetching: rideDetailFetching } =
    useRideDetail({ run_uuid: uid, item_uuid: suid, route_id: ruid });
  const { data: employeeList, isFetching: employeeDetailFetching } =
    useEmployeeDetail({
      run_uuid: uid,
      item_uuid: suid,
      route_id: ruid,
      ride_id: rideId,
    });

  const handleEmployeeSearch = (e: ChangeEvent<HTMLInputElement>) => {
    setSearch(e.target.value);
  };

  useEffect(() => {
    dispatch({
      type: 'ADD_ROUTE',
      payload: {
        path: pathname,
      },
    });
  }, [pathname, dispatch]);

  useEffect(() => {
    if (employeeList?.employees) {
      const { employees } = employeeList;
      setEmployeeSearchList(employees);
    }
  }, [employeeList]);

  useEffect(() => {
    if (!rideId && rideDetailList?.rides?.length) {
      setRideId(String(rideDetailList.rides[0]?.id));
    }
  }, [rideDetailList, rideId]);

  useEffect(() => {
    const searchedEmployeeDetails = employeeList?.employees?.filter(
      (item: IEmployeeDetail) =>
        item?.employee?.name?.toLowerCase()?.includes(search?.toLowerCase())
    );
    if (searchedEmployeeDetails) {
      setEmployeeSearchList(searchedEmployeeDetails);
    }
  }, [search]);

  return (
    <section className={styles.routeEmployeeWrapper}>
      {state?.routeList?.length < 2 && (
        <div className={`${styles.routeBack}`} onClick={history.goBack}>
          <span
            className='flexCentered'
            sx={{
              variant: 'text.body-small',
              color: 'lightslategray',
              fontWeight: 'body',
            }}>
            <Icon name='left_arrow' fill='#ef4349' />
            Go Back
          </span>
        </div>
      )}
      <Grid gap={0} columns='repeat(1, 1fr 3fr)'>
        <div className={styles.leftBlk} style={{ alignSelf: 'start' }}>
          <div className={styles.leftSideWrapper}>
            <div className={styles.headerSidebar}>
              <ul className='listInline'>
                <li
                  className='listInlineItem'
                  sx={{
                    variant: 'text.subhead',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}>
                  Ride :{' '}
                  <BorderTag color={String(rideDetailList?.color_code)}>
                    {rnum}
                  </BorderTag>
                </li>
                <li
                  className='listInlineItem'
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}>
                  Total stations :{' '}
                  <span className='mediumFont'>
                    {rideDetailList?.rides?.length}
                  </span>
                </li>
              </ul>
            </div>
          </div>
          <div className={styles.continouesRouteList}>
            <RouteList className={styles.rideInfoList}>
              {rideDetailList?.rides?.map((item) => {
                return (
                  <ContinouseRouteListItem
                    location={item.station_name}
                    riders={item.total_riders}
                    waitTime={
                      item.waiting_time ? `${item.waiting_time}min` : '-'
                    }
                    onClick={() => {
                      setSearch('');
                      setRideId(String(item.id));
                    }}
                    key={item.id}
                    color={rideDetailList?.color_code}
                    full={rideId === String(item.id) ? true : false}
                  />
                );
              })}
            </RouteList>
          </div>
        </div>
        <div className={styles.rightBlk}>
          <div className={styles.rightSideWrapper}>
            {rideDetailFetching || employeeDetailFetching ? (
              <div className='gridCenteredd'>
                <CasestudyShimmer type='RouteEmployeeDetail' />
              </div>
            ) : (
              <div className={styles.rightSideContainer}>
                <div className={styles.sidebarHeader}>
                  <h4
                    className='heading4'
                    style={{
                      color: String(rideDetailList?.color_code),
                    }}>
                    {employeeList?.station_name}
                  </h4>
                </div>
                <div className={styles.riderDetailTableWrapper}>
                  <h5
                    className='flexCentered'
                    sx={{
                      variant: 'text.headline',
                      color: 'dimgray',
                      fontWeight: 'body',
                    }}>
                    <img src={images.userOutlinedIcon} alt='userOutlinedIcon' />
                    Rider Details
                  </h5>
                  <div className={styles.searchWrapper}>
                    <Input
                      placeholder={'Search for a rider'}
                      value={search}
                      startEnhancer={
                        <img src={images.searchIcon} alt='searchIcon' />
                      }
                      onChange={handleEmployeeSearch}
                    />
                  </div>
                  <DetailTable
                    employeeDetail={employeeSearchList}
                    arrivalTime={employeeList?.arrival_time}
                    departureTime={employeeList?.departure_time}
                  />
                </div>
              </div>
            )}
          </div>
        </div>
      </Grid>
    </section>
  );
};

export default RouteEmployeeDetail;
