//Styped

import FleetDetail from './FleetDetails';
import ClusterMapView from './ClusterMapView';
import { RiderDetail } from './RiderDetails';
import Optimization from './Optimization';
import FileUpload from './FileUpload';
import { csvMandatoryFields } from '../../../utils';
import { TKeyValueProps } from '../../../types';
import { Matrix, CellBase } from 'react-spreadsheet';
import GenerateRoutesShimmer from './Shimmer';

function isEmpty<T extends { length: number }>(value: T) {
  return (
    // null or undefined
    value == null ||
    // has length and it's zero
    (value.hasOwnProperty('length') && value.length === 0) ||
    // is an Object and has no keys
    (value.constructor === Object && Object.keys(value).length === 0)
  );
}

const checkIfInvalidValue = <T extends { length: number }>(
  val: T,
  type: string
) => {
  if (type === 'String') {
    return isEmpty(val) ? true : false;
  } else if (type === 'Coordinates') {
    if (val) {
      return isFinite(Number(val)) && Math.abs(Number(val)) <= 180
        ? false
        : true;
    } else {
      return true;
    }
  } else if (type === 'Boolean') {
    if (typeof val === 'string') {
      return ['true', 'false', 'yes', 'no'].includes(val.toLowerCase())
        ? false
        : true;
    } else {
      return Boolean(val) === true || Boolean(val) === false ? false : true;
    }
  } else if (type === 'Day') {
    const allDays = [
      'mon',
      'tue',
      'tues',
      'wed',
      'thu',
      'thur',
      'thurs',
      'fri',
      'sat',
      'sun',
    ];
    let _val = String(val)
      .split(',')
      ?.map((item) => item.trim().toLowerCase());
    return _val.every((item) => allDays.includes(item)) ? false : true;
  } else if (type === 'Time') {
    if (val) {
      return /^([0-1]?[0-9]|2[0-4]):([0-5][0-9])(:[0-5][0-9])?$/.test(
        String(val)
      )
        ? false
        : true;
    } else {
      return true;
    }
  } else if (type === 'Number') {
    if (typeof val === 'number') {
      return false;
    } else if (typeof val === 'string') {
      var reg = /^[0-9]+$/;
      return reg.test(val) ? false : true;
    } else {
      return true;
    }
  } else if (type === 'Station') {
    if (
      String(val).toLowerCase() === 'station' ||
      String(val).toLowerCase() === 'clustering'
    ) {
      return false;
    } else {
      return true;
    }
  } else {
    return false;
  }
};

const convertObjectToValueType = (arr: Array<string>) => {
  return arr.length !== 0
    ? arr.map((item) => {
        return { value: item };
      })
    : [];
};

const replaceType = (val: string) => {
  const isNumber = /^-?\d*\.?\d*$/;
  if (val.toLowerCase() === 'true') {
    return true;
  } else if (val.toLowerCase() === 'false') {
    return false;
  } else if (isNumber.test(val)) {
    return Number(val);
  } else {
    return val;
  }
};

const convertTableDataToObj = (arr: Matrix<CellBase<any>> | undefined) => {
  if (arr) {
    let fetchKeysArr = arr[0].map((item) => item?.value);

    let convertArrToCSV = arr.slice(1).map((item, i) => {
      let orgObj = {};
      item.forEach((data, j) => {
        Object.assign(orgObj, {
          [fetchKeysArr[j]]: replaceType(data?.value),
        });
      });
      return orgObj;
    });

    return convertArrToCSV;
  } else {
    return arr;
  }
};

const findTypeFromKey = (key: string) => {
  key = key.toLowerCase().replace(/\s|[0-9_]|\W|[#$%^&*()]/g, '');
  let data_type = csvMandatoryFields.find(
    (item) => item.val === key
  )?.data_type;
  return data_type ? data_type : 'String';
};

const validateValue = (val: string, key: string) => {
  const type = findTypeFromKey(key);
  return checkIfInvalidValue(val, type);
};

const convertTableDataToObjErrorFinder = (arr: Matrix<CellBase<any>>) => {
  let fetchKeysArr = arr[0].map((item) =>
    item ? item.value.toLowerCase().replace(/\s|[0-9_]|\W|[#$%^&*()]/g, '') : ''
  );

  let convertArrToCSV = arr.slice(1).map((item, i) => {
    let orgObj = {};
    item.forEach((data, j) => {
      Object.assign(orgObj, {
        [fetchKeysArr[j]]: {
          value: data?.value,
          type: findTypeFromKey(fetchKeysArr[j]),
          error: validateValue(data?.value, fetchKeysArr[j]),
        },
      });
    });
    return orgObj;
  });

  const allValuesofGroupId: Array<string> = [];
  convertArrToCSV.forEach((data: any) => {
    allValuesofGroupId.push(String(data?.employeegroupid?.value));
  });

  if (allValuesofGroupId?.length) {
    let _allValuesofGroupId = [...new Set(allValuesofGroupId)];
    if (allValuesofGroupId?.length !== _allValuesofGroupId?.length) {
      convertArrToCSV.forEach((item: any) => {
        if (item?.employeegroupid?.error) {
          item.employeegroupid.error = true;
        }
      });
    } else {
      convertArrToCSV.forEach((item: any) => {
        if (item?.employeegroupid?.error) {
          item.employeegroupid.error = false;
        }
      });
    }
  }

  return convertArrToCSV;
};

const convertObjectDataToTableData = (arr: Array<TKeyValueProps>) => {
  let convertedEverythingToValue = arr.map((item) => {
    return convertObjectToValueType(Object.values(item));
  });

  let fetchFirstRowLabel = convertObjectToValueType(Object.keys(arr[0]));
  let tableData = [[...fetchFirstRowLabel], ...convertedEverythingToValue];

  return tableData;
};

export {
  FleetDetail,
  ClusterMapView,
  RiderDetail,
  Optimization,
  FileUpload,
  convertObjectDataToTableData,
  convertTableDataToObjErrorFinder,
  convertTableDataToObj,
  GenerateRoutesShimmer,
};
