import { FC } from 'react';

import Tooltip from '@swvl/tooltip';

type TCustomToolTip = {
  children: JSX.Element;
  id: string;
  content?: string;
  top?:number;
  left?:number;
  bottom?:number;
  right?:number;
};

const CustomTooltip: FC<TCustomToolTip> = ({ children, id, content,left,right,top,bottom }) => {
  return (
    <>
      <span data-for={id} data-tip={content} data-html={true}>
        {children}
      </span>
      <Tooltip
        id={id}
        place='right'
        effect='solid'
        type='light'
        multiline
        offset={{
          bottom: bottom ? bottom : 50,
          right: right ? right : 10,
          left,
          top,
        }}
        arrowColor={'transparent'}
      />
    </>
  );
};

export default CustomTooltip;
