import { useEffect, useState, ComponentType } from 'react';
import { withRouter } from 'react-router-dom';

import { NoInternetConnection } from '../components';

function networkDetectorHoc(ComposedComponent: ComponentType<any>) {
  function NetworkDetectorHoc() {
    const [isOnline, setIsOnline] = useState(true);

    const { onLine } = navigator;

    useEffect(() => {
      const condition = onLine ? 'online' : 'offline';
      if (condition === 'offline' && isOnline === true) {
        setIsOnline(false);
      }
    }, [onLine]);

    return <>{onLine ? <ComposedComponent /> : <NoInternetConnection />}</>;
  }
  return withRouter(NetworkDetectorHoc);
}

export default networkDetectorHoc;
