//STyped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, FC, Fragment, useEffect, useContext } from 'react';
import {
  useHistory,
  RouteComponentProps,
  useParams,
  useLocation,
} from 'react-router-dom';
import Grid from '@swvl/grid';
import Icon from '@swvl/icon';

import styles from '../../../assets/styles/case-study.module.scss';
import { ComparisonTable } from '../components';
import { images } from '../../../constants';
import { useCompareScenario } from '../apiServices';
import { calculateETF } from '../../../utils';
import { CustomTooltip } from '../../../shared_ui_components';
import { CasestudyShimmer } from '../components';
import { GlobalContext } from '../../../context/GlobalContext';
import { TGlobalParamsType } from '../../../types';

const CompareScenario: FC<RouteComponentProps> = () => {
  const { state, dispatch } = useContext(GlobalContext);
  const history = useHistory();
  const location = useLocation();
  const { pathname } = location;
  const { uid } = useParams<TGlobalParamsType>();
  const { data: compareScenarioData, isFetching: compareScenarioFetching } =
    useCompareScenario({
      uid: uid ? uid : '',
    });

  const [viewTable, SetViewTable] = useState(false);

  const handleViewTable = () => {
    SetViewTable(!viewTable);
  };

  useEffect(() => {
    dispatch({
      type: 'ADD_ROUTE',
      payload: {
        path: pathname,
      },
    });
  }, [dispatch, pathname]);

  return (
    <Fragment>
      <div className={styles.compareScenarioWrapper}>
        {state?.routeList?.length < 2 && (
          <div className={`${styles.routeBack}`} onClick={history.goBack}>
            <span
              className='flexCentered'
              sx={{
                variant: 'text.body-small',
                color: 'lightslategray',
                fontWeight: 'body',
              }}>
              <Icon name='left_arrow' fill='#ef4349' />
              Exit Comparison
            </span>
          </div>
        )}

        <h2
          sx={{
            variant: 'text.title-large',
            color: 'black',
            fontWeight: 'body',
          }}>
          Compare Scenarios
        </h2>
        <div className={styles.overviewList}>
          <Grid gap={2} columns='repeat(1, 1fr)'>
            {compareScenarioFetching ? (
              <div className='gridCenteredd'>
                <CasestudyShimmer type='CompareScenario' />
              </div>
            ) : (
              <Grid className={`${styles.overviewListItemWrapper}`}>
                {compareScenarioData?.scenario?.map((compare) => {
                  return (
                    <div key={compare.uuid}>
                      <div className={styles.overviewItem}>
                        <div className={styles.header}>
                          <h4
                            sx={{
                              variant: 'text.title-small',
                              color: 'lightslategray ',
                              fontWeight: 'body',
                            }}>
                            {compare?.scenario_type?.label}
                          </h4>
                          <div
                            className={`${styles.currency}`}
                            sx={{
                              variant: 'text.title-x-large',
                              color: 'crimson',
                              fontWeight: 'body',
                            }}>
                            $ {compare?.performance_indices?.cost}
                            <p
                              sx={{
                                variant: 'text.caption-small',
                                color: 'crimson',
                                fontWeight: 'body',
                              }}>
                              USD
                            </p>
                          </div>
                        </div>
                        <div className={styles.bottomWrapper}>
                          <div className={styles.etf}>
                            <ul className='listInline'>
                              <li
                                className='listInlineItem'
                                sx={{
                                  variant: 'text.body-small',
                                  color: 'lightslategray ',
                                  fontWeight: 'body',
                                }}>
                                ETF
                                <CustomTooltip
                                  id={compare.uuid}
                                  content="
                                <div class='tooltip'>
                                  <div class='etfTooltip'>
                                    <div class='description'>
                                      <h2 class='paragraph mediumFont'>Extra Time Factor (ETF)</h2>
                                      <p class='supportText lightFont'>
                                        It is a comparitive metric in terms of time determining how much
                                        longer or shorter a ride is when compared to the likes of Uber
                                      </p>
                                    </div>
                                    <div class='etfDetail'>
                                      <p class='paragraph mediumFont'>Understanding your ETF</p>
                                      <ul class='listInline'>
                                        <li class='listInlineItem supportText mediumFont'>
                                          0 - 1.7 <span class='success'>Good</span>
                                        </li>
                                        <li class='listInlineItem supportText mediumFont'>
                                          1.7 - 2.5 <span class='average'>Average</span>
                                        </li>
                                        <li class='listInlineItem supportText mediumFont'>
                                          > 2.5
                                          <span class='poor'>Bad</span>
                                        </li>
                                      </ul>
                                    </div>
                                  </div>
                                </div>
                              ">
                                  <img
                                    src={images.infoIconGray}
                                    alt='infoIcon'
                                  />
                                </CustomTooltip>
                              </li>
                              <li
                                className='listInlineItemflexCentered'
                                sx={{
                                  variant: 'text.body-small',
                                  color: 'lightslategray ',
                                  fontWeight: 'body',
                                }}>
                                <p>{compare?.performance_indices?.avg_etf}</p>
                                <span
                                  className={`customStatus ${calculateETF(
                                    compare?.performance_indices?.avg_etf
                                  )?.score.toLowerCase()}`}>
                                  <span>
                                    {
                                      calculateETF(
                                        compare?.performance_indices?.avg_etf
                                      )?.score
                                    }
                                  </span>
                                </span>
                              </li>
                            </ul>
                          </div>
                          <ul className={` ${styles.reasons}`}>
                            {compare?.highlights.map((highlight) => {
                              return (
                                <li
                                  sx={{
                                    variant: 'text.body-small',
                                    color: 'lightslategray ',
                                    fontWeight: 'body',
                                  }}>
                                  {highlight}
                                </li>
                              );
                            })}
                          </ul>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </Grid>
            )}
          </Grid>
        </div>
        <Grid>
          <div
            sx={{
              variant: 'text.body-small',
              color: 'crimson ',
              fontWeight: 'body',
            }}
            className={`${styles.viewDetailComparison} flexCentered`}
            onClick={handleViewTable}>
            View Detailed Comparison{' '}
            {viewTable ? (
              <span className={styles.viewMore}>
                <Icon name='down_arrow' />
              </span>
            ) : (
              <span className={styles.viewLess}>
                <Icon name='down_arrow' />
              </span>
            )}
          </div>
        </Grid>
      </div>
      {viewTable && (
        <ComparisonTable compareScenarioData={compareScenarioData} />
      )}
    </Fragment>
  );
};

export default CompareScenario;
