type TimeDetail = {
  hours: number;
  minutes: number;
  days?: number;
  months?: number;
  seconds?: number;
  years?: number;
};

interface CsvMandatory {
  type: string;
  val: string;
  label: string;
  data_type: string;
  is_error: boolean;
}
export const timeConvertion = (time: number): TimeDetail => {
  const initial_time: TimeDetail = {
    days: 0,
    hours: 0,
    minutes: 0,
    months: 0,
    seconds: 0,
    years: 0,
  };

  if (time) {
    const resultTime = intervalToDuration({
      start: 0,
      end: time * 1000 * 60,
    }) as TimeDetail;
    return resultTime;
  } else {
    return initial_time;
  }
};

export const searchFunctionality = <T,>(
  arr: Array<T> | undefined,
  field: string,
  val: string | number | null
): Array<T> => {
  val = String(val);
  val = val ? val.toLowerCase() : '';
  arr = arr ?? [];
  field = field ?? '';
  return arr && arr?.length !== 0
    ? arr?.filter((item: T) => item?.[field]?.toLowerCase()?.includes(val))
    : [];
};

export const updateArrayOfObj = <T,>(
  arr: Array<T>,
  obj: any,
  field: string,
  compare_value: string | number | boolean
): Array<T> => {
  if (
    arr?.length !== 0 &&
    Object.keys(obj)?.length !== 0 &&
    field &&
    compare_value
  ) {
    return arr.map((item) => {
      if (item[field] === compare_value) {
        return { ...item, ...obj };
      } else {
        return { ...item };
      }
    });
  } else {
    return [];
  }
};

export function replaceKeys(o: object): object {
  Object.keys(o).forEach(function (key) {
    var newKey = key.replace(/\s+/g, '');
    if (o[key] && typeof o[key] === 'object') {
      replaceKeys(o[key]);
    }
    if (key !== newKey) {
      o[newKey] = o[key];
      delete o[key];
    }
  });
  return o;
}

