export const downloadFileType = (
  fileData: string,
  fileName: string,
  fileExtension: string
) => {
  if (window.navigator.msSaveOrOpenBlob) {
    window.navigator.msSaveOrOpenBlob(fileData, fileName + fileExtension);
  } else {
    const url = window.URL.createObjectURL(new Blob([fileData]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', fileName + fileExtension);
    document.body.appendChild(link);
    link.click();
  }
};

// ETF criteria for Good, Average & Bad. ETF value will be sent from Oracle engine

export const calculateETF = (etf: number) => {
  if (etf > 0 && etf <= 1.7) {
    return {
      score: 'Good',
      color: '#00A15D',
    };
  } else if (etf > 1.7 && etf <= 2.5) {
    return {
      score: 'Average',
      color: '#FFB114',
    };
  } else {
    return {
      score: 'Bad',
      color: '#EF4349',
    };
  }
};

export function uuidv4(): string {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    var r = (Math.random() * 16) | 0,
      v = c === 'x' ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

export function minutes(minutes: number) {
  return minutes ? 1000 * 60 * minutes : 0;
}

export function hours(hours: number) {
  return hours ? 1000 * 60 * 60 * hours : 0;
}

export function ConvertToCSV<P>(arrayOfJson: P) {
  const replacer = (key: string, value: string | number | boolean) =>
    value === null ? '' : value;
  const header =
    arrayOfJson &&
    Array.isArray(arrayOfJson) &&
    arrayOfJson?.length &&
    arrayOfJson[0]
      ? Object.keys(arrayOfJson[0])
      : [];
  let csv =
    Array.isArray(arrayOfJson) &&
    arrayOfJson?.map((row) =>
      header
        .map((fieldName) => JSON.stringify(row[fieldName], replacer))
        .join(',')
    );
  if (csv) csv.unshift(header.join(','));
  let _csv;
  if (csv) {
    _csv = csv.join('\r\n');
  }

  return _csv;
}

export const checkIfLastRouteIsSame = (
  arr: Array<{ path: string }>,
  current_path: string
) => {
  return arr[arr.length - 1]?.path === current_path;
};

export const downloadBlobFile = (blob: unknown, type: string) => {
  let _sampleFile = blob as BlobPart;
  let _blob = new Blob([_sampleFile], {
    type,
  });
  var link = document.createElement('a');
  var url = URL.createObjectURL(_blob);
  link.setAttribute('href', url);
  link.setAttribute('download', 'sample_file.csv');
  link.style.visibility = 'hidden';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
};

export const capitalize = (val: string) => {
  return val[0].toUpperCase() + val.slice(1);
};

