//Styped

import { useEffect, useState, UIEvent, useContext } from 'react';
import { useLocation, useParams, useHistory } from 'react-router-dom';
import queryString from 'query-string';
import Grid from '@swvl/grid';

import styles from '../../../assets/styles/case-study.module.scss';
import { useScenarios, useEmployeeLocation } from '../apiServices';
import {
  RoutesDrawer,
  SidebarHeader,
  RoutesDetail,
  ViewSwitcher,
  MapRoutes,
  GanttCharts,
} from '../components';
import { GlobalContext } from '../../../context/GlobalContext';
import { TGlobalParamsType } from '../../../types';

let isSyncingLeftScroll = false;
let isSyncingRightScroll = false;

const CaseStudy = () => {
  const { dispatch } = useContext(GlobalContext);
  const history = useHistory();
  const location = useLocation();

  let { uid, suid } = useParams<TGlobalParamsType>();
  const { data: scenariosData, isFetching: scenarioDatFetching } = useScenarios(
    { run_uuid: uid, item_uuid: suid }
  );
  const { data: employeeLocationList, isFetching: employeeLocationIsFetching } =
    useEmployeeLocation({
      uuid: uid ? uid : '',
    });

  const dataFetching = scenarioDatFetching && employeeLocationIsFetching;

  const [view, setView] = useState('map');
  const [sidebarView, setSidebarView] = useState('performance-index');
  const [routeDrawer, setRouteDrawer] = useState(false);
  const [routeId, setRouteId] = useState('');
  const [rideNo, setRideNo] = useState(0);
  const [selectedRouteId, setSelectedRouteId] = useState('');
  const { search } = location;

  useEffect(() => {
    if (uid || suid) {
      dispatch({
        type: 'ADD_ROUTE',
        payload: {
          path: location.pathname,
        },
      });
    }
  }, [uid]);

  useEffect(() => {
    const queryData = queryString.parse(location?.search);
    if (queryData?.view && queryData?.view !== sidebarView) {
      const { view } = queryData;
      if (typeof view === 'string') {
        setSidebarView(view);
      }
    }
  }, [search]);

  const handleOpenRoutesDrawer = (val: boolean, id: string, num_id: number) => {
    setRouteDrawer(val);
    setRouteId(id);
    setRideNo(num_id);
  };

  const handleSwitchView = (type: string) => {
    const leftDiv = document.getElementById(`allRoutesDiv`);
    if (leftDiv) {
      leftDiv.scrollTop = 0;
    }
    setView(type);
  };

  const handleScrollRoutesList = (e: UIEvent<HTMLDivElement>) => {
    let element: any = e.target;
    const rightDiv = document.getElementsByClassName(`time-table-scroller`);
    if (rightDiv && rightDiv.length && !isSyncingLeftScroll) {
      isSyncingRightScroll = true;
      rightDiv[0].scrollTop = element.scrollTop;
    }
    isSyncingLeftScroll = false;
  };

  const handleScrollGanttChart = (e: CustomEvent) => {
    let element: any = e.target;
    const leftDiv = document.getElementById(`allRoutesDiv`);
    if (leftDiv && !isSyncingRightScroll) {
      isSyncingLeftScroll = true;
      leftDiv.scrollTop = element.scrollTop;
    }
    isSyncingRightScroll = false;
  };

  const handleSidebarView = (view: string) => {
    if (view !== sidebarView) {
      setSidebarView(view);
      history.push({ search: `?view=${view}` });
    }
  };

  const handleSelectRouteId = (id: string, from: string) => {
    if (from === 'map') {
      document?.getElementById(`routes-${id}`)?.scrollIntoView({
        behavior: 'smooth',
      });
      handleSidebarView('route-detail');
    }
    if (selectedRouteId !== id) {
      setSelectedRouteId(id);
    }
  };

  return (
    <section className={styles.caseStudySectionWrapper}>
      <RoutesDrawer
        open={routeDrawer}
        routeId={routeId}
        rideNo={rideNo}
        onClose={() => handleOpenRoutesDrawer(false, '', 0)}
      />
      <div className={`${styles.leftSidebarSection}`}>
        <SidebarHeader
          sidebarView={sidebarView}
          handleSidebarView={handleSidebarView}
          downloadFile={false}
          showSwitchRoute={true}
          downloadFileUrl={''}
        />
        <RoutesDetail
          selectedRouteId={selectedRouteId}
          sidebarView={sidebarView}
          handleSelectRouteId={handleSelectRouteId}
          handleSidebarView={handleSidebarView}
          handleScrollRoutesList={handleScrollRoutesList}
          handleOpenRoutesDrawer={handleOpenRoutesDrawer}
          scenariosData={scenariosData}
          scenarioDatFetching={scenarioDatFetching}
          canGenerateNewRoute={false}
        />
      </div>
      <div className={`${styles.rightSidebarSectionWrapper}`}>
        <Grid columns='repeat(1, 1fr)'>
          <div className={styles.rightSidebarSection}>
            <ViewSwitcher view={view} handleSwitchView={handleSwitchView} />
            {view === 'map' ? (
              <MapRoutes
                scenariosData={scenariosData}
                selectedRouteId={selectedRouteId}
                handleSelectRouteId={handleSelectRouteId}
                scenarioDatFetching={dataFetching}
                employeeLocationList={employeeLocationList?.employee_details}
              />
            ) : (
              <GanttCharts
                handleScrollGanttChart={handleScrollGanttChart}
                scenariosData={scenariosData}
                scenarioDatFetching={dataFetching}
              />
            )}
          </div>
        </Grid>
      </div>
    </section>
  );
};

export default CaseStudy;
