/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useEffect, useState, FC } from 'react';
import Spreadsheet, { Matrix, CellBase } from 'react-spreadsheet';
import Button from '@swvl/button';
import Grid from '@swvl/grid';

import styles from '../../../assets/styles/rider-detail.module.scss';
import { images } from '../../../constants';
import { csvMandatoryFields, updateArrayOfObj } from '../../../utils';
import {
  convertObjectDataToTableData,
  convertTableDataToObjErrorFinder,
  convertTableDataToObj,
} from './';

type TTableCSVProps = {
  fileData: Array<{}> | undefined;
  csvName: string;
  onContinue: (val: number) => void;
  onEditFile: (val: Array<{}> | undefined, raw: string) => void;
};

const TableCSV: FC<TTableCSVProps> = ({
  fileData,
  csvName,
  onContinue,
  onEditFile,
}) => {
  const [optionalFields, setOptionalFields] = useState(false);
  const [csvParsedData, setCsvParsedData] = useState<Matrix<CellBase<any>>>([]);
  const [csvHeaderFields, setCsvHeaderFields] = useState<Array<string>>([]);
  const [disabledBtn, setDisabledBtn] = useState(false);
  const [csvFields, setCsvFields] = useState(csvMandatoryFields);

  useEffect(() => {
    if (fileData && fileData?.length !== 0) {
      const allHeaders = Object.keys(fileData[0]).map((item) => {
        return item.toLowerCase().replace(/\s|[0-9_]|\W|[#$%^&*()]/g, '');
      });
      setCsvHeaderFields(allHeaders);
      const _parseData = convertObjectDataToTableData(fileData);
      if (_parseData) {
        handleDataChange(_parseData, allHeaders);
      }
    }
  }, [fileData]);

  const handleDataChange = (
    val: Matrix<CellBase<any>>,
    csvHF: Array<string>
  ) => {
    setCsvParsedData(val);
    if (val?.length && csvHF?.length) {
      let convertedFileData = convertTableDataToObj(val);

      let validateTable = convertTableDataToObjErrorFinder(val);

      if (convertedFileData?.length) {
        const allHeaders = Object.keys(convertedFileData[0]).map((item) => {
          return item.toLowerCase().replace(/\s|[0-9_]|\W|[#$%^&*()]/g, '');
        });
        setCsvHeaderFields(allHeaders);
      }
      let newCsvMandatoryValue = csvMandatoryFields;
      validateTable.forEach((item) => {
        Object.keys(item).forEach((val) => {
          if (item[val]?.error === true) {
            newCsvMandatoryValue = updateArrayOfObj(
              newCsvMandatoryValue,
              { is_error: true },
              'val',
              val
            );
          }
        });
      });
      const checkMandatoryFieldsError = newCsvMandatoryValue
        .filter((item) => item.type === 'mandatory')
        ?.every((item) => item.is_error === false);

      if (!checkMandatoryFieldsError) {
        setDisabledBtn(true);
      } else {
        setDisabledBtn(false);
      }

      setCsvFields(newCsvMandatoryValue);
    }
  };
  return (
    <div className={styles.tableViewWrapper}>
      <Grid columns='repeat(1, 2fr)'>
        <div>
          <h4
            sx={{
              variant: 'text.headline',
              color: 'black',
              fontWeight: 'body',
            }}>
            Enter Rider details / CSV input
          </h4>
        </div>
        <div>
          <div className={styles.fileDetailsWrapper}>
            <ul className={`${styles.fileDetails} flexCentered listInline`}>
              <li className='listInlineItem'>
                <img
                  src={images.csvIcon}
                  alt='csvIcon'
                  className={styles.csvIcon}
                />
              </li>
              <li className='listInlineItem'>
                <p
                  sx={{
                    variant: 'text.body-large',
                    color: 'mediumseagreen',
                    fontWeight: 'body',
                  }}>
                  {csvName ? csvName : '.csv'}
                </p>
              </li>

              <li
                className='listInlineItem'
                onClick={() => {
                  onContinue(1);
                }}>
                <img
                  src={images.removeFile}
                  alt='removeFile'
                  className={styles.removeFile}
                />
              </li>
            </ul>
          </div>
        </div>
      </Grid>
      <div className={styles.twoLayout}>
        <div className={styles.layoutOne}>
          <div className={styles.tableView}>
            <Grid columns='repeat(1, 1fr)'>
              <div className={styles.tableViewContained}>
                {csvParsedData?.length !== 0 && (
                  <Spreadsheet
                    data={csvParsedData}
                    onChange={(val) => {
                      handleDataChange(val, csvHeaderFields);
                    }}
                  />
                )}
              </div>
            </Grid>
          </div>
        </div>
        <div className={styles.layoutTwo}>
          <div className={styles.detailsNeededWrapper}>
            <div className={`${styles.detailsNeeded} ${styles.noBottomButton}`}>
              <h5
                sx={{
                  variant: 'text.body-large',
                  color: 'black',
                  fontWeight: 'body',
                }}>
                Details we would need
              </h5>
              <p
                sx={{
                  variant: 'text.body-small',
                  color: 'black',
                  fontWeight: 'body',
                }}>
                Mandatory
              </p>
              <div className={styles.checkBoxType}>
                <ul className={`listUnstyled ${styles.detailsList}`}>
                  {csvFields
                    .filter((item) => item.type === 'mandatory')
                    .map((item) => {
                      return (
                        <li
                          sx={{
                            variant: 'text.caption-large',
                            color: 'dimgray',
                            fontWeight: 'body',
                          }}
                          key={item.label}
                          className={
                            csvHeaderFields.includes(item.val) &&
                            item.is_error === false
                              ? styles.success
                              : styles.error
                          }>
                          {item.label}
                        </li>
                      );
                    })}
                </ul>
              </div>
              <p
                sx={{
                  variant: 'text.body-small',
                  color: 'black',
                  fontWeight: 'body',
                }}
                className={`${styles.optionalField} ${styles.arrow} ${
                  optionalFields ? styles.active : ''
                }`}
                onClick={() => {
                  setOptionalFields(!optionalFields);
                }}>
                Optional
              </p>
              {optionalFields && (
                <div className={styles.checkBoxType}>
                  <ul
                    className={`listUnstyled captionReg ${styles.detailsList}`}>
                    {csvFields
                      .filter((item) => item.type === 'optional')
                      .map((item) => {
                        return (
                          <li
                            sx={{
                              variant: 'text.caption-large',
                              color: 'dimgray',
                              fontWeight: 'body',
                            }}
                            key={item.label}
                            className={
                              csvHeaderFields.includes(item.val) &&
                              item.is_error === false
                                ? styles.success
                                : styles.error
                            }>
                            {item.label}
                          </li>
                        );
                      })}
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      <div className={styles.bottomContinueWrapper}>
        <Button
          variant='primary'
          disabled={disabledBtn ? true : false}
          onClick={() => {
            const _tableObject = convertTableDataToObj(csvParsedData);

            const _jsoncsv = JSON.stringify(_tableObject);

            onEditFile(_tableObject, _jsoncsv);
            onContinue(3);
          }}>
          Continue
        </Button>
      </div>
    </div>
  );
};

export default TableCSV;
