//Styped

import { AxiosResponse } from 'axios';
import { useQuery } from 'react-query';
import { minutes, hours } from '../../utils';
import axiosInstance from '../../utils/httpInterceptor';

const reactQueryGeneralConfig = {
  keepPreviousData: true,
  refetchOnMount: false,
  refetchOnWindowFocus: false,
  staleTime: minutes(10),
  cacheTime: hours(24),
};

// Employee Location Query

type TEmployeeLocationQuery = {
  uuid: string | undefined;
};

interface IEmployeeDetailList {
  name: string;
  company_name: string;
  days_of_week: string;
  gender: string;
  group_id: string;
  uuid: string;
  latitude: string;
  location: string;
  longitude: string;
}

interface IEmployeeDetailResponse {
  employee_details: Array<IEmployeeDetailList>;
}

function useEmployeeLocation(query: TEmployeeLocationQuery) {
  return useQuery(
    [`employee-location-`, query],
    async () => {
      const { data }: AxiosResponse<IEmployeeDetailResponse> =
        await axiosInstance({
          method: 'GET',
          url: `/run/${query.uuid}/employees/`,
        });
      return data;
    },
    { ...reactQueryGeneralConfig, enabled: !!query?.uuid }
  );
}

// Case Study Run Query

type TCaseStudyDetailQuery = {
  uuid: string | undefined;
};

interface IValueLabel {
  label: string;
  value: number;
}

interface ICaseStudyScenarioList {
  name: string | null;
  scenario_type: IValueLabel;
  status: IValueLabel;
  uuid: string;
}

interface ICaseStudyResponse {
  scenarios: Array<ICaseStudyScenarioList>;
}

function useCaseStudyDetail(query: TCaseStudyDetailQuery) {
  const { uuid } = query;
  return useQuery(
    [`scenario-detail-`, query],
    async () => {
      const { data }: AxiosResponse<ICaseStudyResponse> = await axiosInstance({
        method: 'GET',
        url: `/run/${uuid}/scenarios/`,
      });
      return data;
    },
    {
      ...reactQueryGeneralConfig,
      enabled: uuid ? true : false,
    }
  );
}

// Scenario Query

type TScenariosQuery = {
  run_uuid: string | undefined;
  item_uuid: string | undefined;
};

//Calendar View List Interface

interface ISchedulerList {
  count: number;
  from_date: string;
  name: string;
  no_of_seats: number;
  percentage: number;
  to_date: string;
}
interface ICalendarViewList {
  color_code: string;
  direction: string;
  id: number;
  name: string;
  scheduler: Array<ISchedulerList>;
}

// Ride Points List Interface

interface IRideList {
  id: number;
  station_latitude: string;
  station_longitude: string;
  station_name: string;
}

interface IRidePointsList {
  color_code: string;
  id: number;
  rides: Array<IRideList>;
  route_id: string;
}

interface IRidersDetail {
  company_name: string;
  days_of_week: string;
  gender: string;
  group_id: string;
  id: number;
  latitude: string;
  location: string;
  longitude: string;
  name: string;
}

interface IRouteDetailList {
  color_code: string;
  end_station_name: string;
  first_station_latitude: string;
  first_station_longitude: string;
  geometry: string;
  id: number;
  last_station_latitude: string;
  last_station_longitude: string;
  route_id: string;
  start_station_name: string;
  total_riders: number;
  total_vehicles: number;
  rides: Array<IRideList>;
  riders: Array<IRidersDetail>;
}

interface IPerformanceIndices {
  avg_distance: number;
  avg_duration: number;
  avg_etf: number;
  avg_utilization: number;
  cost: number;
  capacity_input: number;
  distance_input: number;
  duration_input: number;
  max_distance: number;
  max_duration: number;
  min_distance: number;
  min_duration: number;
  total_capacity: number;
  vehicles: number;
}

interface IPolylines {
  distance: number;
  hint: string;
  id: number;
  latitude: string;
  longitude: string;
  name: string;
}
interface IPolylineData {
  color_code: string;
  id: number;
  polylines: Array<IPolylines>;
  route_id: string;
}

interface IScenariosResponse {
  calender_view: Array<ICalendarViewList>;
  ride_points: Array<IRidePointsList>;
  route_details: Array<IRouteDetailList>;
  polyline_data: Array<IPolylineData>;
  scenario: {
    document: string;
    performance_indices: IPerformanceIndices;
    scenario_type: IValueLabel;
    status: IValueLabel;
    uuid: string;
  };
  total_riders: number;
}

function useScenarios(query: TScenariosQuery) {
  const { run_uuid, item_uuid } = query;
  return useQuery(
    ['scenarios', query],
    async () => {
      const { data }: AxiosResponse<IScenariosResponse> = await axiosInstance({
        method: 'GET',
        url: `/run/${run_uuid}/scenario/${item_uuid}/`,
      });
      return data;
    },
    {
      ...reactQueryGeneralConfig,
      enabled: run_uuid && item_uuid ? true : false,
    }
  );
}

// Rider Details Query

type TRideDetailsQuery = {
  run_uuid: string | undefined;
  item_uuid: string | undefined;
  route_id: string | undefined;
};

interface IRides {
  arrival_time: string;
  departure_time: string;
  id: number;
  station_latitude: string;
  station_longitude: string;
  station_name: string;
  station_num: number;
  total_riders: number;
  travel_time: string;
  waiting_time: string;
}

interface IRideDetailsResponse {
  color_code: string;
  total_riders: number;
  rides: Array<IRides>;
}

function useRideDetail(query: TRideDetailsQuery) {
  const { run_uuid, item_uuid, route_id } = query;
  return useQuery(
    ['ride', query],
    async () => {
      const { data }: AxiosResponse<IRideDetailsResponse> = await axiosInstance(
        {
          method: 'GET',
          url: `run/${run_uuid}/scenario/${item_uuid}/route/${route_id}/rides/`,
        }
      );
      return data;
    },
    {
      ...reactQueryGeneralConfig,
      enabled: run_uuid && item_uuid && route_id ? true : false,
    }
  );
}

// Employee Detail Query

type TEmployeeDetailQuery = {
  run_uuid: string | undefined;
  item_uuid: string | undefined;
  route_id: string | undefined;
  ride_id: string | undefined;
};

interface IEmployeeDetail {
  id: number;
  start_time: string;
  station_name: string;
  direction: IValueLabel;
  employee: IEmployeeDetailList;
}

interface IEmployeeDetailResponse {
  arrival_time: string;
  departure_time: string;
  employees: Array<IEmployeeDetail>;
  id: number;
  station_latitude: string;
  station_location_name: null | string;
  station_longitude: string;
  station_name: string;
  station_num: number;
  total_riders: number;
  travel_time: string;
  waiting_time: string;
}

function useEmployeeDetail(query: TEmployeeDetailQuery) {
  const { run_uuid, item_uuid, route_id, ride_id } = query;
  return useQuery(
    ['employee-detail', query],
    async () => {
      const { data }: AxiosResponse<IEmployeeDetailResponse> =
        await axiosInstance({
          method: 'GET',
          url: `/run/${run_uuid}/scenario/${item_uuid}/route/${route_id}/ride/${ride_id}/`,
        });
      return data;
    },
    {
      ...reactQueryGeneralConfig,
      enabled: run_uuid && item_uuid && route_id && ride_id ? true : false,
    }
  );
}

//Compare Scenario

interface ICompareQuery {
  uid: string;
}

interface ICompareScenarioList {
  highlights: Array<string>;
  performance_indices: {
    avg_distance: number;
    avg_duration: number;
    avg_etf: number;
    avg_utilization: number;
    cost: number;
    max_duration: number;
    total_capacity: number;
    vehicles: number;
  };
  scenario_type: {
    label: string;
    value: number;
  };
  status: {
    label: string;
    value: number;
  };
  uuid: string;
}

interface ICompareScenarioResponse {
  scenario: Array<ICompareScenarioList>;
}

function useCompareScenario(query: ICompareQuery) {
  const { uid } = query;
  return useQuery(
    ['compare', query],
    async () => {
      const { data }: AxiosResponse<ICompareScenarioResponse> =
        await axiosInstance({
          method: 'GET',
          url: `/run/${uid}/compare/`,
        });
      return data;
    },
    {
      ...reactQueryGeneralConfig,
      enabled: uid ? true : false,
    }
  );
}

export {
  useEmployeeDetail,
  useEmployeeLocation,
  useRideDetail,
  useScenarios,
  useCaseStudyDetail,
  useCompareScenario,
};
export type {
  IValueLabel,
  ICaseStudyScenarioList,
  IEmployeeDetailList,
  IRideList,
  ISchedulerList,
  ICaseStudyResponse,
  IRidePointsList,
  ICalendarViewList,
  IEmployeeDetailResponse,
  IRouteDetailList,
  IRideDetailsResponse,
  IEmployeeDetail,
  IRides,
  IScenariosResponse,
  IPerformanceIndices,
  IRidersDetail,
  IPolylineData,
  IPolylines,
  ICompareScenarioList,
  ICompareScenarioResponse,
};
