/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import {
  Link,
  withRouter,
  useParams,
  RouteComponentProps,
} from 'react-router-dom';
import { images } from '../constants';
import { SIDEBAR_ROUTES } from '../utils';
import { useState, useEffect, FC, useContext } from 'react';
import { useCaseStudyDetail } from '../pages/casestudy/apiServices';
import { GlobalContext } from '../context/GlobalContext';
import { TGlobalParamsType, scenarioState } from '../types';

interface SidebarProps extends RouteComponentProps {
  handleSidebarToggle: (val: boolean) => void;
}

const Sidebar: FC<SidebarProps> = ({ handleSidebarToggle, history }) => {
  const { dispatch } = useContext(GlobalContext);
  const { uid, type, suid, guid } = useParams<TGlobalParamsType>();
  const [sidebarScene, setSidebarScene] = useState<Array<any>>(SIDEBAR_ROUTES);

  const { data: RunScenes } = useCaseStudyDetail({
    uuid: uid ? uid : guid,
  });

  useEffect(() => {
    const scenesForCompare = RunScenes;

    if (scenesForCompare?.scenarios?.length) {
      const getListOfScenes = scenesForCompare.scenarios.map((item) => {
        return item.scenario_type.value;
      });
      const filteredScenes = SIDEBAR_ROUTES.filter((item) => {
        return getListOfScenes.includes(item.type);
      });
      const getMergedScenes = filteredScenes.map((item) => {
        let getSceneFromUtil = scenesForCompare.scenarios.find(
          (scene) => item.type === scene.scenario_type.value
        );
        if (getSceneFromUtil?.status?.value === 3) {
          return {
            ...item,
            ...getSceneFromUtil,
            uuid: scenarioState.noSolution,
          };
        } else {
          return { ...item, ...getSceneFromUtil };
        }
      });

      setSidebarScene(getMergedScenes);

      if (!suid && getMergedScenes?.length) {
        const { url, uuid } = getMergedScenes[0];
        let path = `/casestudy/${uid}/${url}/${uuid}`;
        if (uid) {
          dispatch({
            type: 'ADD_ROUTE',
            payload: {
              path,
            },
          });
          history.push(path);
        } else if (guid) {
          let path = `/generated-case/${guid}/${url}/${uuid}`;
          history.push(path);
        }
      }
    }
  }, [RunScenes]);

  return (
    <div
      className={`sideBar expandedSibar`}
      onMouseOver={() => handleSidebarToggle(true)}
      onMouseEnter={() => handleSidebarToggle(true)}
      onMouseLeave={() => handleSidebarToggle(false)}>
      <div className='menuListWrapper'>
        <ul className='listUnstyled'>
          {/* add class 'recommended' to ListItem*/}
          {sidebarScene.map((item, i) => {
            return (
              <li
                key={item.url}
                className={`listItem ${
                  type === `${item?.url}` ? `active` : ''
                } ${item?.scenario_type?.value === 1 ? 'recommended' : ''}`}>
                <Link
                  to={
                    uid
                      ? `/casestudy/${uid}/${item.url}/${item.uuid}`
                      : `/generated-case/${guid}/${item.url}/${item.uuid}`
                  }>
                  <img
                    className='activeImg'
                    src={item.img_active}
                    alt='Original Route'
                  />
                  <img
                    className='defaultImg'
                    src={item.img_default}
                    alt='Original Route'
                  />
                  <p>
                    <span
                      className='menuLabel'
                      sx={{
                        variant: 'text.body-large',
                        color: 'white',
                        fontWeight: 'bold',
                      }}>
                      {item.label.val}
                    </span>
                    {item?.scenario_type?.value === 1 ? (
                      <span
                        className='suggestionLabel'
                        sx={{
                          variant: 'text.body-small',
                          color: 'mediumseagreen',
                          fontWeight: 'bold',
                        }}>
                        Recommended
                      </span>
                    ) : null}
                  </p>
                </Link>
              </li>
            );
          })}
          {/* Do Not Show Compare Screen if the generated scenes are less that 2 */}
          {sidebarScene?.length > 1 && (
            <li className={`listItem compare `}>
              <Link
                to={
                  uid ? `/compare/screens/${uid}` : `/compare/screens/${guid}`
                }
                onClick={() => {
                  dispatch({
                    type: 'ADD_ROUTE',
                    payload: {
                      path: uid
                        ? `/compare/screens/${uid}`
                        : `/compare/screens/${guid}`,
                    },
                  });
                }}>
                <img
                  className='activeImg'
                  src={images.compareActive}
                  alt='Compare'
                />
                <img
                  className='defaultImg'
                  src={images.compareDefault}
                  alt='Compare'
                />
                <p>
                  <span
                    className='menuLabel'
                    sx={{
                      variant: 'text.body-large',
                      color: 'white',
                      fontWeight: 'bold',
                    }}>
                    {' '}
                    Compare
                  </span>
                </p>
              </Link>
            </li>
          )}
        </ul>
      </div>
    </div>
  );
};

export default withRouter(Sidebar);
