import { useState, useEffect, FC, useContext, Fragment } from 'react';
import { images } from '../constants';
import { ProfileDropDown } from './';
import { Select } from '@swvl/select';
import Button from '@swvl/button';
import Grid from '@swvl/grid';
import { useCaseStudies, ICaseStudyResponse } from '../pages/home/apiServices';
import { GlobalContext } from '../context/GlobalContext';
import { getLastPath } from '../context/Reducers';
import { useLocalStorage } from '../utils';
import { TUserInfoProp, TGlobalParamsType } from '../types';
import {
  Link,
  withRouter,
  useParams,
  matchPath,
  useLocation,
  RouteComponentProps,
} from 'react-router-dom';

type OptionType = {
  label: string;
  value: string | number;
};

const GENERATE_ROUTE_BUTTON = [
  '/generated-case/:guid/:type?/:gsuid?',
  '/casestudy/:uid/:type/:suid?',
  '/activity',
];

const SWITCH_CASE_STUDY = ['/casestudy/:uid/:type/:suid?'];

const getOptions = <T,>(arr: T): Array<{ label: string; value: string }> => {
  if (arr && Array.isArray(arr)) {
    return arr.map((item: ICaseStudyResponse) => {
      return {
        label: item.name,
        value: item.uuid,
      };
    });
  } else {
    return [];
  }
};

const Header: FC<RouteComponentProps> = ({ history }) => {
  const location = useLocation();
  const { pathname } = location;
  let { uid, type } = useParams<TGlobalParamsType>();
  const { localStorageValue } = useLocalStorage<TUserInfoProp>('userInfo');
  const { dispatch, state } = useContext(GlobalContext);
  const [routeUid, setRouteUid] = useState('');
  const [disableBtn, setDisableBtn] = useState(false);

  const { data: caseStudies } = useCaseStudies({
    authorized:
      localStorageValue?.user?.role?.permissions?.can_access_case_study,
  });

  const showComponent = (arr: Array<string>, path: string) => {
    const doesMatchAny: Array<Boolean> = [];
    arr.forEach((item) => {
      let val = matchPath(path, {
        path: item,
        exact: true,
        strict: true,
      })?.isExact;
      if (typeof val === 'boolean') {
        doesMatchAny.push(val);
      }
    });

    return doesMatchAny.some((item) => item);
  };

  const handleSetCaseStudy = (obj: OptionType) => {
    if (obj.value !== routeUid) {
      setRouteUid(routeUid);
      if (type) {
        history.push(`/casestudy/${obj.value}/${type}`);
      }
    }
  };

  const handleGenerateRoute = () => {
    history.push(`/generateroute`);
  };

  useEffect(() => {
    if (routeUid !== uid) {
      setRouteUid(uid);
    }
  }, [uid, routeUid]);

  useEffect(() => {
    setDisableBtn(false);
  }, [pathname]);

  return (
    <Fragment>
      <header className='primaryHeader'>
        <div className='headerWrapper'>
          <div className='headerRow'>
            <Grid gap={4} columns='repeat(2, 1fr)'>
              <div className='leftBlk'>
                <ul className='listInline flexCentered'>
                  {state?.routeList?.length > 1 && (
                    <li className='listInlineItem'>
                      <a
                        onClick={(e) => {
                          e.preventDefault();
                          setDisableBtn(true);
                          if (!disableBtn) {
                            history.push(
                              String(getLastPath(state.routeList, dispatch))
                            );
                          }
                        }}
                        className={`logoBlk ${disableBtn ? 'disabled' : ''}`}
                        href='/'>
                        <img className='logo' src={images.backBtn} alt='Back' />
                      </a>
                    </li>
                  )}

                  <li className='listInlineItem'>
                    <Link to='/' className='logoBlk'>
                      <img className='logo' src={images.logoSvg} alt='SWVL' />
                    </Link>
                  </li>
                  {showComponent(SWITCH_CASE_STUDY, location.pathname) && (
                    <li className='listInlineItem'>
                      <div className={'selectField'}>
                        <Select
                          options={
                            getOptions(caseStudies)
                              ? getOptions(caseStudies)
                              : []
                          }
                          value={getOptions(caseStudies)?.find(
                            (item: OptionType) => item.value === routeUid
                          )}
                          height={'small'}
                          name='Casestudy'
                          label=''
                          onChange={(selectedOption) =>
                            selectedOption && handleSetCaseStudy(selectedOption)
                          }
                        />
                      </div>
                    </li>
                  )}
                </ul>
              </div>
              <div className='rightBlk'>
                <ul className='listInline textRight flexCentered'>
                  {showComponent(GENERATE_ROUTE_BUTTON, location.pathname) && (
                    <li className='listInlineItem'>
                      <div className='generateRouteBtnWrapper'>
                        <Button variant='primary' onClick={handleGenerateRoute}>
                          Generate New Routes
                        </Button>
                      </div>
                    </li>
                  )}
                  <li className='listInlineItem'>
                    <ProfileDropDown isHomeScreen={false} />
                  </li>
                </ul>
              </div>
            </Grid>
          </div>
        </div>
      </header>
      <div className='emptyHeaderdiv'></div>
    </Fragment>
  );
};

export default withRouter(Header);
