/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import Button from '@swvl/button';
import { FC } from 'react';

import { images } from '../constants';


type NoResultProps = {
  height: string;
  image?: string;
  title: string | JSX.Element;
  subtext: string | JSX.Element;
  buttonText?: string;
  onClick?: () => void | undefined;
};

const NoResult: FC<NoResultProps> = ({
  height,
  image,
  title,
  subtext,
  onClick,
  buttonText,
}) => {
  return (
    <div className='noResultFound' style={{ height: height ? height : '' }}>
      <div className='contentRow textCenter'>
        {image ? (
          <img src={image} alt='imageBasePath' />
        ) : (
          <img src={images.noResultImage} alt='imageBasePath' />
        )}
        {title ? (
          <h5
            sx={{
              variant: 'text.headline',
              color: 'black',
              fontWeight: 'bold',
            }}>
            {title}
          </h5>
        ) : null}
        {subtext ? (
          <h6
            sx={{
              variant: 'text.body-large',
              color: 'black',
              fontWeight: 'body',
            }}>
            {subtext}
          </h6>
        ) : null}
        <div className='gridCentered'>
          {buttonText && onClick && (
            <Button onClick={onClick} variant='secondary'>
              {buttonText}
            </Button>
          )}
        </div>
      </div>
    </div>
  );
};

export default NoResult;
