import { useEffect, useRef, ComponentType, FC, MutableRefObject } from 'react';

function useClickAwayListener(
  ref: MutableRefObject<any>,
  onClickAway: () => void
) {
  useEffect(() => {
    function handleClickAway<P extends { target: any }>(e: P) {
      if (ref.current && e?.target && !ref.current.contains(e?.target)) {
        onClickAway();
      }
    }
    document.addEventListener('mousedown', handleClickAway);
    return () => {
      document.removeEventListener('mousedown', handleClickAway);
    };
  }, [ref]);
}

type TClickAwayProps = {
  onClickAway: () => void;
  children: JSX.Element | ComponentType;
};
const ClickAwayListener: FC<TClickAwayProps> = ({ onClickAway, children }) => {
  const wrapperRef = useRef(null);
  useClickAwayListener(wrapperRef, onClickAway);

  return <div ref={wrapperRef}>{children}</div>;
};

export default ClickAwayListener;
