/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from "theme-ui";
import { useState, useEffect, FC } from "react";
import { Marker, MarkerClusterer, InfoBox } from "@react-google-maps/api";
import Icon from "@swvl/icon";
import Button from "@swvl/button";
import Grid from "@swvl/grid";

import styles from "../../../assets/styles/rider-detail.module.scss";
import { images, mapStyle } from "../../../constants";
import { CustomMap } from "../../../shared_ui_components";
import { TKeyValueProps } from "../../../types";

const infoOptions = {
  closeBoxURL: "",
  enableEventPropagation: true,
};

type TLocationProps = {
  coords: {
    lat: number;
    lng: number;
  };
  label: number;
};

type C_STYLE = {
  width: number;
  height: number;
  className: string;
  url: string;
};

type CLUSTER_STYLES = C_STYLE[];

const CLUSTER_STYLE: CLUSTER_STYLES = [
  {
    width: 30,
    height: 30,
    className: "custom-clustericon-1",
    url: "",
  },
  {
    width: 40,
    height: 40,
    className: "custom-clustericon-2",
    url: "",
  },
  {
    width: 50,
    height: 50,
    className: "custom-clustericon-3",
    url: "",
  },
];

const objectKeysToLowerCase = function (origObj: TKeyValueProps) {
  return Object.keys(origObj).reduce(function (newObj, key) {
    let val: string = origObj[key];
    let newVal: TKeyValueProps =
      typeof val === "object" ? objectKeysToLowerCase(val) : val;
    newObj[key.toLowerCase()] = newVal;
    return newObj;
  }, {});
};

interface IClusterMapViewProps {
  fileData: Array<TKeyValueProps> | undefined;
  onContinue: (val: number) => void;
}

const ClusterMapView: FC<IClusterMapViewProps> = ({ fileData, onContinue }) => {
  const [employees, setEmployees] = useState(0);
  const [uniqueLocation, setUniqueLocation] = useState(0);
  const [disabledBtn, setDisabledBtn] = useState(false);
  const [mapError, setMapError] = useState(false);
  const [dataPoints, setDataPoints] = useState<Array<TLocationProps>>([]);
  const [employeeId, setEmployeeId] = useState("");

  const [mapProps, setMapProps] = useState({
    options: {
      zoom: 8,
      styles: mapStyle,
    },
    center: { lat: -31.56391, lng: 147.154312 },
  });

  useEffect(() => {
    if (fileData?.length) {
      let _fileData: Array<TKeyValueProps> = fileData.map((item) => {
        return objectKeysToLowerCase(item);
      });
      let dataToPoint = _fileData.map((item) => {
        return {
          coords: {
            lat: Number(item["employeelatitude"]),
            lng: Number(item["employeelongitude"]),
          },
          label: Number(item["emp_count"]),
          employeename: item.employeename,
          employeegroupid: item.employeegroupid,
        };
      });

      let center = {
        lat: dataToPoint[0].coords.lat,
        lng: dataToPoint[0].coords.lng,
      };

      setDataPoints(dataToPoint);

      let employeeCount = _fileData
        .map((item) => {
          return Number(item["emp_count"]);
        })
        .reduce((a, b) => a + b, 0);
      let uniqueLocationLength = [
        ...new Set(
          _fileData.map((item) => {
            return item["employeelocation"];
          })
        ),
      ];

      setMapProps({ ...mapProps, center });

      if (employeeCount) setEmployees(employeeCount);
      if (uniqueLocationLength)
        setUniqueLocation(Number(uniqueLocationLength?.length));
    }
  }, [fileData]);

  return (
    <div className={styles.clusterMapViewWrapper}>
      <h4
        sx={{
          variant: "text.title-medium",
          color: "dimgray",
          fontWeight: "light",
        }}
      >
        Demand Map
      </h4>
      <div>
        {mapError && (
          <Grid columns="repeat(1, 1fr)">
            <div className={styles.mapError} sx={{ background: "mistyrose" }}>
              <span
                className={styles.closeIcon}
                onClick={() => setMapError(false)}
              >
                <img src={images.closeIconRead} alt="mapError" />
              </span>
              <ul className="listInline flexCentered">
                <li className="listInlineItem">
                  <span className={styles.errorTypeIcon}>
                    <Icon name="cancel" />
                  </span>
                </li>
                <li className="listInlineItem">
                  <h5 className="subtitle">Error</h5>
                  <p className="paragraph">
                    The uploaded file has insufficient details for route
                    generation!
                  </p>
                </li>
              </ul>

              <div className={styles.startOver}>
                <button
                  className="secondaryBtn plainBtn"
                  onClick={() => {
                    onContinue(1);
                  }}
                >
                  Start Over
                </button>
              </div>
            </div>
          </Grid>
        )}
        <Grid columns="repeat(1, 1fr)">
          <div className={styles.mapBoxViewWrapper}>
            <div className={styles.mapBoxView}>
              <CustomMap
                {...mapProps}
                onLoad={(map) => {
                  var bounds = new google.maps.LatLngBounds();
                  var markers = dataPoints.map((location, i) => {
                    bounds.extend(location.coords);
                    map.fitBounds(bounds);
                  });
                }}
              >
                <MarkerClusterer
                  gridSize={100}
                  minimumClusterSize={3}
                  styles={CLUSTER_STYLE}
                  clusterClass={"custom-clustericon"}
                  zoomOnClick={true}
                >
                  {(clusterer) =>
                    dataPoints?.map((location: any) => (
                      <span>
                        <Marker
                          icon={images.userIconMarker}
                          position={location.coords}
                          clusterer={clusterer}
                          onMouseOver={() => {
                            setEmployeeId(String(location?.employeegroupid));
                          }}
                          onMouseOut={() => {
                            setEmployeeId("");
                          }}
                        />
                        {String(location.employeegroupid) === employeeId && (
                          <InfoBox
                            options={infoOptions}
                            position={location.coords}
                          >
                            <div className="mapInfoBox">
                              <p
                                sx={{
                                  variant: "text.body-small",
                                  color: "dimgray",
                                  fontWeight: "body",
                                }}
                              >
                                {location?.employeename
                                  ? location.employeename
                                  : "--"}
                              </p>
                            </div>
                          </InfoBox>
                        )}
                      </span>
                    ))
                  }
                </MarkerClusterer>
              </CustomMap>
            </div>
            <div className={styles.mapInfo}>
              <ul className="listInline">
                <li className="listInlineItem">
                  <span
                    className="flexCentered"
                    sx={{
                      variant: "text.title-x-large",
                      color: "slategray",
                      fontWeight: "body",
                    }}
                  >
                    <img src={images.peopleIcon} alt="employeeIcon" />
                    {employees}
                  </span>
                  <p
                    sx={{
                      variant: "text.body-large",
                      color: "slategray",
                      fontWeight: "body",
                    }}
                  >
                    Employee Found
                  </p>
                </li>
                <li className="listInlineItem">
                  <span
                    className="flexCentered"
                    sx={{
                      variant: "text.title-x-large",
                      color: "slategray",
                      fontWeight: "body",
                    }}
                  >
                    <img
                      className={styles.mapMarkerIcon}
                      src={images.mapMarkerIcon}
                      alt="mapMarkerIcon"
                    />
                    {uniqueLocation}
                  </span>
                  <p
                    sx={{
                      variant: "text.body-large",
                      color: "slategray",
                      fontWeight: "body",
                    }}
                  >
                    Unique locations
                  </p>
                </li>
              </ul>
            </div>
          </div>
        </Grid>
        <Grid
          columns="repeat(1, 1fr)"
          onClick={() => {
            onContinue(4);
          }}
        >
          <div className={styles.bottomContinueWrapper}>
            <Button variant={"primary"} disabled={disabledBtn ? true : false}>
              Continue
            </Button>
          </div>
        </Grid>
      </div>
    </div>
  );
};

export default ClusterMapView;
