/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { useState, useCallback, FC } from 'react';
import { useDropzone } from 'react-dropzone';
import csv from 'csvtojson';
import Button from '@swvl/button';
import { useMutation } from 'react-query';

import styles from '../../../assets/styles/rider-detail.module.scss';
import { images } from '../../../constants';
import { csvMandatoryFields, downloadBlobFile, downloadFileFromUrl } from '../../../utils';
import { TKeyValueProps } from '../../../types';
import axiosInstance from '../../../utils/httpInterceptor';

type FileUploadProps = {
  onDragged: (val: string) => void;
  onContinue: (val: number) => void;
  onFileUpload: (val: object[]) => void;
};

const FileUpload: FC<FileUploadProps> = ({
  onDragged,
  onContinue,
  onFileUpload,
}) => {
  const [optionalFields, setOptionalFields] = useState(false);
  const [fileUploadFail, setFileUploadFail] = useState(false);
  const [fileName, setFileName] = useState('sample.csv');
  const [csvFile, setCsvFile] = useState<File>();

  const sampleFileDownloaded = useMutation((data: { action: string }) => {
    return axiosInstance({
      method: 'POST',
      url: `/activity-log/`,
      data: data,
    });
  });

  const onDrop = useCallback((acceptedFiles: Array<File>) => {
    if (acceptedFiles[0]?.name) {
      onDragged(acceptedFiles[0]?.name);
      setFileName(acceptedFiles[0]?.name);
    }

    if (
      acceptedFiles[0].type === 'text/csv' ||
      acceptedFiles[0].type === 'application/vnd.ms-excel'
    ) {
      setCsvFile(acceptedFiles[0]);
      acceptedFiles.forEach((file) => {
        const reader = new FileReader();
        reader.onabort = () => {}
        reader.onerror = () => {}
        reader.onload = () => {
          const fileAsBinaryString = reader.result;

          if (typeof fileAsBinaryString === 'string') {
            csv({
              noheader: true,
              output: 'json',
            })
              .fromString(fileAsBinaryString)
              .then((csvRows: Array<TKeyValueProps>) => {
                const toJson: Array<TKeyValueProps> = [];
                csvRows.forEach((aCsvRow, i) => {
                  if (i !== 0) {
                    const builtObject = {};

                    Object.keys(aCsvRow).forEach((aKey) => {
                      const valueToAddInBuiltObject = aCsvRow[aKey];
                      const keyToAddInBuiltObject = csvRows[0][aKey];
                      builtObject[keyToAddInBuiltObject] =
                        valueToAddInBuiltObject;
                    });

                    toJson.push(builtObject);
                  }
                });

                if (toJson?.length !== 0) {
                  const getHeaders = Object.keys(toJson[0]).map((item) => {
                    return item
                      .toLowerCase()
                      .replace(/\s|[0-9_]|\W|[#$%^&*()]/g, '');
                  });

                  if (
                    getHeaders?.length &&
                    csvMandatoryFields.some((item) => {
                      return getHeaders.includes(item.val);
                    })
                  ) {
                    onFileUpload(toJson);
                    onContinue(2);
                  } else {
                    setFileUploadFail(true);
                  }
                }
              });
          }
        };
        reader.readAsText(file);
      });
    } else {
      setFileUploadFail(true);
    }
  }, []);

  const handleResetFiles = () => {
    setFileUploadFail(false);
  };

  const handleDownloadSampleFile = () => {
    axiosInstance({
      method: 'GET',
      url: `/download/sample-file/`,
    }).then((sampleFile: { [key: string]: any }) => {
      if (sampleFile) {
         if (sampleFile.data?.file_name) {
          downloadFileFromUrl(sampleFile.data?.file_name);
        }
        sampleFileDownloaded.mutate({ action: 'download_sample_input' });
      }
    });
  };

  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  return (
    <div className={styles.fileUploadWrapper}>
      {isDragActive && (
        <div className={styles.fileUploadDialogWrapper}>
          <div
            sx={{ background: 'darkgray' }}
            className={styles.fileUploadDialog}
            {...getRootProps()}>
            <div>
              <img src={images.cloudUploadWhite} alt='dropFileIcon' />
              <h2 className='heading2 lightFont'>Drop file here to upload</h2>
              <input {...getInputProps()} accept='.csv' />
            </div>
          </div>
        </div>
      )}
      <h4 sx={{ variant: 'text.headline', color: 'black', fontWeight: 'bold' }}>
        Please upload the file containing rider details
      </h4>
      {fileUploadFail && (
        <div className={styles.noResultWrapper}>
          <div>
            <div className='noResultFound'>
              <div className='textCenter'>
                <div>
                  <img src={images.csvErrorIcon} alt='csvErrorIcon' />
                </div>
                <div className='textCenter'>
                  <span className='fileName '>
                    <img src={images.pinError} alt='pinError' />
                    {fileName}
                  </span>
                </div>
                <div className='textCenter'>
                  <h5
                    className='heading4'
                    sx={{
                      variant: 'text.title-large',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    Oops!
                  </h5>
                  <h6
                    sx={{
                      variant: 'text.body-large',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    We could not process your rider details due to
                    <br /> incorrect information.
                  </h6>
                  <div className='gridCentered'>
                    <Button
                      onClick={() => {
                        handleResetFiles();
                      }}>
                      Start Over
                    </Button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
      {!fileUploadFail && (
        <div>
          <div className={styles.twoLayout}>
            <div className={styles.layoutOne}>
              <div>
                <div {...getRootProps()}>
                  <div className={styles.fileUploadDnD}>
                    <ul className={'listUnstyled'}>
                      <li>
                        <img
                          src={images.cloudUpload}
                          alt={'cloud upload'}
                          className={styles.cloudImage}
                        />
                      </li>
                      <li>
                        <ul className='listInline'>
                          <li className='listInlineItem'>
                            <input {...getInputProps()} accept='.csv' />
                            <Button variant={'gray'}>Choose File</Button>
                          </li>
                          <li className='listInlineItem'>
                            <h6 className='supportText'>
                              or drop .CSV file here to upload
                            </h6>
                          </li>
                        </ul>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
            <div className={styles.layoutTwo}>
              <div className={styles.detailsNeededWrapper}>
                <div className={styles.detailsNeeded}>
                  <h5
                    sx={{
                      variant: 'text.body-large',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    Details we would need
                  </h5>
                  <p
                    sx={{
                      variant: 'text.body-small',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    Mandatory
                  </p>
                  <ul className={`listUnstyled ${styles.detailsList}`}>
                    {csvMandatoryFields
                      .filter((item) => item.type === 'mandatory')
                      .map((item) => {
                        return (
                          <li
                            key={item.label}
                            sx={{
                              variant: 'text.caption-large',
                              color: 'dimgray',
                              fontWeight: 'body',
                            }}>
                            {item.label}
                          </li>
                        );
                      })}
                  </ul>

                  <p
                    sx={{
                      variant: 'text.body-small',
                      color: 'black',
                      fontWeight: 'body',
                    }}
                    className={`${styles.optionalField} ${styles.arrow} ${
                      optionalFields ? styles.active : ''
                    }`}
                    onClick={() => {
                      setOptionalFields(!optionalFields);
                    }}>
                    Optional
                  </p>
                  {optionalFields && (
                    <ul className={`listUnstyled ${styles.detailsList}`}>
                      {csvMandatoryFields
                        .filter((item) => item.type === 'optional')
                        .map((item) => {
                          return (
                            <li
                              key={item.label}
                              sx={{
                                variant: 'text.caption-large',
                                color: 'dimgray',
                                fontWeight: 'body',
                              }}>
                              {item.label}
                            </li>
                          );
                        })}
                    </ul>
                  )}
                  <div className={styles.bottomBtn}>
                    <a
                      href={'/'}
                      onClick={(e) => {
                        e.preventDefault();
                        e.stopPropagation();
                        handleDownloadSampleFile();
                      }}
                      className='link'>
                      <span
                        className={'flexCentered'}
                        sx={{
                          variant: 'text.body-large',
                          color: 'mediumslateblue',
                          fontWeight: 'body',
                        }}>
                        <img
                          src={images.fileDownloadIcon}
                          alt='fileDownloadIcon'
                        />
                        Sample file
                      </span>
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default FileUpload;
