//Styped

import { createContext, Dispatch, useReducer, FC } from 'react';
import { saveStore } from '../store/GlobalStore';
import { RouteActions, routesReducer, RouteObject } from './Reducers';

type InitialStateType = {
  routeList: Array<RouteObject>;
};

export const initialState: InitialStateType = {
  routeList: [],
};

export const GlobalContext = createContext<{
  state: InitialStateType;
  dispatch: Dispatch<any>;
}>({ state: initialState, dispatch: () => null });

const mainReducer = (
  { routeList }: InitialStateType,
  action: RouteActions
) => ({
  routeList: routesReducer(routeList, action),
});

// central context store

const GlobalContextProvider: FC = ({ children }) => {
  const [state, dispatch] = useReducer(mainReducer, initialState);
  const { Provider } = GlobalContext;

  saveStore({ state, dispatch });

  return <Provider value={{ state, dispatch }}> {children}</Provider>;
};
export default GlobalContextProvider;
