/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { Link, useHistory } from 'react-router-dom';
import Button from '@swvl/button';
import Grid from '@swvl/grid';
import { FC } from 'react';

import styles from '../../../assets/styles/home.module.scss';
import { images } from '../../../constants';
import { Casestudy, HomeShimmer } from '../components';
import { useCaseStudies } from '../apiServices';
import { ProfileDropDown } from '../../../components';
import { useLocalStorage } from '../../../utils';
import { TUserInfoProp } from '../../../types';

const Home: FC = () => {
  const { localStorageValue: userInfo } =
    useLocalStorage<TUserInfoProp>('userInfo');
  const history = useHistory();

  const { data: caseStudies, isFetching } = useCaseStudies({
    authorized: userInfo?.user?.role?.permissions?.can_access_case_study,
  });

  return (
    <div className={styles.homePage + ' homePage'}>
      <section className={styles.landingSection}>
        <div className={styles.homeRow}>
          <Grid gap={2} columns={[2, '2fr 1fr']}>
            <div className={styles.leftBlk}>
              <Link to='/' className={styles.logoBlk}>
                <img className={styles.logo} src={images.logoSvg} alt='SWVL' />
              </Link>
              <div className={styles.contentWrapper}>
                <h1
                  aria-label='main-heading'
                  sx={{
                    variant: 'text.title-large',
                    color: 'slategray',
                    fontWeight: 'body',
                  }}>
                  Lets get started.{' '}
                </h1>
                <h2
                  aria-label='heading2'
                  sx={{
                    variant: 'text.subhead',
                    color: 'slategray',
                    fontWeight: 'body',
                  }}>
                  Select a case study to understand my capabilities or start
                  generating a scenario
                </h2>
                <div className={styles.contentRow}>
                  <Grid gap={2} columns={[2, '2fr 1fr']}>
                    <div className={styles.innerGridWrapper}>
                      <div>
                        {!isFetching && caseStudies ? (
                          <Casestudy caseStudies={caseStudies} />
                        ) : (
                          <HomeShimmer type='CasestudyList' />
                        )}

                        <h5
                          className={styles.optionHeading}
                          sx={{
                            variant: 'text.body-large',
                            color: 'slategray',
                            fontWeight: 'bold',
                          }}>
                          <span> OR </span>
                        </h5>
                      </div>

                      <p className={styles.generateRoutes}>
                        <Button
                          variant='secondary'
                          aria-label='generate-route'
                          onClick={() => {
                            history.push('/generateroute');
                          }}>
                          Generate New Routes
                        </Button>
                      </p>
                    </div>
                  </Grid>
                </div>
              </div>
            </div>
            <div className={styles.rightBlock}>
              <div className={styles.profileDropdown}>
                <ProfileDropDown isHomeScreen={true} />
              </div>
            </div>
          </Grid>
        </div>
        <img
          className={styles.featuredImg}
          src={images.homeFeatured2x}
          alt='homeFeatured2x'
        />
      </section>
    </div>
  );
};

export default Home;
