//Styped
/** @jsxRuntime classic */
/** @jsx jsx */
import { jsx } from 'theme-ui';
import { FC, Fragment, useState, useEffect } from 'react';
import Grid from '@swvl/grid';
import Button from '@swvl/button';

import styles from '../../../assets/styles/case-study.module.scss';
import { images } from '../../../constants';
import { CustomSlider, CustomTooltip } from '../../../shared_ui_components';
import { NumberTag, GenerateRouteDialog } from './';
import { timeConvertion, calculateETF } from '../../../utils';
import { IPerformanceIndices } from '../apiServices';

type TIndicesProps = {
  performanceIndicesData: IPerformanceIndices | undefined;
  handleSidebarView: (val: boolean) => void;
  canGenerateNewRoute: boolean;
};

type SliderData = {
  max_routes_seconds: number;
  max_route_meters: number;
  capacity_limitation: number;
};

const initialSliderData: SliderData = {
  max_routes_seconds: 0,
  max_route_meters: 0,
  capacity_limitation: 0,
};
const secondsToMinutes = (val: number) => {
  return Math.round(val / 60);
};

const metersToKm = (val: number) => {
  return Math.round(val / 1000);
};

const Indices: FC<TIndicesProps> = ({
  performanceIndicesData,
  handleSidebarView,
  canGenerateNewRoute,
}) => {
  const [sliderData, setSliderData] = useState<SliderData>(initialSliderData);
  const [disableBtn, setDisableBtn] = useState(true);
  const [openDialog, setOpenDialog] = useState(false);

  useEffect(() => {
    if (performanceIndicesData) {
      setSliderData({
        max_routes_seconds: secondsToMinutes(
          performanceIndicesData.duration_input
        ),
        max_route_meters: metersToKm(performanceIndicesData.distance_input),
        capacity_limitation: performanceIndicesData.capacity_input,
      });
    }
  }, [performanceIndicesData]);

  const handleUpdateSliderData = (key: string, value: number) => {
    //Check If Value has been changed then only disable the Button
    if (
      sliderData?.capacity_limitation !==
        performanceIndicesData?.avg_utilization ||
      sliderData?.max_route_meters !== performanceIndicesData.avg_distance ||
      sliderData?.max_routes_seconds !== performanceIndicesData?.avg_duration
    ) {
      setDisableBtn(false);
    } else {
      setDisableBtn(true);
    }

    setSliderData({ ...sliderData, ...{ [key]: Number(value) } });
  };

  const putGreater = (a: number, b: number) => {
    a = a ? a : 0;
    b = b ? b : 0;
    return a >= b ? a : b;
  };

  const checkZero = (val: number) => {
    return val ? val : 0;
  };

  const checkIfMinAndMaxAreNotEqual = (min: number, max: number) => {
    return min === max ? max + 1 : max;
  };

  return (
    <Fragment>
      {performanceIndicesData && (
        <div className={styles.performanceInfoBlock}>
          <h4
            sx={{
              variant: 'text.headline',
              color: 'black',
              fontWeight: 'body',
            }}>
            Performance Indices
          </h4>
          <span
            className={styles.closeIcon}
            onClick={() => handleSidebarView(true)}>
            <img src={images.closeFloaterIcon} alt='' />
          </span>
          <Grid gap={1} columns='repeat(2, 1fr)'>
            <div>
              <div className={styles.infoBox}>
                <span
                  className={`${styles.currencySymbol}`}
                  sx={{
                    variant: 'text.caption-small',
                    color: 'black',
                    fontWeight: 'body',
                  }}>
                  $
                </span>
                <span
                  className={`${styles.mainValueSymbol}`}
                  sx={{
                    variant: 'text.title-medium',
                    color: 'black',
                    fontWeight: 'body',
                  }}>
                  {performanceIndicesData?.cost}
                  <p
                    className={`${styles.unit}`}
                    sx={{
                      variant: 'text.caption-large',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    USD
                  </p>
                </span>
                <span
                  className={styles.metric}
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}>
                  Cost
                </span>
              </div>
            </div>
            <div>
              <div className={styles.infoBox}>
                <span className={`${styles.mainValue} flexCentered`}>
                  <span
                    sx={{
                      variant: 'text.title-medium',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    {performanceIndicesData?.avg_etf}
                  </span>
                  <NumberTag
                    color={calculateETF(performanceIndicesData?.avg_etf)?.color}
                    padding='2px 4px'
                    fontColor='#fff'
                    fontWeight='700'>
                    {calculateETF(performanceIndicesData?.avg_etf)?.score}
                  </NumberTag>
                </span>
                <span
                  className={`${styles.metric} flexCentered`}
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}>
                  ETF
                  <CustomTooltip
                    id='pETF'
                    content="
                    <div class='tooltip'>
                      <div class='etfTooltip'>
                        <div class='description'>
                          <h2 class='paragraph mediumFont'>Extra Time Factor (ETF)</h2>
                          <p class='supportText lightFont'>
                            It is a comparitive metric in terms of time determining how much
                            longer or shorter a ride is when compared to the likes of Uber
                          </p>
                        </div>
                        <div class='etfDetail'>
                          <p class='paragraph mediumFont'>Understanding your ETF</p>
                          <ul class='listInline'>
                            <li class='listInlineItem supportText mediumFont'>
                              0 - 1.7 <span class='success'>Good</span>
                            </li>
                            <li class='listInlineItem supportText mediumFont'>
                              1.7 - 2.5 <span class='average'>Average</span>
                            </li>
                            <li class='listInlineItem supportText mediumFont'>
                              > 2.5
                              <span class='poor'>Bad</span>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>">
                    <img src={images.infoIcon} alt='infoIcon' />
                  </CustomTooltip>
                </span>
              </div>
            </div>
            <div>
              <div className={styles.infoBox}>
                <span
                  className={`${styles.mainValue}`}
                  sx={{
                    variant: 'text.title-medium',
                    color: 'black',
                    fontWeight: 'body',
                  }}>
                  {performanceIndicesData?.avg_distance}
                  <p
                    className={`${styles.unit}`}
                    sx={{
                      variant: 'text.caption-large',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    Km
                  </p>
                </span>
                <span
                  className={styles.metric}
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}>
                  Avg. Distance
                </span>
              </div>
            </div>
            <div>
              <div className={styles.infoBox}>
                <span
                  className={`${styles.mainValue}`}
                  sx={{
                    variant: 'text.title-medium',
                    color: 'black',
                    fontWeight: 'body',
                  }}>
                  {timeConvertion(performanceIndicesData.avg_duration)?.hours}
                  <p
                    className={`${styles.unit}`}
                    sx={{
                      variant: 'text.caption-large',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    HR
                  </p>{' '}
                  {timeConvertion(performanceIndicesData.avg_duration)?.minutes}
                  <p
                    className={`${styles.unit}`}
                    sx={{
                      variant: 'text.caption-large',
                      color: 'black',
                      fontWeight: 'body',
                    }}>
                    MINS
                  </p>
                </span>
                <span
                  className={styles.metric}
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}>
                  Avg. Duration
                </span>
              </div>
            </div>
            <div>
              <div className={styles.infoBox}>
                <span
                  className={`${styles.mainValue}`}
                  sx={{
                    variant: 'text.title-medium',
                    color: 'black',
                    fontWeight: 'body',
                  }}>
                  {performanceIndicesData?.avg_utilization}%
                </span>
                <p
                  className={styles.metric}
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}>
                  Avg. Capactiy
                </p>
              </div>
            </div>
            <div>
              <div className={styles.infoBox}>
                <span
                  className={`${styles.mainValue}`}
                  sx={{
                    variant: 'text.title-medium',
                    color: 'black',
                    fontWeight: 'body',
                  }}>
                  {performanceIndicesData?.vehicles}
                </span>
                <span
                  className={styles.metric}
                  sx={{
                    variant: 'text.body-small',
                    color: 'dimgray',
                    fontWeight: 'body',
                  }}>
                  Routes
                </span>
              </div>
            </div>
          </Grid>

          <Grid gap={1} columns='repeat(1, 1fr)'>
            <div>
              <div className={styles.sliderWrapper}>
                {
                  <div
                    className={styles.progressSlider}
                    //@ts-ignore
                    minval={`${secondsToMinutes(
                      performanceIndicesData?.min_duration
                    )} Mins`}
                    maxval={`${secondsToMinutes(
                      performanceIndicesData?.max_duration
                    )} Mins`}>
                    <div className={styles.progressSliderValueLabel}>
                      <p
                        sx={{
                          variant: 'text.body-large',
                          color: 'black',
                          fontWeight: 'body',
                        }}>
                        {sliderData.max_routes_seconds} Mins
                      </p>
                    </div>
                    <div className={styles.progressSliderLabel}>
                      <p
                        sx={{
                          variant: 'text.body-large',
                          color: 'black',
                          fontWeight: 'body',
                        }}>
                        Max Route Duration
                      </p>
                    </div>
                    <CustomSlider
                      step={1}
                      hideThumb={true}
                      max={checkIfMinAndMaxAreNotEqual(
                        secondsToMinutes(performanceIndicesData?.min_duration),
                        secondsToMinutes(performanceIndicesData?.max_duration)
                      )}
                      min={checkZero(
                        secondsToMinutes(performanceIndicesData?.min_duration)
                      )}
                      value={[
                        putGreater(
                          sliderData.max_routes_seconds,
                          performanceIndicesData?.min_duration
                        ),
                      ]}
                      handleChange={(val) => {
                        handleUpdateSliderData('max_routes_seconds', val[0]);
                      }}
                      disabled={canGenerateNewRoute ? false : true}
                      label={'Max Route Duration'}
                    />
                  </div>
                }
              </div>
            </div>
            <div>
              <div className={styles.sliderWrapper}>
                {
                  <div
                    className={styles.progressSlider}
                    //@ts-ignore
                    minval={`${metersToKm(
                      performanceIndicesData?.min_distance
                    )} Km`}
                    maxval={`${metersToKm(
                      performanceIndicesData?.max_distance
                    )} Km`}>
                    <div className={styles.progressSliderValueLabel}>
                      <p
                        sx={{
                          variant: 'text.body-large',
                          color: 'black',
                          fontWeight: 'body',
                        }}>
                        {sliderData.max_route_meters} Km
                      </p>
                    </div>
                    <div className={styles.progressSliderLabel}>
                      <p
                        sx={{
                          variant: 'text.body-large',
                          color: 'black',
                          fontWeight: 'body',
                        }}>
                        Max Route Distance
                      </p>
                    </div>
                    <CustomSlider
                      step={1}
                      max={checkIfMinAndMaxAreNotEqual(
                        metersToKm(performanceIndicesData?.min_distance),
                        metersToKm(performanceIndicesData?.max_distance)
                      )}
                      min={checkZero(
                        metersToKm(performanceIndicesData?.min_distance)
                      )}
                      hideThumb={true}
                      value={[
                        putGreater(
                          sliderData.max_route_meters,
                          metersToKm(performanceIndicesData?.min_distance)
                        ),
                      ]}
                      handleChange={(val) => {
                        handleUpdateSliderData('max_route_meters', val[0]);
                      }}
                      disabled={canGenerateNewRoute ? false : true}
                      label={'Max Route Distance'}
                    />
                  </div>
                }
              </div>
            </div>
            <div>
              <div className={styles.sliderWrapper}>
                {
                  <div
                    className={styles.progressSlider}
                    //@ts-ignore
                    minval={'10%'}
                    maxval={'100%'}>
                    <div className={styles.progressSliderValueLabel}>
                      <p
                        sx={{
                          variant: 'text.body-large',
                          color: 'black',
                          fontWeight: 'body',
                        }}>
                        {sliderData.capacity_limitation}%
                      </p>
                    </div>
                    <div className={styles.progressSliderLabel}>
                      <p
                        sx={{
                          variant: 'text.body-large',
                          color: 'black',
                          fontWeight: 'body',
                        }}>
                        Capacity Limitation
                      </p>
                    </div>
                    <CustomSlider
                      step={1}
                      min={4}
                      hideThumb={true}
                      max={100}
                      value={[putGreater(sliderData.capacity_limitation, 5)]}
                      handleChange={(val) => {
                        handleUpdateSliderData('capacity_limitation', val[0]);
                      }}
                      disabled={canGenerateNewRoute ? false : true}
                      label={'Feet Capacity'}
                    />
                  </div>
                }
              </div>
            </div>
            {canGenerateNewRoute && (
              <div className={styles.generateRoute}>
                <Button
                  size='default'
                  disabled={disableBtn}
                  onClick={() => {
                    setOpenDialog(true);
                  }}>
                  Generate Route
                </Button>
              </div>
            )}
            <GenerateRouteDialog
              open={openDialog}
              handleCancel={() => {
                setOpenDialog(false);
              }}
              sliderData={sliderData}
            />
          </Grid>
        </div>
      )}
    </Fragment>
  );
};

export default Indices;
